import ModelBase from "../../../framework/core/mvvm/model_base";
import NetClient from "../../../framework/net/net_client";
import TimeManager from "../../../manager/time_manager";
import { GameManager } from "../../../manager/game_manager";
import ModuleManager from "../../../framework/core/module_manager";
import NetPrompt from "../../public/module/netprompt/netprompt";
import GameServer from "../gameserver/game_server";
import ModelData from "../../public/model_data";
import AppData from "../../public/const/app_data";
import { LoginNetRoute } from "./login_net_route";
import HallServer from "../hallserver/hall_server";
import ServerBase from "../server_base";
import SceneManager from "../../../manager/scene_manager";
import UserDataManager from "../../../manager/user_manager";


const { ccclass, property } = cc._decorator;

@ccclass
export default class LoginServer extends ServerBase {

    public static instance: LoginServer;

    onLoad() {
        LoginServer.instance = this
        this.init('LoginServer');
    }

    _init() {
        let self = this;
        this.model.registerNetMsg(LoginNetRoute.queryEntry,function(data){
            self.loginRsp(data);
        },true);
    }

    //登录成功断开登录服
    loginRsp(data:any){
        cc.log('------------->>>',data);
        this.close();
        GameManager.setHallServer(data.host,data.port);
        HallServer.instance.loginHallServer()
    }

    //账号登陆
    accountLogin(account:string){
        this.connectServer(GameManager.loginServer,(state:string)=>{
            if (state == 'handshake') {
                this.startCheckPing();//登陆成功
                let data = {code:account};
                AppData.code = account;
                this.model.send(LoginNetRoute.queryEntry,data);
            }
        })
    }

    //连接登录
    connectServer(url, callback?: (state: string) => void) {
        this.connect(url,callback);
        cc.log("登陆服连接时间：", TimeManager.instance.getServerTime())
    }

    protected onConnected(){
        this._init();
    }

    protected connectTimes(times:number){
        if (this._reconTimes >= 1) {
            ModuleManager.instance.showModule(NetPrompt, { showImmediately: true, reconnect: true })
        }

        if (this._reconTimes > this._maxReconTimes) {
            cc.warn("登陆服重连次数用完，提示登陆失败")
            SceneManager.gotoLogin();
        }
    }
}
