import PokerTableSeatPokers from "../../../public/poker_table/poker_seat_pokers_base";

const { ccclass, property, menu } = cc._decorator;

@ccclass
@menu("custom/doudizhu/DouDiZhuTableSeatPokers")
export default class PdkSeatPokers extends PokerTableSeatPokers{
    
    showPokers(show: boolean, codeList?: number[], params?: any){
        let centerLocalPos = super.showPokers(show, codeList, params)
        if(show){
            if(params){
                if(params.withAnim){
                    if(params.onFinish){
                        params.onFinish()
                    }
                }else{
                    if(params.onFinish){
                        params.onFinish()
                    }
                }
            }
        }
        return centerLocalPos
    }
    
}
