const { ccclass } = cc._decorator;

export enum LogType {
    GoldTable,
    MatchTable,
    Table,
    Hall,
    Recharge,
}

@ccclass
export default class Debug {

    static _curIndex = 0 //当前上传列表
    static _isUploading = false
    static _uploadArray = [] //等待上传列表
    static logOpen: boolean = true

    static getLogStr(values?: any[]) {
        let result = Date.now() + ": "
        if (values) {
            let length = values.length
            for (let i = 0; i < length; i++) {
                if (values[i]) {
                    if (typeof (values[i]) == "object") {//如果是对象
                        result = result + " " + i + ": " + JSON.stringify(values[i])
                    } else {
                        result = result + " " + i + ": " + values[i].toString()
                    }
                } else {
                    result = result + ": null "
                }
            }
        } else {
            result = result + ": null "
        }
        return result
    }

    static log(...values) {
        if (!this.logOpen) {
            return
        }
        if (!values || values.length == 0) {
            return
        }
        let length = values.length
        if (length == 1) {
            console.log(values[0])
        } else if (length == 2) {
            console.log(values[0], values[1])
        } else if (length == 3) {
            console.log(values[0], values[1], values[2])
        } else if (length == 4) {
            console.log(values[0], values[1], values[2], values[3])
        } else if (length == 5) {
            console.log(values[0], values[1], values[2], values[3], values[4])
        } else if (length == 6) {
            console.log(values[0], values[1], values[2], values[3], values[4], values[5])
        } else if (length == 7) {
            console.log(values[0], values[1], values[2], values[3], values[4], values[5], values[6])
        } else if (length == 8) {
            console.log(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7])
        } else if (length == 9) {
            console.log(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7], values[8])
        } else {
            console.log(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7], values[8], values[9])
        }
    }

    static warn(...values) {
        if (!this.logOpen) {
            return
        }
        if (!values || values.length == 0) {
            return
        }
        let length = values.length
        if (length == 1) {
            console.warn(values[0])
        } else if (length == 2) {
            console.warn(values[0], values[1])
        } else if (length == 3) {
            console.warn(values[0], values[1], values[2])
        } else if (length == 4) {
            console.warn(values[0], values[1], values[2], values[3])
        } else if (length == 5) {
            console.warn(values[0], values[1], values[2], values[3], values[4])
        } else if (length == 6) {
            console.warn(values[0], values[1], values[2], values[3], values[4], values[5])
        } else if (length == 7) {
            console.warn(values[0], values[1], values[2], values[3], values[4], values[5], values[6])
        } else if (length == 8) {
            console.warn(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7])
        } else if (length == 9) {
            console.warn(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7], values[8])
        } else {
            console.warn(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7], values[8], values[9])
        }
    }

    static error(...values) {
        if (!this.logOpen) {
            return
        }
        if (!values || values.length == 0) {
            return
        }
        let length = values.length
        if (length == 1) {
            console.error(values)
        } else if (length == 2) {
            console.error(values[0], values[1])
        } else if (length == 3) {
            console.error(values[0], values[1], values[2])
        } else if (length == 4) {
            console.error(values[0], values[1], values[2], values[3])
        } else if (length == 5) {
            console.error(values[0], values[1], values[2], values[3], values[4])
        } else if (length == 6) {
            console.error(values[0], values[1], values[2], values[3], values[4], values[5])
        } else if (length == 7) {
            console.error(values[0], values[1], values[2], values[3], values[4], values[5], values[6])
        } else if (length == 8) {
            console.error(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7])
        } else if (length == 9) {
            console.error(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7], values[8])
        } else {
            console.error(values[0], values[1], values[2], values[3], values[4], values[5], values[6], values[7], values[8], values[9])
        }
    }
}