
import { ClubRoute } from "./net_clubRoute";
import ModelBase from "../../framework/core/mvvm/model_base";
import { Club } from "./club_data";

const {ccclass, property} = cc._decorator;


@ccclass
export class ClubModel extends ModelBase {

    selectClub:Club.IClubData = null;//当前选中的俱乐部;

    playWays:Club.ITableInfo[] = [];

    //请求单个俱乐部
    send_enterClub(clubId:number){
        let data = {
            clubId:clubId,
        }
        this.send(ClubRoute.enterClub,data);
    }

    send_setClubPlayway(data:any){
        this.send(ClubRoute.setClubPlayway,data);
    }

    send_getClubTable(clubId:number, gameMode:number, gameId:number, playwayId:string) {
        let data = {
            clubId:clubId,
            gameMode:gameMode || 0,
            gameId:gameId || 0,
            playwayId:playwayId || "0",
        }
        this.send(ClubRoute.getClubTable,data);
    }

    send_getClubPlayway(clubId:number, gameMode:number, gameId:number, playwayId:string){
        let data = {
            clubId:clubId,
            gameMode:gameMode || 0,
            gameId:gameId || 0,
            playwayId:playwayId || "0",
        }
        this.send(ClubRoute.getClubPlayway,data);
    }

    reset(){
        
    }
}
