
//提示工具类
export class RecommendTools{
    //将表中的所有元素复制指定数量
    static copy_table_value(t, count){
        let ntable = []
        for (let i = 0; i < t.length; i++) {
            const v = t[i];
            for (let j = 0; j < count; j++) {                
                ntable.push(v)
            }
        }
        return ntable
    }

    //两个表相减
    static ArraySubtract(t1, t2){
        let r = []
        let tempt2 = RecommendTools.copy_array(t2)
        for (let i = 0; i < t1.length; i++) {
            const element = t1[i];
            let temp1 = false
            let num1 = 0
            for (let j = 0; j < tempt2.length; j++) {
                const tmpE = tempt2[j];
                if(element == tmpE){
                    temp1 = true
                    num1 = j
                }
            }
            if(temp1){
                tempt2.splice(num1, 1)
            }else{
                r.push(element)
            }
        }
        return r
    }

    //复制table，只复制一层
    static copy_array(t){
        let copyt = []
        for (let i = 0; i < t.length; i++) {
            const v = t[i];
            copyt.push(v)
        }
        return copyt
    }

    static contains(array, e){
        for (let i = 0; i < array.length; i++) {
            const v = array[i];
            if(v == e){
                return true
            }
        }
        return false
   }

   //是否是相同
   static isSameValue(array: number[]): boolean{
    if(RecommendTools.is_null_or_empty(array)){
        return true
    }else{
        let lastVal = null
        for (let i = 0; i < array.length; i++) {
            const element = array[i];
            if(i > 0 && lastVal != element){
                return false
            }
            lastVal = element
        }
        return true
    }
}

   
   //降序排序
   static sort_descend(t){
        t.sort(function(a, b){
            return b - a
        })
        return t
   }

   //升序排序
   static sort_ascend(t){
        t.sort(function(a, b){
            return a - b
        })
        return t
   }

    //去掉重复的元素并升序排序(复制出一份新表操作)
    static usort(array: number[]): number[]{
        let newArray = []
        for (let i = 0; i < array.length; i++) {
            const element = array[i];
            if(!RecommendTools.contains(newArray, element)){
                newArray.push(element)
            }
        }
        RecommendTools.sort_ascend(newArray)
        return newArray
    }

    //获取相同count牌的数组
    static get_mulsame(array: number[], count: number): number[]{
        let copyt = RecommendTools.usort(array)       //去掉重复的牌并排序
        let mul = []
        for (let i = 0; i < copyt.length; i++) {
            const element = copyt[i];
            let ts = 0
            for (let j = 0; j < array.length; j++) {
                const tmp = array[j];
                if(tmp == element){
                    ts = ts + 1
                }
            }
            if(ts >= count){
                mul.push(element)
            }
        }
        return mul
    }

    //移除 remove 相同count的数组，surl array中移除相同count的牌值
    static remove_count_same(array: number[], count: number){
        let removel = RecommendTools.get_mulsame(array, count)
        let surl = RecommendTools.ArraySubtract(array, RecommendTools.copy_table_value(removel, count))
        return {removel:removel, surl: surl}
    }

    static table_value_shunzi(t){
        let shunzi = true
        if(t.length == 0){
            return false
        }else if(t.length > 1){
            let copyt = RecommendTools.copy_array(t)
            copyt = RecommendTools.sort_ascend(copyt)
            let num
            for (let i = 0; i < copyt.length; i++) {
                if(i > 0){
                    if(num + 1 != copyt[i]){
                         return false
                    }
                }
                num = copyt[i]
            }           
        }
        return shunzi
    }

    static is_null_or_empty(list: any){
        if(list == null){
            return true
        }
        if(!Array.isArray(list)){
            return true
        }
        if(list.length == 0){
            return true
        }
        return false
    }

    //新表添加 
    static TableAdd(t, num?:number){
        if(num == null){
            num = t.length
        }
        let r = []
        for (let i = 0; i < num; i++) {
            let e = t[i]
            for (let j = 0; j < e.length; j++) {
                const element = e[j];
                r.push(element)
            }
            
        }
        return r
    }

    static find_point_shunzi(t, len){
        let copyt = RecommendTools.usort(t)
        let shunzil = []
        if(copyt.length >= len && len > 1){
            let tmpLen = copyt.length - len + 1
            for (let i = 0; i < tmpLen; i++) {
                let daixuan = []
                for (let j = i; j <= len + i - 1; j++) {
                    const element = copyt[j];
                    daixuan.push(element)
                }
                if(RecommendTools.table_value_shunzi(daixuan)){
                    shunzil.push(daixuan)
                }
            }
        }
        return shunzil
    }

    
    static two_table_add(t1, t2){
        return RecommendTools.TableAdd([t1, t2], 2)
    }

    
    static Max(t){
        let max = 0
        for (let i = 0; i < t.length; i++) {
            const element = t[i];
            if(element > max){
                max = element
            }
        }
        return max
    }    
}