import Module, { moduleInit } from "../../../../framework/core/mvvm/module_base";
import { PdkModel } from "./pdk_model";
import GameServer from "../../../server/gameserver/game_server";
import { PdkNetRoute } from "../../net/pdk_route";
import { PdkView } from "./pdk_view";
import SceneManager from "../../../../manager/scene_manager";
import { PConst } from "./data/pdk_const";
import { PdkModuleBase } from "./pdk_module_base";
import { PdkSound } from "../component/pdk_table_sound";
import { PatternType, CardPattern } from "./poker/poker_pattern";
import AudioManager from "../../../../manager/audio_manager";
import { PdkData } from "./data/pdk_data";
import ModuleManager from "../../../../framework/core/module_manager";
import { ResultModule } from "../result/result_module";
import UserDataManager from "../../../../manager/user_manager";
import { PdkUtils } from "../help/PdkUtils";
import Toast from "../../../public/module/toast/toast";

const {ccclass, property} = cc._decorator;

@ccclass
@moduleInit('PdkModule')
export class PdkModule extends PdkModuleBase {
    //动画处理
    onOutCard(msg:any){
        super.onOutCard(msg);
        this.showOutCard(msg.outcardUser,msg.cardData)
    }

    onUserEntryRoom(msg:any){
        super.onUserEntryRoom(msg);
        let user = this.model.getUser(msg.id);
        if (user) {
            let viewid = this.model.viewLocation(user.user.chairID)
            this.view.userEnter(viewid,user.user,this.model.roomInfo);
        }
    }

    onHandCardUser(msg:any){
        super.onHandCardUser(msg);
        this.view.updateMyHandCards(msg.handCardData);
        this.model.setPokerList(msg.handCardData);
    }

    //刷新打出牌
    onOutCardNotify(msg:any){
        super.onOutCardNotify(msg);
        this.view.hidePlayerTag(0);
        let viewid = this.model.viewLocation(msg.currentUser);
        this.view.showSeatClock(viewid,true,10);//倒计时需要修改
        this.view.showHandCards(viewid,false);
        this.view.showSelect(viewid,true);
        if (this.model.isRole(msg.currentUser)) {
            this.view.changeBtnState('playing');
            this.view.showSeatPassAnim(viewid,false);
            if ( this.model.isMyOutCard()) {
                this.model.mCommonData.outCards = [];                
            }
            this.onClickRemindCard(true);
        }else{
            this.view.changeBtnState('none');
        }
    }

    onLeaveRoom(msg:any){
       super.onLeaveRoom(msg);
       let viewid = this.model.viewLocation(msg.wChairID);
       this.view.userLeave(viewid);
    }

    onReadyGame(msg:any){
        super.onReadyGame(msg)
        this._readyGame(msg.wChairID)
    }

    private showOutCard(chairID:number,cards:number[]){
        let viewid = this.model.viewLocation(chairID);
        this.view.showHandCards(viewid,true,cards,true);
        if (viewid == 0) {
            this.model.mCommonData.lastPattern = null;
        }else{
            this.model.mCommonData.lastPattern = CardPattern.newPattern(cards);
        }
       this.dispatchPoker(this.model.getUserLocalID(chairID),cards);
    }

    private _readyGame(wChairID:number){
        let viewid = this.model.viewLocation(wChairID)
        this.view.showReadyTag(viewid,true);
        if (this.model.isRole(wChairID)) {
            this.view.changeBtnState('none');
        }
    }

    readyGame(msg:any){
        super.readyGame(msg);
        if (msg.code == 0) {
            let myself = this.model.mySeatInfo;
            this._readyGame(myself.user.chairID)
        }else if (msg.code == 4) {
            ModuleManager.instance.showModule(Toast, '余额不足,请尽快充值！'); 
        }else if (msg.code == 3) {
            SceneManager.gotoHall(false);
        }
    }

    onSettlement(msg:any){
        super.onSettlement(msg);
        let func = ()=>{
            this.model.send_readyGame();
        }
        let data = {
            func:func,
            data:{msg:msg,roleid:this.model.mChair},
        }
        //弹框
        ModuleManager.instance.showModule(ResultModule,data)
        //亮牌
        this.settlementcards(msg);
    }

    //亮牌
    private settlementcards(msg:any){
        let handCardData = msg.handCardData;
        for (let i = 0; i < handCardData.length; i++) {
            let cards = handCardData[i];
            let viewid = this.model.viewLocation(i);
            if (viewid != 0) {//自己无需亮牌
                this.view.showHandCards(viewid,true,cards,true);
            }
        }
    }

    //重连消息
    onSendGameScene(msg:any){
        super.onSendGameScene(msg);
        this.view.updateAllUser(this.model.users,this.model.roomInfo);
        this.clearTable();
        this.view.changeBtnState('ready');        
        if (msg.gameStatus == PConst.GameStatus.START) {//游戏中，直接显示手牌
            this.view.updateMyHandCards(msg.handCardData);
            this.model.setPokerList(msg.handCardData);

            //上家出牌
            if (msg.turnUser != undefined 
                && !this.model.isRole(msg.turnUser) 
                && msg.turnUser != 65535
                && msg.turnUser != -1) {
                this.showOutCard(msg.turnUser,msg.turnCardData)
                this.model.mCommonData.chairID = msg.turnUser;
                this.model.mCommonData.outCards = msg.turnCardData;
            }else{
                this.model.mCommonData.outCards = [];
            }

            if (msg.currentUser == this.model.mChair) {//我出牌
                let viewid = this.model.viewLocation(this.model.mChair)
                this.view.showSeatClock(viewid,true,10);
                this.view.showHandCards(viewid,false);
                this.view.changeBtnState('playing');
                this.view.showSeatPassAnim(viewid,false);
                this.onClickRemindCard(true);
            }else{
                this.view.changeBtnState('none');
            }
            //更新警报
            for (let idx = 0; idx < msg.bUserWarn.length; idx++) {
                let cid = msg.bUserWarn[idx];
                if (cid && !this.model.isRole(idx)) {
                    this.showWarn(idx);
                }
            }
            let outCardUser = this.model.viewLocation(msg.currentUser);
            this.view.showSelect(outCardUser,true);
            //闹钟
            this.view.showSeatClock(outCardUser,true,10);
        }else{
            
        }
    }

    //开始游戏
    onStartGame(msg:any){
        super.onStartGame(msg);
        this.clearTable();
        let viewid = this.model.viewLocation(msg.wCurrentUser)
        this.view.showSelect(viewid,true);
        //发牌
        this.view.dealCards(msg.cbCardData);
        this.model.setPokerList(msg.cbCardData)
        if (this.model.isRole(msg.wCurrentUser)) {
            this.view.changeBtnState('playing');
            this.view.showHandCards(viewid,false);
            this.view.showSeatPassAnim(viewid,false);
        }else{
            this.view.changeBtnState('none');
        }
        this.view.showSeatClock(viewid,true,10);
    }

    onWarnUser(msg:any){
        super.onWarnUser(msg);
        this.showWarn(msg.wWarnUser);
        PdkSound.playWarningSound(UserDataManager.instance.isMale(),true);
    }

    private showWarn(chairid:number){
        let viewid = this.model.viewLocation(chairid)
        this.view.showSingle(viewid,true);
    }

    //清理牌桌
    protected clearTable(){
        this.view.isForbidOutBtn(false);
        for (let index = 0; index < this.model.roomInfo.playerCount; index++) {
            this.view.showHandCards(index,false)
        }
        this.view.myhandPokers.removeAllPokers();
        this.view.hidePlayerTag(0)
        this.recommendFunc = null;
        this.model.mCommonData.lastPattern = null;        
    }

    playCard(msg:any){
        super.playCard(msg);
        if (msg.code == 0) {
            this.view.updateMyHandCards(msg.handCardData);
            this.model.setPokerList(msg.handCardData);
        }
    }

    protected dispatchPoker(seatInfo: PdkData.SeatInfoData,outCards:number[]) {
        let cards = outCards
        let centerWorldPos = this.view.showDispatchCards(seatInfo.viewID, true, cards, true)
        this.playCardPatternSoundAndEffect(seatInfo,CardPattern.newPattern(outCards),centerWorldPos);
    }

    //动画展示
    protected playCardPatternSoundAndEffect(seatInfo: PdkData.SeatInfoData, cardPatter: CardPattern, centerWorldPos: cc.Vec2) {
        let self = this

        PdkSound.playPokerTypeSound(true, cardPatter)

        let type = cardPatter.type
        if (!type) {
            return;
        }
        let disp_type = cardPatter.disp_type
        switch (type) {
            case PatternType.type_zhadan:
                this.view.playZhaDanEffect(seatInfo.viewID, centerWorldPos)
                AudioManager.instance.vibrate()
                break;
            case PatternType.type_huojian:
                this.view.playWangZhaEffect(seatInfo.viewID, centerWorldPos, function () {
                    self.view.scheduleOnce(function () {
                        PdkSound.playPokerTypeEffectSound(cardPatter)
                    }, 0.3)
                    AudioManager.instance.vibrate()
                })
                return
            case PatternType.type_liandui:
                this.view.playLianDuiEffect(seatInfo.viewID, centerWorldPos)
                break;
            case PatternType.type_feiji:
                this.view.playFeiJiEffect(seatInfo.viewID, centerWorldPos)
                break;
            case PatternType.type_shunzi:
                this.view.playShunZiEffect(seatInfo.viewID, centerWorldPos)
                break;
            case PatternType.type_sandaiyi:
                if (disp_type == 'sandaiyi') {
                    this.view.playSanDaiYiEffect(seatInfo.viewID, centerWorldPos)
                } else if (disp_type == 'sandaier') {
                    this.view.playSanDaiErEffect(seatInfo.viewID, centerWorldPos)
                }
                break;
            case PatternType.type_sidaier:
                if (disp_type == 'sidaisi') {
                    this.view.playSiDaiLiangDuiEffect(seatInfo.viewID, centerWorldPos)
                }
                else {
                    this.view.playSiDaiErEffect(seatInfo.viewID, centerWorldPos)
                }
                break;
            default:
                break;
        }
        PdkSound.playPokerTypeEffectSound(cardPatter)
    }

}
