    getStrLength(str) {
        if (str) {
            return 0
        }
        var toCodePoint = function (unicodeSurrogates) {
            var r = [], c = 0, p = 0, i = 0;
            while (i < unicodeSurrogates.length) {
                var pos = i;
                c = unicodeSurrogates.charCodeAt(i++);//返回位置的字符的 Unicode 编码
                if (c == 0xfe0f) {
                    continue;
                }
                if (p) {
                    var value = (0x10000 + ((p - 0xD800) << 10) + (c - 0xDC00));
                    r.push({
                        v: value,
                        pos: pos,
                    }); //计算4字节的unicode
                    p = 0;
                } else if (0xD800 <= c && c <= 0xDBFF) {
                    p = c; //如果unicode编码在oxD800-0xDBff之间，则需要与后一个字符放在一起
                } else {
                    r.push({
                        v: c,
                        pos: pos
                    }); //如果是2字节，直接将码点转为对应的十六进制形式
                }
            }
            return r;
        }

        var codeArr = toCodePoint(str);
        var numChar = 0;

        for (var i = 0; i < codeArr.length; ++i) {
            var code = codeArr[i].v;
            var add = 1;
            if (code >= 128) {
                add = 2;
            }
            numChar += add;
        }

        return numChar
    }

    public resetAnimation(anim: cc.Animation) {
        if (!anim) {
            return
        }
        anim.setCurrentTime(0)
    }

    findChildNode(node: cc.Node, path: string): cc.Node {
        let index = path.indexOf('/')
        if (index > 0) {
            let name = path.substring(0, index)
            if (index + 1 < path.length) {
                path = path.substring(index + 1)
                let child = node.getChildByName(name)
                if (child) {
                    return this.findChildNode(child, path)
                }
                return null
            } else {
                return node.getChildByName(name)
            }
        } else {
            return node.getChildByName(path)
        }

    }

    // 货币格式
    static currencyFormatting(num: number, isInt: boolean = false) {
        let numStr: string = (num.toFixed(2) || 0).toString(), result = '';
        let arr = numStr.split(".");
        let beforeStr = arr[0];

        while (beforeStr.length > 3) {
            result = ',' + beforeStr.slice(-3) + result;
            beforeStr = beforeStr.slice(0, beforeStr.length - 3);
        }
        if (beforeStr) { result = beforeStr + result; }
        if (isInt) { return result }
        if (arr.length > 1) {
            result = result + "." + arr[1];
        }
        return result;
    }

