import GameUtil from "../utility/game_util";
import TimeManager from "./time_manager";
import Debug from "../manager/debug";

const { ccclass } = cc._decorator;

class TextureCache {
    key: string
    spriteFrame: cc.SpriteFrame
    constructor() { }
}

class ReloadCache {
    key: string
    count: number
    constructor() { }
}

@ccclass
export default class TextureManager extends cc.Component {

    public static readonly instance: TextureManager = new TextureManager();

    spriteCache: TextureCache[] = []
    reloadCache: ReloadCache[] = []
    reloadTimeArray = [1, 3, 5]//加载失败后重新加载间隔时间

    clear() {
        let length = this.spriteCache.length
        for (let i = 0; i < length; i++) {
            cc.loader.releaseAsset(this.spriteCache[i].spriteFrame)
        }
        this.spriteCache = []
        this.reloadCache = []
    }

    //加载远程图片
    getSprite(key: string, success: Function) {
        let index = this.hasSprite(key)
        if (index >= 0) {
            return success(this.spriteCache[index].spriteFrame)
        }
        let self = this
        GameUtil.loadTexture(key, function (err, texture) {
            if (texture) {
                let frame = new cc.SpriteFrame(texture)
                self.addSprite(key, frame)
                self.removeReloadCache(key)
                return success(frame)
            } else {
                Debug.warn("下载图片出错：", key)
                let time = self.getReloadTime(key)
                if (time > 0) {
                    TimeManager.instance.scheduleOnce(() => {
                        self.getSprite(key, success)
                    }, time)
                }
            }
        });
    }

    //加载本地图片
    getLocalSprite(name: string, success: Function) {
        let index = this.hasSprite(name)
        if (index >= 0) {
            return success(this.spriteCache[index].spriteFrame)
        }
        let self = this
        cc.loader.loadRes("image/" + name, cc.SpriteFrame, function (error: Error, resource) {
            if (error) {
                Debug.log(error)
            } else {
                let frame = <cc.SpriteFrame>resource
                self.addSprite(name, frame)
                return success(frame)
            }
        })
    }

    hasSprite(key: string) {
        let length = this.spriteCache.length
        for (let i = 0; i < length; i++) {
            if (this.spriteCache[i].key == key) {
                return i
            }
        }
        return -1
    }

    addSprite(key: string, spriteFrame: cc.SpriteFrame) {
        let index = this.hasSprite(key)
        if (index < 0) {
            let spriteData = new TextureCache()
            spriteData.key = key
            spriteData.spriteFrame = spriteFrame
            this.spriteCache.push(spriteData)
        } else {
            this.spriteCache[index].spriteFrame = spriteFrame
        }
    }

    hasReloadCache(key: string) {
        let length = this.reloadCache.length
        for (let i = 0; i < length; i++) {
            if (this.reloadCache[i].key == key) {
                return i
            }
        }
        return -1
    }

    removeReloadCache(key: string) {
        let index = this.hasReloadCache(key)
        if (index > 0) {
            this.reloadCache.splice(index, 1)
        }
    }

    getReloadTime(key: string) {
        let index = this.hasReloadCache(key)
        if (index < 0) {
            let spriteData = new ReloadCache()
            spriteData.key = key
            spriteData.count = 0
            this.reloadCache.push(spriteData)
            if (spriteData.count >= this.reloadTimeArray.length) {
                return -1
            } else {
                return this.reloadTimeArray[spriteData.count]
            }
        } else {
            let spriteData = this.reloadCache[index]
            spriteData.count = spriteData.count + 1
            if (spriteData.count >= this.reloadTimeArray.length) {
                return -1
            } else {
                return this.reloadTimeArray[spriteData.count]
            }
        }
    }
}