import Module, { moduleInit } from "../../framework/core/mvvm/module_base";
import { ClubView } from "./club_view";
import HallServer from "../server/hallserver/hall_server";
import { ClubRoute } from "./net_clubRoute";
import ModuleManager from "../../framework/core/module_manager";
import LoaclStorage from "../../manager/local_storage";
import Toast from "../public/module/toast/toast";
import EventManager from "../../manager/event_manager";
import { Club } from "./club_data";
import { ClubModel } from "./club_model";
import HallModule from "../hall/hall_module";
import ClubMemListModule from "./mem/clubMemList_module";
import ClubRankModule from "./rank/clubRank_module";

const {ccclass, property} = cc._decorator;

@ccclass
@moduleInit('ClubModule')
export class ClubModule extends Module<ClubView,ClubModel> {
    constructor(){
        super();
        this.windowPrefabResPath = 'package/club/club';
        this.viewType = ClubView;
        this.modelType = ClubModel;
    }

    onShow(clubId:number){
        this.model.netClient = HallServer.instance.netClient;
        this.model.clearMsgEvent();
        this.view.clickToggleFunc = this.clickToggleFunc.bind(this);
        this.registerMsg();
        let select = LoaclStorage.getNumber('_select_club_id')
        if (clubId || select) {
            clubId = clubId || select;
            this.model.send_enterClub(clubId);
        }else{
            ModuleManager.instance.showModule(HallModule,null,()=>{
                ModuleManager.instance.destroyModule('ClubModule')
            })
        }
    }

    protected registerMsg(){
        this.model.registerNetMsg(ClubRoute.enterClub, this.retRefreshCLub.bind(this));
        this.model.registerNetMsg(ClubRoute.getClubPlayway, this.retGetClubPlayway.bind(this));
        this.model.registerNetMsg(ClubRoute.getClubTable, this.retGetClubTable.bind(this));
        this.model.registerNetMsg(ClubRoute.onRefreshClubTable, this.retRefreshClubTable.bind(this));
        this.model.registerNetMsg(ClubRoute.onRemoveClubTable, this.onRemoveClubTable.bind(this));
    }

    protected onButtonClick(node: cc.Node, customEventData){
        let pName = node.name;
        switch (pName) {
            case 'Button_close':
                ModuleManager.instance.showModule(HallModule,null,()=>{
                    ModuleManager.instance.destroyModule('ClubModule');
                });
                break;
            case 'Button_mode':
                let gameMode = LoaclStorage.getNumber('_club_gameMode', 0);
                if (gameMode == 0) {
                    // 进入积分厅
                    LoaclStorage.setNumber('_club_gameMode', 1);
                    this.view.enterClubAction(1);
                } else {
                    // 进入金币厅
                    LoaclStorage.setNumber('_club_gameMode', 0);
                    this.view.enterClubAction(0);
                }
                LoaclStorage.setNumber('_club_gameId', 0);
                this.enterClubAllTable();
                break;
            case 'Button_memList':
                ModuleManager.instance.showModule(ClubMemListModule, this.model.selectClub);
                break;
            case 'Button_rank':
                ModuleManager.instance.showModule(ClubRankModule);
                break;
            default:
                break;
        }
    }

    clickToggleFunc(toggle) {
        if (toggle.name == 'alltoggle<Toggle>') {
            LoaclStorage.setNumber('_club_gameId', 0);
            this.enterClubAllTable();
        } else if (toggle.name == 'pdk<Toggle>'){
            LoaclStorage.setNumber('_club_gameId', 15);
            this.enterClubSingleGame();
        }
    }

    protected updateSelectClub(data:any){
        LoaclStorage.setNumber('_select_club_id',data.clubInfo.clubId);
        this.model.selectClub = data.clubInfo;
        this.view.updateClubInfo(data.clubInfo);
        let str = {
            content:"<color=#00ffff>{0}</color>".format(data.clubInfo.announcement),
            tag:'',
            showCount:1000,
        }
        EventManager.instance.dispatch('Event_Notify_Data',str)

        let gameId = LoaclStorage.getNumber('_club_gameId', 0);
        if (gameId == 0) {
            this.enterClubAllTable();
        } else {
            this.enterClubSingleGame();
        }
        let gameMode = LoaclStorage.getNumber('_club_gameMode', 0);
        this.view.enterClubAction(gameMode);
    }

    enterClubAllTable() {
        let gameMode = LoaclStorage.getNumber('_club_gameMode', 0);
        this.view.clearTableList();
        this.view.mTables = [];
        this.model.playWays = [];
        this.model.send_getClubTable(this.model.selectClub.clubId, gameMode, 0, "0");
    }

    enterClubSingleGame() {
        let clubId = LoaclStorage.getNumber('_select_club_id');
        let gameMode = LoaclStorage.getNumber('_club_gameMode', 0);
        let gameId = LoaclStorage.getNumber('_club_gameId', 0);
        let playwayId = LoaclStorage.getString('_club_playwayId', "0");
        this.view.clearTableList();
        this.view.mTables = [];
        this.model.playWays = [];
        this.model.send_getClubPlayway(clubId, gameMode, gameId, playwayId);
    }

    /*********************************************
     * 服务端消息返回
    *********************************************/
    // 刷新俱乐部
    retRefreshCLub(data:any){
        if (data.code == 0) {
            this.updateSelectClub(data);
        }else{
            ModuleManager.instance.showModule(Toast,'异常错误code=' + data.code);
        }
    }

    // 获取俱乐部玩法
    retGetClubPlayway(data:any){
        if (data.code == 0) {
            let playwayNum = data.playways.length;
            if (playwayNum <= 0) {
                ModuleManager.instance.showModule(Toast,'当前大厅暂未设置玩法.');
            }else{
                let ways:Club.ITableInfo[] = [];
                for (let idx = 0; idx < playwayNum; idx++) {
                    let way:Club.ITableInfo = {
                        playWay:data.playways[idx],
                        tbType:Club.TableType.PWTable,
                        tableid:data.playways[idx].id,
                    }
                    ways.push(way);
                }
                this.model.playWays = this.model.playWays.concat(ways);
                this.view.appendTable(ways, true);

                // 获取俱乐部已创建桌子
                let gameMode = LoaclStorage.getNumber('_club_gameMode', 0);
                let gameId = LoaclStorage.getNumber('_club_gameId', 0);
                let playwayId = LoaclStorage.getString('_club_playwayId', "0");
                this.model.send_getClubTable(this.model.selectClub.clubId, gameMode, gameId, playwayId);
            }
        } else{
            ModuleManager.instance.showModule(Toast,'获取俱乐部玩法失败.');
        }
    }

    // 获取俱乐部桌子
    retGetClubTable(data:any){
        if (data.code == 0) {
            let gtable:Club.ITableInfo[] = [];
            for (let idx = 0; idx < data.tableInfos.length; idx++) {
                let gtb = data.tableInfos[idx];
                let gData:Club.ITableInfo = {
                    gameInfo:gtb,
                    tableid:gtb.tableId,
                    tbType:Club.TableType.GameTable
                }
                gtable.push(gData);
            }
            this.view.appendTable(gtable, true);
        } else{
            ModuleManager.instance.showModule(Toast,'获取俱乐部桌子失败.');
        }
    }

    onRemoveClubTable(data:any){
        this.view.removeTableItem(data.tableId);
        cc.log('---------->>>>onremove',data)
    }

    //推送桌子
    retRefreshClubTable(data:any){
        let gameMode = LoaclStorage.getNumber('_club_gameMode', 0);
        if (data.gameMode != gameMode) {
            return;
        }

        let gameTable:Club.ITableInfo = {
            tbType:Club.TableType.GameTable,
            gameInfo:data,
            tableid:data.tableId,
        }
        this.view.appendTable([gameTable], false);
    }
}
