
const { ccclass, property } = cc._decorator;

let canRelease = function(key, caches){
    for (var tmpId in caches){
        if (caches.hasOwnProperty(tmpId)) {
            const element = caches[tmpId];
            let deps = element.dependKeys
            if(deps){
                for (let i = 0; i < deps.length; i++) {
                    const d = deps[i];
                    if(d == key){
                        return false
                    }
                }
            }
        }
    }
    return true
  }

  

@ccclass
export default class LoaderWrap{
    static init(){
        let release = cc.loader.release
        cc.loader.release = function(asset){
            let dependKeys = null
            if (Array.isArray(asset)){
            }else if (asset) {
                var id = this._getReferenceKey(asset);
                var item = this.getItem(id);
                if(item){
                    dependKeys = item.dependKeys
                }
            }
            release.call(this, asset)
            if (Array.isArray(asset)){
            }else if (asset) {
                if(dependKeys){
                    for (let i = 0; i < dependKeys.length; i++) {
                        const element = dependKeys[i];
                        if(canRelease(element, this._cache)){
                            this.release(element)
                        }
                    }
                }                
            }           
        }
    }
}
