import Module, { bindButtonClickEvent, moduleInit } from "../../../../framework/core/mvvm/module_base";
import ModuleManager from "../../../../framework/core/module_manager";
import Utility from "../../../../utility/Utility";
import LoaclStorage from "../../../../manager/local_storage";
import { CreateRoomModel } from "./createroom_model";
import { CreateRoomView } from "./createroom_view";
import { PdkRoomConfig } from "../../../pdk/module/room_config";
import { RuleFactory } from "./data/rule_factory";


const { ccclass, menu } = cc._decorator;

@ccclass
@moduleInit("CreateRoomModule")
export class CreateRoomModule extends Module<CreateRoomView, CreateRoomModel> {

    private lineHeight = 1

    constructor() {
        super();
        this.windowPrefabResPath = "package/public/public_createroom";
        this.viewType = CreateRoomView;
        this.modelType = CreateRoomModel;
        this.needAnim = true
        this.clickAnywhereClose = false;
        this.needViewMask = true
    }

    protected onButtonClick(node: cc.Node, customEventData){
        let pName = node.name;
        switch (pName) {
            case 'closeview':
                ModuleManager.instance.destroyModule('CreateRoomModule')
                break;
            default:
                break;
        }
    }

    onInit() {
        let d = RuleFactory.instance.getConfig(PdkRoomConfig.config);
        // let c = d;
        // let d = 0;
    }


}




