import ModelBase from "../../framework/core/mvvm/model_base";
import NetClient, { NetClientProtoType } from "../../framework/net/net_client";
import TimeManager from "../../manager/time_manager";

const { ccclass } = cc._decorator;


@ccclass
export default class ServerBase extends cc.Component {

    model: ModelBase = new ModelBase()
    netClient: NetClient = new NetClient();
    _pingDelayTime: number = 0
    clientName : string
    _lastPingTime:number;
    netClientProtoType : NetClientProtoType

    onNotifyPing: (ping:number) => void;

    protected init(clientName) {
        this.model = new ModelBase();
        this.netClient = new NetClient();
        this.netClient.name = clientName;
        this.clientName = clientName;
        this.model.netClient = this.netClient;
    }

    _onConnectState: (state: string) => void;

    state = null
    _reconTimes = 0
    _maxReconTimes = 5

    private url : string 


    //连接服务器
    protected connect(url, callback?: (state: string) => void) {
        this.close()
        this._onConnectState = callback
        this.url = url
        this.netClient.connect(url, this.netClientProtoType ? this.netClientProtoType :NetClientProtoType.GameServer);
        //bind是非常必要的，因为从回调回来的this已经改变了已经变了
        this.netClient.onConnectStateEvent = this._onConnectStateEvent.bind(this)
        this.model.netClient = this.netClient;
        this.netClient.heartbeatEvent = this._heartbeatEvent.bind(this);
        cc.log("服务器连接：", url, TimeManager.instance.getServerTime())
        this.unschedule(this.checkConnect)
        this.scheduleOnce(this.checkConnect, 30)
    }

    _heartbeatEvent(){
        if (this._lastPingTime) {
            this._pingDelayTime = this.netClient.lastReceivePingTime - this._lastPingTime;
            this._lastPingTime = null;
            if (this._pingDelayTime == 0) {
                this._pingDelayTime = 0.06;
            }

            if(this.onNotifyPing){
                this.onNotifyPing(this._pingDelayTime)
            }
        }
    }

    checkConnect() {
        cc.log(`${0}连接超时，重连！`,this.clientName,TimeManager.instance.getServerTime())
        this.unschedule(this.checkConnect)
        this.reconnectServer()
    }

    close() {
        this.unschedule(this._reconnect)
        this.unschedule(this.checkConnect)
        this.unschedule(this._checkPing)
        this.unschedule(this._loginIsTimeOut);
        this.netClient.activeClose();
    }


    protected startCheckPing() {
        this.unschedule(this._loginIsTimeOut);
        this._reconTimes = 0
        this.netClient.lastReceivePingTime = Date.now();
        this._lastPingTime = Date.now();
        this._requestPing()
        this.unschedule(this._checkPing)
        this.schedule(this._checkPing, this.netClient.heartbeatInterval, cc.macro.REPEAT_FOREVER)
    }

    private _checkPing() {
        let time = Date.now()
        //cc.log("checkPing",this.clientName, time - this.netClient.lastReceivePingTime , this.netClient.heartbeatTimeout)
        this._requestPing()
        if (this._lastPingTime == null) {
            this._lastPingTime = time;
        }

        let passTime = time - this.netClient.lastReceivePingTime;
        let isConnect = passTime - this.netClient.heartbeatTimeout > 0;//超时
        if (isConnect) {
            this.heartbeat_timeout_reconnect()
        }

        if(this.onNotifyPing){
            this.onNotifyPing(this._pingDelayTime)
        }
    }

    protected _requestPing() {
    //cc.log('----->>ping:',this.netClient.netState);
        if (this.netClient.netState) {
            this.netClient.heartbeat();
        }
    }

    reconnectServer() {
        if (TimeManager.instance.backGround) {
            cc.warn("正在后台中，不重连！")
            return
        }
        let time = 1
        if (this._reconTimes >= 2) {
            time = 3
        }
        cc.log("断线重连！", time, this._reconTimes)
        this.unschedule(this._reconnect)
        this.unschedule(this.checkConnect)
        this.unschedule(this._checkPing)
        this.unschedule(this._loginIsTimeOut);
        this.scheduleOnce(this._reconnect, time)
    }

    protected onReconnectServer() {

    }

    protected onConnected() {

    }
    

    private _reconnect() {
        this._reconTimes = this._reconTimes + 1
        this.connectTimes(this._reconTimes);
        if (this._reconTimes < this._maxReconTimes) {
            this.connect(this.url, this._onConnectState)
        } else {
            cc.warn(this.clientName, "断线重连次数用完提示登陆失败")
            this.close()
            this.reconnectServerFail()
        }
    }

    protected connectTimes(times:number){

    }


    protected reconnectServerFail() {

    }

    _loginIsTimeOut() {
        cc.warn(this.clientName, "服务器连接超时！")
        this.reconnectServer()
    }


    _onConnectStateEvent(state) {
        this.unschedule(this.checkConnect)
        cc.warn(this.clientName, "onConnectStateEvent", state)
        if ("OPEN" == state) {
            this._reconTimes = 0
            this.subscibeMsgEvent()
            this.scheduleOnce(this._loginIsTimeOut, 10);
        } else if (state == 'error') {
            this.reconnectServer()
        } else if ("CLOSE" == state) {
            cc.warn("连接中断，自动重连！")
            this.reconnectServer()
        } else if (state == "TIMEOUT") {//连接超时
            this.reconnectServer()
        }else if (state == 'handshake') {
            this.onConnected()
        }
        if (this._onConnectState != null) {
            this._onConnectState(state)
        }

    }

    private subscibeMsgEvent() {
        let self = this
        this.model.clearMsgEvent()
    }


    heartbeat_timeout_reconnect(){
        cc.warn("断线重连...！")
        let self = this
        self.close()
        self.scheduleOnce(function(){
            self.reconnectServer()
        }, 0.5)        
    }

    exit() {
        this.onNotifyPing = null;
    }

}
