import CustomUIStateSwitcher from "../ui/custom_ui_state_switcher";
import SpriteFrameHolder from "../ui/spriteFrameHolder";
import Utility from "../../../utility/Utility";
import TimeManager from "../../../manager/time_manager";
import GameUtil from "../../../utility/game_util";
import AudioManager from "../../../manager/audio_manager";
import { COPYFILE_FICLONE } from "constants";

const { ccclass, property, requireComponent } = cc._decorator;


class SpriteCache{
    private m_spriteFrame: cc.SpriteFrame
    private m_refreshTime: number
    private m_key: string
    constructor(key: string, spriteFrame: cc.SpriteFrame){
        this.m_spriteFrame = spriteFrame
        this.refreshTime()
    }

    getSpriteFrame(): cc.SpriteFrame{
        this.refreshTime()
        return this.m_spriteFrame
    }

    getKey(){
        return this.m_key
    }

    getRefreshTime(): number{
        return this.m_refreshTime
    }

    release(){
        let spriteFrame = this.m_spriteFrame
        if(spriteFrame){            
            let tex = spriteFrame.getTexture()
            if(tex){
                tex.destroy()
            }
            spriteFrame.clearTexture()
            spriteFrame.destroy()
        }
        this.m_spriteFrame = null
    }

    private refreshTime(){
        this.m_refreshTime = new Date().getTime()
    }
}

class SpriteCacheManager{
    private m_maxCount;
    private m_map;
    constructor(maxCount: number){
        this.m_maxCount = maxCount
        this.m_map = {}
    }
    
    addSpriteFrame(key: string, spriteFrame: cc.SpriteFrame){
        this.m_map[key] = new SpriteCache(key, spriteFrame)
    }
    
    findSpriteFrame(key: number|string): cc.SpriteFrame{
        let spritCache = this.m_map[key] as SpriteCache
        if(spritCache == null){
            return null
        }
        return spritCache.getSpriteFrame()
    }

    cleanExcessCaches(){
        let cacheList = new Array<SpriteCache>();
        for (const key in this.m_map) {
            if (this.m_map.hasOwnProperty(key)) {
                const element = this.m_map[key];
                if(element){
                    cacheList.push(element)
                }                
            }
        }
        if(cacheList.length < this.m_maxCount){
            return
        }
        cacheList.sort(function(a, b){
            return b.getRefreshTime() - a.getRefreshTime()
        })
        for (let index = this.m_maxCount - 1; index < cacheList.length; index++) {
            const element = cacheList[index];
            let key = element.getKey()
            // element.release()
            this.m_map[key] = null;
        }
    }

    cleanAllCaches(){
        for (const key in this.m_map) {
            if (this.m_map.hasOwnProperty(key)) {
                const element = this.m_map[key];
                if(element){
                    element.release()
                }                
            }
        }
        this.m_map = {}
    }
}

@ccclass
@requireComponent(CustomUIStateSwitcher)
export default abstract class PokerTableSeatBaseView extends cc.Component{

    static s_avatarSpriteCacheManager: SpriteCacheManager = new SpriteCacheManager(20);

    @property(cc.Node)  
    nodeRoleInfo: cc.Node = null

    @property(cc.Sprite)  //头像
    spriteAvatar: cc.Sprite = null

    @property(cc.Label)  //名字
    lableName: cc.Label = null

    @property(cc.Sprite)  //掉线
    spriteOffLine: cc.Sprite = null

    @property(cc.Sprite)    //准备图标
    spriteReady: cc.Sprite = null

    @property(CustomUIStateSwitcher)
    styleUiStateSwitcher: CustomUIStateSwitcher = null

    @property(cc.Sprite)    //房主
    spriteOwner: cc.Sprite = null

    @property(cc.Node)
    nodeClock: cc.Node = null

    @property(cc.Label)
    lableClock: cc.Label = null

    @property(cc.Node)
    nodeChatText: cc.Node = null

    @property(cc.Node)
    nodeSelect: cc.Node = null

    @property(cc.Button)
    buttonAvatar:cc.Button = null;

    @property(cc.Size)
    avatarSize:cc.Size = null;

    @property(cc.Node)
    single:cc.Node = null;
    
    @property(cc.Label)
    lableCoinBalance: cc.Label = null

    protected mOnclickKickBtnCallback: Function = null
    protected mOnclickAvatarBtnCallback: Function = null
    protected mOnclickNotSeatBtnCallback: Function = null
    private m_clockScheduleId: number = 0
    onLoad(){
        let self = this
        if(this.styleUiStateSwitcher == null){
            this.styleUiStateSwitcher = this.getComponent<CustomUIStateSwitcher>(CustomUIStateSwitcher)
        }
        if(this.buttonAvatar != null){
            this.buttonAvatar.node.on('click', function(event: cc.Button){
                if(self.mOnclickAvatarBtnCallback){
                    self.mOnclickAvatarBtnCallback(self)
                }
            })
        }
    }

    reset(){
        this.showName(false)
        this.showAvatar(false)
        this.showOffLineTag(false)
        this.showOwnerTag(false)
        this.showReadyTag(false)
        this.showSingle(false);
    }

    showName(show: boolean, name?: string){
        this.lableName.node.active = show
        if(show && name != null){
            this.lableName.string = Utility.instance.filterPlayerName(name)
        }        
    }

    showCoin(show:boolean,coin:number){
        this.lableCoinBalance.string = Utility.instance.formateNumb(coin).toString();
        this.lableCoinBalance.node.active = show;
    }

    showSingle(show:boolean){
        this.single.active = show;
    }

    showAvatar(show: boolean, spriteFrame?: cc.SpriteFrame){
        this.spriteAvatar.node.active = show        
        if(show){
            this.spriteAvatar.spriteFrame = spriteFrame
        }
    }


    showOffLineTag(show: boolean){
        if(this.spriteOffLine == null){
            return
        }
        this.spriteOffLine.node.active = show
    }

    showAvatarByUrl(show: boolean, url: string){
        let self = this
        if(show){
            let spriteFrame = PokerTableSeatBaseView.s_avatarSpriteCacheManager.findSpriteFrame(url)
            if(!spriteFrame){
                GameUtil.loadTexture(url, function (err, texture) {
                    if (texture != null) {
                        if (!self.isValid) {
                            cc.warn("网络加载头像 self.isValid")
                            return
                        }
                        let frame = new cc.SpriteFrame(texture)
                        frame.setOriginalSize(self.avatarSize)
                        PokerTableSeatBaseView.s_avatarSpriteCacheManager.addSpriteFrame(url, frame)
                        self.showAvatar(show, frame)                        
                    }
                });
            }
            this.showAvatar(show, spriteFrame)
            return
        }
        this.showAvatar(false)
    }

    showOwnerTag(show: boolean){
        this.spriteOwner.node.active = show
    }


    showReadyTag(show: boolean){
        this.spriteReady.node.active = show
    }

    resetSeatStyle(index: number){
        this.styleUiStateSwitcher.SwitchState(this.getSeatStyleName(index))
    }


    showSelect(show: boolean){
        if(!this.nodeSelect) return        
        this.nodeSelect.active = show
    }


    showClock(show: boolean, secs?: number, needShake?: boolean, onFinish?: Function){
        this.nodeClock.active = show
        if(this.m_clockScheduleId > 0){
            TimeManager.instance.cancelSchedule(this.m_clockScheduleId)
            this.m_clockScheduleId = 0
        }
        if(show){            
            this.refreshClockLable(secs, needShake, onFinish)
        }

    }


    protected refreshClockLable(secs: number, needShake?: boolean, onFinish?: Function){
        let self = this
        this.lableClock.string = secs.toString()
        if(secs <= 0){
            if(needShake){
                AudioManager.instance.vibrate()
            }
            if(onFinish){
                onFinish()
            }
            return
        }
        this.m_clockScheduleId = TimeManager.instance.doScheduleOnce(function(){
            self.m_clockScheduleId = 0
            secs--
            self.refreshClockLable(secs, needShake, onFinish)
        }, 1)
    }

    enableAvatarBtn(enable: boolean){
        if(!this.buttonAvatar) return
        this.buttonAvatar.enabled = enable
    }


    setClickAvatarBtnListener(callback: Function){
        this.mOnclickAvatarBtnCallback = callback
    }

    setClickNotSeatBtnListener(callback: Function){
        this.mOnclickNotSeatBtnCallback = callback
    }

    setClickKickBtnListener(callback: Function){
        this.mOnclickKickBtnCallback = callback
    }
    
    abstract getSeatStyleName(index: number):string;

 

    getChatTextWorldPos(){
        return this.nodeChatText.parent.convertToWorldSpaceAR(this.nodeChatText.getPosition())
    }



    onDestroy(){
        if(this.m_clockScheduleId > 0){
            TimeManager.instance.cancelSchedule(this.m_clockScheduleId)
            this.m_clockScheduleId = 0
        }
        this.unscheduleAllCallbacks()
    }
}
