import ViewBase from "../../../../framework/core/mvvm/view_base";
import { GameManager } from '../../../../manager/game_manager';

const { ccclass, property, menu} = cc._decorator;

@ccclass
@menu("package/hall/auth/authView")
export default class AuthView extends ViewBase {

    @property(cc.Node)
    browserLoginNode: cc.Node = null;

    @property(cc.EditBox)
    editBoxAccount: cc.EditBox = null;

    @property(cc.Node)
    buttonLogin: cc.Node = null;
    
    onLoad() {
        this.browserLoginNode.active = true
    }

}
