import Utility from "../utility/Utility";
import ModuleManager from "../framework/core/module_manager";

import {Base64} from "../utility/base64";
import Debug from "./debug";
import SdkFacebook from "../sdk/collection/sdk_facebook";
import SdkDefine from "../sdk/collection/sdk_define";
import SdkLine from "../sdk/collection/sdk_line";

export default class SdkManager extends cc.Component {
    
    private static _instance : SdkManager;
    public static get instance() : SdkManager {

        if (!SdkManager._instance) {
            
            SdkManager._instance = new SdkManager();
        }
        return SdkManager._instance;
    }

    public receive(base64Str : string){

        let json = JSON.parse(Utility.instance.decodeBase64(base64Str));        
        if (json.sdkName == SdkDefine.FACE_BOOK_NAME) {            

            SdkFacebook.instance.receive(json);

        }else if(json.sdkName == SdkDefine.LINE_NAME){

            SdkLine.instance.receive(json);
        }
        
    }

    public requestNativeFunc(skdName : string, funcName : string,jsonParam : string = "" ){
  
        let jsonObj = { "sdkName" : skdName, 
                        "funcName": funcName, 
                        "params": jsonParam };                                

        let jsonStr = JSON.stringify(jsonObj);

        if (cc.sys.os === cc.sys.OS_ANDROID) {

            let className = "org/cocos2dx/javascript/sdk/SDKManager";            
            jsb.reflection.callStaticMethod(className, "receiveJSCall","(Ljava/lang/String;)V",jsonStr);
        }      
    }

}

//@ts-ignore
window.sdkMgr = SdkManager.instance;


