    public addModule<T extends ModuleBase<ViewBase, ModelBase>>(T, moduleName: string, intentData?: any, onFinishLoad?: (module : ModuleBase<ViewBase, ModelBase>, state : boolean) => void): T {
        let tName = moduleName;
        let module : T = this._modules[tName];
        if (module == null) {
            let newNode = new cc.Node()
            newNode.name = tName
            let widget = newNode.addComponent(cc.Widget)
            widget.isAlignTop = true;
            widget.isAlignBottom = true;
            widget.isAlignLeft = true;
            widget.isAlignRight = true;
            widget.alignMode = cc.Widget.AlignMode.ONCE

            module = newNode.addComponent(T)
            if (module == null) {
                Debug.warn("addModule失败：" + tName)
                return null
            }
            module.moduleType = T
            module.moduleName = tName
            switch (module.layer) {
                case ViewLayer.Low:
                    newNode.width = this.low.width
                    this.low.addChild(newNode)
                    break;
                case ViewLayer.Mid:
                    newNode.width = this.middle.width
                    this.middle.addChild(newNode)
                    break;
                case ViewLayer.High:
                    newNode.width = this.high.width
                    this.high.addChild(newNode)
                    break;
                case ViewLayer.Highest:
                    newNode.width = this.highest.width;
                    this.highest.addChild(newNode);
                    break;
                default:
                    newNode.width = this.low.width
                    this.low.addChild(newNode)
                    break;
            }
            this._modules[tName] = module;
            module.node.setSiblingIndex(module.node.parent.childrenCount - 1)
            module.init(intentData, onFinishLoad);
        }
        return module
    }

    //跳转到某个模块，会把其他模块都隐藏
    goToModule<T>(T, intentData?: any, completeCallback?: Function, replaceModuleName?: string) {
        let self = this
        let moduleName = T.moduleClassName
        this.showModule(T, intentData, function () {
            self.hideAllModuleS(moduleName, true)
            if (completeCallback) {
                completeCallback()
            }
        }, replaceModuleName)
    }

    //replaceModuleName  替换模块名字 如：需要打开牌桌模块 我用匹配模块去转换
    showModule<T extends ModuleBase<ViewBase, ModelBase>>(T, intentData?: any, completeCallback?: (module : ModuleBase<ViewBase, ModelBase>, state : boolean) => void, replaceModuleName?: string): T {
        let moduleName = T.moduleClassName
        if (!moduleName) {
            console.error("请在模块Module类中添加@moduleClassName('模块的类名')装饰器", T)
            return
        }
        Debug.warn("showModule: " + moduleName)
        if (moduleName == "GamePlayerInfoModule") {
            return
        }
        if (replaceModuleName != null && replaceModuleName != "") {
            let module : T = this._modules[replaceModuleName];
            if (module != null) {
                module.resetModule()
                let node: cc.Node = module.node
                //TODO:这是什么逻辑呢？用处？
                if (node.childrenCount > 0) {//有可能View已经被销毁或者加载失败
                    // let T1 = this._moudleType[moduleName]
                    // let T2 = this._moudleType[replaceModuleName]
                    // node.removeComponent(T1)
                    // node.removeComponent(T2)
                    // this._modules[replaceModuleName] = null
                    // node.name = moduleName
                    // let newModule: any = node.addComponent(T1)
                    // this._modules[moduleName] = newModule
                    // newModule.moduleName = moduleName
                    // Debug.log("showModule: " + moduleName, replaceModuleName)
                    // newModule.node.setSiblingIndex(newModule.node.parent.childrenCount - 1)
                    // newModule.resetInit(node.children[0], intentData, completeCallback)
                    // module = null
                    // return newModule
                } else {
                    this._modules[moduleName] = null
                }
            }
        }

        let tName = moduleName;
        let module : T = this._modules[tName];
        if (module == null) {
            module = this.addModule(T, moduleName, intentData, completeCallback)
        } else { 
            module.node.setSiblingIndex(module.node.parent.childrenCount - 1)
            module.show(intentData, completeCallback)
        }
        return module
    }

    showModuleName(moduleName: string, intentData?: any, completeCallback?: (module : ModuleBase<ViewBase, ModelBase>, state : boolean) => void, replaceModuleName?: string) {
        Debug.warn("showModuleName：" + moduleName)
        let module : ModuleBase<ViewBase, ModelBase> = null
        if (replaceModuleName != null && replaceModuleName != "") {
            let module : ModuleBase<ViewBase, ModelBase> = this._modules[replaceModuleName];
            if (module != null) {
                module.resetModule()
                let node: cc.Node = module.node
                if (node.childrenCount > 0) {//有可能View已经被销毁或者加载失败
                } else {
                    this._modules[moduleName] = null
                }
            }
        }
        module = this._modules[moduleName];
        if (module == null) {
            if (moduleTypeNames[moduleName]) {
                let T = moduleTypeNames[moduleName]
                module = this.addModule(T, moduleName, intentData, completeCallback)
            } else {
                cc.error("showModuleName没找到对应的class：", moduleName)
            }
        } else {
            module.node.setSiblingIndex(module.node.parent.childrenCount- 1)
            module.show(intentData, completeCallback)
        }
        return module
    }


