    moduleHasActive(moduleName: string) {
        let module : ModuleBase<ViewBase, ModelBase> = this._modules[moduleName];
        if (module) {
            return module.nodeIsActive
        }
    }

    moduleLayerType(moduleName: string) {
        let module : ModuleBase<ViewBase, ModelBase> = this._modules[moduleName];
        if (module) {
            return module.layer
        }
    }


    setModuleActive(moduleName: string, needAdd) {
        let module : ModuleBase<ViewBase, ModelBase> = this._modules[moduleName];
        if (module) {
            module.setActive()
        } else {
            if (needAdd) {
                this.showModuleName(moduleName)
            }
        }
    }

    getModule(moduleName: string) {
        let module = this._modules[moduleName]
        return module
    }

    getModuleFromType<T extends ModuleBase<ViewBase, ModelBase>>(T) : T{
        let moduleName = T.moduleClassName;
        if (!moduleName) {
            Debug.error("请在模块Module类中添加@moduleClassName('模块的类名')装饰器", T)
            return;
        }

        return this.getModule(moduleName) as T;
    }

    hideModule(moduleName: string) {
        let module : ModuleBase<ViewBase, ModelBase> = this._modules[moduleName];
        if (module) {
            Debug.warn("hideModule：", moduleName)
            module.hide()
        }
    }

    hideAllModuleS(exclusiveMoudleName?: string, incluePublicPackage?: boolean) {
        for (let key in this._modules) {
            let module : ModuleBase<ViewBase, ModelBase> = this._modules[key]
            if (module == null || (exclusiveMoudleName && module.moduleName && exclusiveMoudleName == module.moduleName)) {
                continue;
            }
            if (!incluePublicPackage) {
                if (module.package != "public") {
                    module.hide()
                }
            } else {
                Debug.warn("hideModule：", module.moduleName)
                module.hide()
            }
        }
    }

    hideAllModule(except?: any) {
        let exceptList = ['Toast', 'NetPrompt']
        if (except) {
            if (except instanceof Array) {
                for (let i = 0; i < except.length; i++) {
                    exceptList.push(except[i]);
                }
            } else if (typeof (except) == 'string') {
                exceptList.push(except);
            }
        }
        for (const name in this._modules) {
            let needHide = true
            for (let i = 0; i < exceptList.length; i++) {
                if (name == exceptList[i]) {
                    needHide = false;
                    break;
                }
            }
            if (needHide) {
                this.hideModule(name);
            }
        }
    }

    //移除module，但是不销毁
    removeModule(moduleName: string, release?: boolean) {
        let module : ModuleBase<ViewBase, ModelBase> = this._modules[moduleName];
        if (module) {
            Debug.warn("removeModule：", moduleName)
            this._modules[moduleName] = null
            this._removeModules[moduleName] = module
        }
        return module
    }

    destroyRemoveModule(moduleName: string, release: boolean = false) {
        let module : ModuleBase<ViewBase, ModelBase> = this._removeModules[moduleName];
        if (module) {
            Debug.warn("destroyRemoveModule：", moduleName)
            module.destroyModule(release)
            this._removeModules[moduleName] = null
        }
        return module
    }

    destroyModule(moduleName: string, release: boolean = true) {
        let module : ModuleBase<ViewBase, ModelBase> = this._modules[moduleName];
        if (module) {
            Debug.warn("destroyModule：", moduleName)
            module.destroyModule(release)
            this._modules[moduleName] = null
        }
    }
    

    destroyAllModule(except?: any,release : boolean = true) {

        let exceptList = except ? this.allwaysInMemory.concat(except) : this.allwaysInMemory;
        // if (except) {
        //     if (except instanceof Array) {
        //         for (let i = 0; i < except.length; i++) {
        //             this.destroyAllModuleExceptList.add(except[i]);
        //         }
        //     } else if (typeof (except) == 'string') {
        //         this.destroyAllModuleExceptList.add(except);
        //     }
        // }

        for (const name in this._modules) {
            let needDestory = true;
            for (let index = 0; index < exceptList.length; index++) {
                const element = exceptList[index];
                if (element == name) {
                    needDestory = false;
                    break;
                }
            }
            if (needDestory) {
                this.destroyModule(name,release);
            }
            
        }
        //this.hideModule("NetPrompt")
        this.hideModule("Dialog")
    }

    //销毁整个package
    destroyPackage(packageName : string) {
        let needDestroyModuleName
        for (const key in this._modules) {
            let module  = this._modules[key]
            if (module.packageName == packageName) {
                needDestroyModuleName = needDestroyModuleName ? needDestroyModuleName : []
                needDestroyModuleName.push(key)
            }
        }

        if (needDestroyModuleName) {
            for (const moduleName in needDestroyModuleName) {
                this.destroyModule(moduleName)
            }
        }
    }

    //隐藏package下的所有模块
    hidePackage(packageName : string) {
        for (const key in this._modules) {
            let module  = this._modules[key]
            if (module.packageName == packageName) {
                module.hide()
            }
        }
    }

