    addPreloadResUrl(resUrl) {

    }



    preLoadRes(completeCallback?: Function, progressCallback?: Function) {
        let self = this
        if (self._asynLoadResCount < self._asynPath.length) {
            let path = self._asynPath[self._asynLoadResCount]
            GameUtil.loadRes(path, (error: Error, resource: any) => {
                ++self._asynLoadResCount;
                if (error) {
                    Debug.error(error)
                } else {
                    Debug.log("预加载资源成功：", path)
                }
                self.preLoadRes(completeCallback, progressCallback)
                if(progressCallback)
                {
                    progressCallback(self._asynLoadResCount, self._asynPath.length)
                }
            })
        }else
        {
            if(completeCallback)
            {
                completeCallback();
            }
        }
    }
