import ViewBase from "../../../../framework/core/mvvm/view_base";
import ModuleManager from "../../../../framework/core/module_manager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PublicRuleView extends ViewBase {

    @property(cc.RichText)
    ruleRichText: cc.RichText = null
    @property(cc.Node)
    closeBtnNode: cc.Node = null

    @property(cc.Node)
    leftToggleRoot: cc.Node = null
    @property(cc.Node)
    topToggleRoot: cc.Node = null

    _leftCurrentSelect = 1
    _topCurrentSelect = 1
    _textArr: string[][] = [[]]
    onLoad() {
        this.closeBtnNode.on("click", () => {
            ModuleManager.instance.hideModule("PublicRule")
        })
        let leftTabs = this.leftToggleRoot.children
        for (let i = 0; i < leftTabs.length; i++) {
            const tab = leftTabs[i]
            tab.off("toggle", this.onLeftTabClick, this)
            tab.on("toggle", this.onLeftTabClick, this)
        }

        let topTabs = this.topToggleRoot.children
        for (let i = 0; i < topTabs.length; i++) {
            const tab = topTabs[i]
            tab.off("toggle", this.onTopTabClick, this)
            tab.on("toggle", this.onTopTabClick, this)
        }

        // text
        this._textArr.push([])
        this._textArr[1][1] = "1、斗地主是一种三人游戏，对局开始时通过叫地主决定\n" + "阵营，阵营双方对战，先出完牌的一方获胜。\n" +
            " 2、地主拥有优先出牌权，玩家按逆时针方向跟牌，每位\n" + "玩家可以选择用更大的牌组跟牌或不跟。\n"
        this._textArr[1][2] = "1、单牌：单张的牌（例如黑桃K）\n" +
            "2、对子：牌面数字相同的2张牌（例如黑桃K+红桃K）\n" +
            "3、三张：牌面数字相同的3张牌（例如黑桃K+红桃K+梅花\n" + "K）\n" +
            "4、三带一：牌面数字相同的3张牌+1张单牌或对子（例如\n" + "KKK+8 或 QQQ+44）\n" +
            "5、飞机：2个或更多的连续三张牌型（例如：333444 或 \n" + "555666777）\n" +
            "6、飞机带翅膀：飞机+同数量的单牌 或 飞机+同数量的对\n" + "子（例如：333444+78 或 444555+7788）\n" +
            "7、顺子：5张或以上的连续单牌，2不可以和其它牌组成顺\n" + "子（例如78910J，3456789）\n" +
            "8、连对：3对或以上的连续对子牌型，2不可以和其它牌组\n" + "成连对（例如334455，991010JJQQ）\n" +
            "9、四带二：牌面数字相同的4张牌+2张不同单牌或2个对子\n" + "（例如KKKK+89，8888+KKAA）\n" +
            "10、炸弹：牌面数字相同的4张牌（例如KKKK，QQQQ，\n" + "3333）\n" +
            "11、王炸：双王，即大小王，最大的牌\n"
        this._textArr.push([])
        this._textArr[2][1] = "1、不洗牌玩法的牌型规则与经典玩法相同，但发牌方式\n" + "不同。\n" +
            "2、在不洗牌玩法中，玩家打完一局后，所有的牌将不会\n" + "洗牌，按一定顺序切牌后依次发给下一桌玩家。\n" +
            "3、玩法特点1：手牌牌型更加整齐。\n" +
            "4、玩法特点2：拿到炸弹的可能性更高。\n" +
            "5、玩法特点3：单局耗时更短，更刺激，更流畅！\n"
        this._textArr[2][2] = "1、同经典玩法"
    }

    private onLeftTabClick(event: cc.Button) {
        this._leftCurrentSelect = Number(event.node.name)
        cc.log("点击了", this._leftCurrentSelect)
        this.ruleRichText.string = this._textArr[this._leftCurrentSelect][this._topCurrentSelect]
    }

    private onTopTabClick(event: cc.Button) {
        this._topCurrentSelect = Number(event.node.name)
        this.ruleRichText.string = this._textArr[this._leftCurrentSelect][this._topCurrentSelect]
    }

    showRule() {
        this.ruleRichText.string = this._textArr[this._leftCurrentSelect][this._topCurrentSelect]//content
    }

}