import Module, { moduleInit } from "../../../../framework/core/mvvm/module_base";
import { PdkModel } from "./pdk_model";
import GameServer from "../../../server/gameserver/game_server";
import { PdkNetRoute } from "../../net/pdk_route";
import { PdkView } from "./pdk_view";
import SceneManager from "../../../../manager/scene_manager";
import { PConst } from "./data/pdk_const";
import { CardPattern } from "./poker/poker_pattern";
import ModelData from "../../../public/model_data";
import ModuleManager from "../../../../framework/core/module_manager";
import { PdkSound } from "../component/pdk_table_sound";
import UserDataManager from "../../../../manager/user_manager";
import { PokerRecommend } from "./poker/poker_recommend";
import { PokerSet } from "./poker/poker_logic_set";
import PublicRule from "../rule/public_rule";

const {ccclass, property} = cc._decorator;

@ccclass
export class PdkModuleBase extends Module<PdkView,PdkModel> {
    constructor(){
        super()
        this.viewType = PdkView;
        this.modelType = PdkModel;
        this.windowPrefabResPath = 'package/pdk/module/table/pdk';
    }

    onInit(){
        this.view.menueMask.on('click',this.clickMenueMask,this);
        let self = this;
        this.view.myhandPokers.setCustomOnFinishDragPokerReselectFunc(function () {
            self.on_finish_drag_pokers_reselect_fun()
        })
        this.view.myhandPokers.setCustomOnSelectPokersChanged(()=>{
            self.on_select_pokers_changed();
        })
    }

    onShow(){
        this.model.netClient = GameServer.instance.netClient
        this.model.clearMsgEvent()
        this.registerNetMsg();
    }

    registerNetMsg(){
        let self = this;
        self.model.registerNetMsg(PdkNetRoute.onSendParameter,function(msg:any){
            self.onSendParameter(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onSendGameScene,function(msg:any){
            self.onSendGameScene(msg)
        });
        
        self.model.registerNetMsg(PdkNetRoute.onUserEntryRoom,function(msg:any){
            self.onUserEntryRoom(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onStartGame,function(msg:any){
            self.onStartGame(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.readyGame,function(msg:any){
            self.readyGame(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onReadyGame,function(msg:any){
            self.onReadyGame(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.playCard,function(msg:any){
            self.playCard(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onOutCard,function(msg:any){
            self.onOutCard(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onPassCard,function(msg:any){
            self.onPassCard(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onDissolveRoom,function(msg:any){
            self.onDissolveRoom(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onSettlement,function(msg:any){
            self.onSettlement(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onHandCardUser,function(msg:any){
            self.onHandCardUser(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onOutCardNotify,function(msg:any){
            self.onOutCardNotify(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onWarnUser,function(msg:any){
            self.onWarnUser(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.onLeaveRoom,function(msg:any){
            self.onLeaveRoom(msg)
        });

        self.model.registerNetMsg(PdkNetRoute.leaveRoom,function(msg:any){
            self.leaveRoom(msg)
        });
    }

    onWarnUser(msg:any){

    }

    onSendGameScene(msg:any){
        this.initTable(msg);
        this.recommendFunc = null;
        this.model.mCommonData.lastPattern = null;
    }

    //初始化牌桌
    initTable(msg:any){
        this.model.initModel(msg);
    }

    playCard(msg:any){
        if (msg.code == 1) {
            ModuleManager.instance.toast('没有轮到自己出牌');
        }else if (msg.code == 2) {
            ModuleManager.instance.toast('出牌类型错误');
        }
    }

    //刷新打出牌
    onOutCard(msg:any){
        this.view.hidePlayerTag(4);
        let viewid = this.model.viewLocation(msg.outcardUser);
        this.view.showHandCards(viewid,true,msg.cardData,true);
        if (this.model.isRole(msg.outcardUser)) {
            this.view.changeBtnState('none');   
        }
        this.view.showSeatClock(viewid,false);
        //牌型 检测
        this.model.mCommonData.chairID = msg.outcardUser;
        this.model.mCommonData.outCards = msg.cardData;
    }

    onPassCard(msg:any){
        this.view.hidePlayerTag(4);
        let viewid = this.model.viewLocation(msg.wPassUser);
        this.view.showSeatPassAnim(viewid,true);
        this.view.showHandCards(viewid,false);
        PdkSound.playPassSound(UserDataManager.instance.isMale());
    }

    onDissolveRoom(msg:any){
        
    }

    onSettlement(msg:any){

    }

    //刷新手牌
    onHandCardUser(msg:any){
        let viewid = this.model.viewLocation(msg.wChairID);
        if (viewid == 0) {
            this.view.myhandPokers.showPokers(true,msg.handCardData);
            this.model.setPokerList(msg.handCardData);
        }
    }

    onLeaveRoom(msg:any){

    }

    leaveRoom(msg:any){
        if (msg.code == 0
            || msg.code == 3) {
            SceneManager.gotoHall();
        }else if (msg.code == 2) {
            ModuleManager.instance.toast('游戏已经开始不能离开牌桌');
        }
    }

    //刷新打出牌
    onOutCardNotify(msg:any){
        
    }

    onUserEntryRoom(msg:any){
        this.model.enterUser(msg);
    }

    onSendParameter(msg:any){
        this.model.initRoomInfo(msg);

    }

    onStartGame(msg:any){
    }

    readyGame(msg:any){

    }

    onReadyGame(msg:any){
        
    }

    on_select_pokers_changed(){
        cc.log('-->>>>>>>>>>poker changed')
        let selectedData = this.view.myhandPokers.getSelectedPokers()
        let selected_codeList = selectedData.selected_codeList
        let result = this.checkCanChuPai(this.model.mCommonData.lastPattern,selected_codeList);
        this.view.isForbidOutBtn(!result.canChuPai || !result.pattern);
    }

    on_finish_drag_pokers_reselect_fun(){
        cc.log('0000--');
 
        let self = this
        let selectedData = self.view.myhandPokers.getSelectedPokers()
        let selected_codeList = selectedData.selected_codeList
        if (selected_codeList.length > 0) {
            let lastPattern = null//this.model.mCommonData.lastPattern || null
            let pattern = this.model.mySeatInfo.handSet.choose_hintIterator(selected_codeList, lastPattern)
            if (pattern) {
                self.view.myhandPokers.selectPokers(pattern.cards, null)
                //self.isPlayingSelectPokerAnim = true
                self.view.myhandPokers.refreshPokerSelectState(true, function () {
                    //self.isPlayingSelectPokerAnim = false
                })
                //检测牌型
                let result = this.checkCanChuPai(this.model.mCommonData.lastPattern,pattern.cards);
                this.view.isForbidOutBtn(!result.canChuPai || !result.pattern);
            }
        }
    }

    protected clickMenueMask(){
        this.view.menueRoot.active = false;
    }

    protected onButtonClick(node: cc.Node, customEventData){
        let pName = node.name;
        switch (pName) {
            case 'BtnLeave':
                this.view.showMenue()
                break;
            case 'LeavelButton':
                SceneManager.gotoHall(false);
                break;
            case 'ChuPaiMask':
                this.resetPokers();
                break;
            case 'BtnReady':
                this.model.send_readyGame();
                break;
            case 'FreeButton':
                this.model.send_leaveRoom();
                break;
            case 'HintButton'://提示
                this.onClickRemindCard(false);
                break;
            case 'OutButton'://出牌
                this.onClickSendCard();
                break;
            case 'RuleButton':
                ModuleManager.instance.showModule(PublicRule) 
                break;
            default:
                break;
        }
    }

    //出牌
    protected onClickSendCard(){
        let selectedData = this.view.myhandPokers.getSelectedPokers();

        if (!selectedData.real_selected_codeList || selectedData.real_selected_codeList.length == 0) {
            ModuleManager.instance.toast('请选择要出的牌')
            return
        }

        this.recommendFunc = null;
        let codes = selectedData.selected_codeList;
        this.model.send_playCard(selectedData.selected_codeList,codes.length)

    }

    recommendFunc:Function = null;
    //提示出牌
    protected onClickRemindCard(isHaveMask:boolean){
        let self = this;
        if (!this.recommendFunc) {
            let tishiData = this.model.mySeatInfo.handSet.hintIterator(this.model.mCommonData.lastPattern);
            if (!tishiData.tishiFun ) {
                return;
            }
            this.recommendFunc = tishiData.tishiFun;
        }

        let pattern = this.recommendFunc();
        if (!pattern) {
            return;
        }
       
        self.view.myhandPokers.selectPokers(pattern.cards, null)

        self.view.myhandPokers.refreshPokerSelectState(true)

        //检测牌型
        let result = this.checkCanChuPai(this.model.mCommonData.lastPattern,pattern.cards);
        this.view.isForbidOutBtn(!result.canChuPai || !result.pattern);

        //自动出牌服务器支持 即可
        // if (this.model.mySeatInfo.handSet.card_cnt == pattern.cards.length) {//执行自动出牌
        //     this.model.send_playCard(pattern.cards,pattern.cards.length)
        // }
    }

    //检测牌型是否正确

    protected checkCanChuPai(srcPattern, codeList) {
        let result = {
            canChuPai: false,
            pattern: null,
        }
        let selectedPattern = CardPattern.newPattern(codeList)
        if (!selectedPattern) {
            return result
        }
        if (!srcPattern) {
            result.canChuPai = true
            result.pattern = selectedPattern
            return result
        } else {
            let pattern = selectedPattern
            let compable = pattern.compable(srcPattern)
            if (!compable) {
                return result
            } else {
                let le = pattern.le(srcPattern)
                if (le) {
                    return result
                } else {
                    result.canChuPai = true
                    result.pattern = pattern
                    return result
                }
            }
        }
    }

    protected resetPokers(){
        this.view.myhandPokers.resetPokers(true);
        this.view.isForbidOutBtn(true);
    }

    protected playPokerTypeSound(){
        
    }
}
