import ModelBase from "../../../framework/core/mvvm/model_base";
import { ClubRoute } from "../net_clubRoute";

const {ccclass, property} = cc._decorator;

@ccclass
export class ClubRankModel extends ModelBase {

    curPageNum:number = 1;
    pageCeilNum:number = 6;
    totalPageNum:number = 0;
   
    send_getRankList(rankType:number, pageIdx:number, pageNum:number){
        let data = {
            rankType:rankType,
            pageIdx:pageIdx,
            pageNum: pageNum,
        }
        this.send(ClubRoute.getRankList,data);
    }
}
