import ModuleManager from "../framework/core/module_manager";
import AppData from "../pacakge/public/const/app_data";

import AuthModule from "../pacakge/hall/module/auth/auth_module";
import SceneManager from "../manager/scene_manager";
import CustomUIStateSwitcher from "../pacakge/public/ui/custom_ui_state_switcher";
import { PdkRoomConfig } from "../pacakge/pdk/module/room_config";
import { CreateRoomModule } from "../pacakge/hall/module/createroom/createroom_module";
import LoadingModule from "../pacakge/public/loading/loading";
import GameUtil from "../utility/game_util";
import EventManager from "../manager/event_manager";
const { ccclass, property } = cc._decorator;

@ccclass
export default class Init extends cc.Component {
    @property(cc.Canvas)
    canvas: cc.Canvas = null;

    curDR : cc.Size

    onLoad() {

        this.resize()

        cc.view.setResizeCallback(() => {
            cc.log("setResizeCallback", cc.view.getFrameSize())
            EventManager.instance.dispatch('_resize_');
            this.resize()
        })

        SceneManager.init();
        
        AppData.setServerByData()
        
        if (cc.sys.isNative) {
            
            ModuleManager.instance.showModule(LoadingModule)
        }else{
            GameUtil.loadResArray(["package/hall/module/auth/AuthModule",], function (error: Error, resource: any[]) {
                if (error) {
                    //TODO:需要处理的异常
                } else {
                    ModuleManager.instance.showModule(AuthModule)
                }
            })
        }
    }

    resize() {
         // var cvs = cc.find('Canvas').getComponent(cc.Canvas);
         let canvas = this.canvas
         //保存原始设计分辨率，供屏幕大小变化时使用
         if (!this.curDR) {
             this.curDR = canvas.designResolution;
         }
         var dr = this.curDR;
         var s = cc.view.getFrameSize();
         var rw = s.width;
         var rh = s.height;
         var finalW = rw;
         var finalH = rh;
 
         if ((rw / rh) > (dr.width / dr.height)) {
             //!#zh: 是否优先将设计分辨率高度撑满视图高度。 */
             //cvs.fitHeight = true;
 
             //如果更长，则用定高
             finalH = dr.height;
             finalW = finalH * rw / rh;
         }
         else {
             /*!#zh: 是否优先将设计分辨率宽度撑满视图宽度。 */
             //cvs.fitWidth = true;
             //如果更短，则用定宽
             finalW = dr.width;
             finalH = rh / rw * finalW;
         }
         canvas.designResolution = cc.size(finalW, finalH);
         canvas.node.width = finalW;
         canvas.node.height = finalH;
         // canvas.node.emit('resize');
 
         if (true) {
             let frameSize = s;
             AppData.isIphoneX = frameSize.width / frameSize.height >= 2
             // AppData.isIphoneX = false
             AppData.designResolution = canvas.designResolution
             AppData.iphonexOffset = AppData.designResolution.width * (44 / 812)//偏移齐刘海宽度
         }
         cc.log("canvas", canvas.designResolution, AppData.iphonexOffset)
    }

    loadiPhoneX(){
        if (CC_PREVIEW && AppData.iphonexOffset) {
            GameUtil.loadRes("package/public/iPhoneX", function (err, prefab: cc.Prefab) {
                let newNode = cc.instantiate(prefab)
                ModuleManager.instance.highest.addChild(newNode);
            })
        }
    }
}
