import ViewBase from "../../../framework/core/mvvm/view_base";
import TextureManager from "../../../manager/texture_manager";
import UserDataManager from "../../../manager/user_manager";
import GameUtil from "../../../utility/game_util";
import Utility from "../../../utility/Utility";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ClubRankView extends ViewBase {
    @property(cc.Label)
    Label_rank:cc.Label = null;

    @property(cc.Sprite)
    Sprite_head:cc.Sprite = null;

    @property(cc.Label)
    Label_name:cc.Label = null;

    @property(cc.Label)
    Label_coins:cc.Label = null;

    @property(cc.Label)
    Label_pageIdx:cc.Label = null;

    @property(cc.Sprite)
    Sprite_rankNode:cc.Sprite = null;

    @property(cc.Node)
    Sprite_item:cc.Node = null;

    onShow(){
        let data = UserDataManager.instance.getUserData();
        this.Label_name.string = data.name;
        this.Label_coins.string = Utility.instance.formateNumb(data.coin).toString();
        TextureManager.instance.getSprite(data.avatarUrl,(sp:cc.SpriteFrame)=>{
            this.Sprite_head.spriteFrame = sp;
        })
    }

    loadRankList(data:any, curPageNum:number, pageCeilNum:number, myRank:number) {
        if (myRank == 0) {
            this.Label_rank.string = '榜外';
        } else {
            this.Label_rank.string = myRank.toString();
        }
        this.Label_pageIdx.string = curPageNum.toString();
        this.Sprite_rankNode.node.removeAllChildren()
        for (let i = 0; i < data.length; i++) {
            const item = data[i];
            let Sprite_item = cc.instantiate(this.Sprite_item);
            Sprite_item.active = true;
            Sprite_item.parent = this.Sprite_rankNode.node;
            Sprite_item.x = 0;
            Sprite_item.y = -50 - i * 82;

            let Sprite_front = Sprite_item.getChildByName('Sprite_front').getComponent(cc.Sprite);
            let Label_before = Sprite_item.getChildByName('Label_before').getComponent(cc.Label);
            let Sprite_head = Sprite_item.getChildByName('Sprite_head').getComponent(cc.Sprite);
            let Label_name = Sprite_item.getChildByName('Label_name').getComponent(cc.Label);
            let Label_coins = cc.find("Sprite_coinIcon/Label_coins", Sprite_item).getComponent(cc.Label);
            TextureManager.instance.getSprite(item.avatarUrl,(sp:cc.SpriteFrame)=>{
                Sprite_head.spriteFrame = sp;
            })
            Label_name.string = item.name;
            Label_coins.string = Utility.instance.formateNumb(item.coin).toString();;

            let rank = i + 1 + (curPageNum - 1) * pageCeilNum;
            if (rank < 4) {
                Sprite_front.node.active = true;
                Label_before.node.active = false;
                TextureManager.instance.getLocalSprite("club_rank{0}.png".format(rank), (spriteFrame:cc.SpriteFrame)=>{
                    Sprite_front.spriteFrame = spriteFrame;
                });
            } else {
                Sprite_front.node.active = false;
                Label_before.node.active = true;
                Label_before.string = rank.toString();
            }
        }
    }
}
