import ViewBase from "../../framework/core/mvvm/view_base";
import { ListView, ListViewDir } from "../../framework/tools/scrollview/listview";
import SceneManager from "../../manager/scene_manager";
import { ClubInfo } from "./component_club";
import CustomUIStateSwitcher from "../public/ui/custom_ui_state_switcher";
import LoaclStorage from "../../manager/local_storage";
import { TableInfo } from "./component_table";
import HallServer from "../server/hallserver/hall_server";
import MarqueeComponent from "./component_marquee";
import { Club } from "./club_data";
import TextureManager from "../../manager/texture_manager";
import UserDataManager from "../../manager/user_manager";
import Utility from "../../utility/Utility";
import ModuleManager from "../../framework/core/module_manager";
import Toast from "../public/module/toast/toast";


const {ccclass, property} = cc._decorator;

@ccclass
export class ClubView extends ViewBase {

    @property(cc.ScrollView)
    scrollview: cc.ScrollView = null;

    @property(cc.Node)
    view: cc.Node = null;

    @property(cc.Node)
    content: cc.Node = null;

    @property(cc.Node)
    prefab: cc.Node = null;

    m_tablelist: ListView = null

    @property(cc.Label)
    clubName:cc.Label = null;

    mTables:Club.ITableInfo[] = [];//所有的table

    @property(cc.Node)
    Sprite_leftFrame: cc.Node = null;

    @property(cc.Node)
    Node_top:cc.Node = null;

    @property(cc.Sprite)
    Sprite_modeIcon:cc.Sprite = null;

    @property(cc.Node)
    Node_tips:cc.Node = null;

    @property(cc.Sprite)
    Sprite_scolle:cc.Sprite = null;

    @property(cc.Toggle)
    Toggle_all:cc.Toggle = null;

    @property(cc.Toggle)
    Toggle_pdk:cc.Toggle = null;

    onShow(){
        let gameId = LoaclStorage.getNumber('_club_gameId', 0);
        if (gameId == 0) {
            this.Toggle_all.isChecked = true;
        } else {
            this.Toggle_pdk.isChecked = true;
        }

        this.Node_top.active = false;
        this.Node_top.y = 80;
        this.Sprite_leftFrame.active = false;
        this.Sprite_leftFrame.x = -767;
    }

    onLoad(){
        //创建tablelist
        let list: ListView = new ListView({
            scrollview: this.scrollview,
            view: this.view,
            content: this.content,
            item_prefab: this.prefab,
            cb_host: this,
            item_update_func: this.update_table_list_item,
            row: 2,
            gap_x: 18,
            direction: ListViewDir.Horizontal,
        });
        this.m_tablelist = list

        // toggle回调
        this.Toggle_all.node.on('toggle', this.toggleCB, this);
        this.Toggle_pdk.node.on('toggle', this.toggleCB, this);
    }

    clickClubFunc:Function = null;
    setClickClubFunc(func:Function){
        this.clickClubFunc = func;
    }

    clickToggleFunc:Function = null;
    toggleCB(toggle) {
        if (this.clickToggleFunc) {
            this.clickToggleFunc(toggle);
        }
    }

    //移除牌桌
    removeTable(data:Club.ITableInfo[]){
        for (let i = 0; i < data.length; i++) {
            let tbData = data[i];
            for (let idx = 0; idx < this.m_tablelist.datas.length; idx++) {
                let tb = this.m_tablelist.datas[idx];
                if (tb.tableid == tbData.tableid) {
                    this.m_tablelist.remove_data(idx,1);
                    this.mTables = this.mTables.splice(idx,1)
                    break;
                }
            }
        }
    }

    //新增牌桌
    appendTable(datas:Club.ITableInfo[], isSort:boolean){
        if (!datas || datas.length <= 0) {
            return;
        }

        let adds:Club.ITableInfo[] = [];
        for (let idx = 0; idx < datas.length; idx++) {
            let tb = datas[idx];
            let isadd = true;
            for (let i = 0; i < this.mTables.length; i++) {
                let mtb = this.mTables[i];
                if (tb.tableid == mtb.tableid) {
                    this.mTables[i] = tb;
                    isadd = false;
                    break
                }
            }
        
            if (isadd) {
                cc.log('----->>>>>>新增',tb);
                adds.push(tb);
            }
        }
        if (adds.length > 0) {//新增
            this.mTables = this.mTables.concat(datas);
            for (let i = 0; i < adds.length; i++) {
                this.m_tablelist.append_data(adds[i]);
            }
        }

        //排序
        if (isSort) {
            Club.rank(this.mTables);
        }
        this.refreshTableItem();
    }

    //更新所有table
    refreshTableItem(){
        for (let idx = 0; idx < this.m_tablelist.datas.length; idx++) {
            let tb = this.mTables[idx];
            this.m_tablelist.refresh_item(idx,tb);
        }
    }

    removeTableItem(tableid:number){
        for (let idx = 0; idx < this.m_tablelist.datas.length; idx++) {
            let tb = this.mTables[idx];
            if (tb.tableid == tableid) {
                this.m_tablelist.remove_data(idx,1);
                this.mTables.splice(idx,1);
            }
        }
    }


    //更新我选择的俱乐部信息
    updateClubInfo(data:any){
        this.clubName.string = data.clubName;
        let Sprite_coinFrame = cc.find("Sprite_topFrame/Sprite_coinFrame", this.Node_top);
        let Label_coin = Sprite_coinFrame.getChildByName('Label_coin').getComponent(cc.Label);
        let userData = UserDataManager.instance.getUserData();
        Label_coin.string = Utility.instance.formateNumb(userData.coin).toString();
    }

    enterClubAction(gameMode:number) {
        if (gameMode == 0) {
            TextureManager.instance.getLocalSprite('club_111',(spriteFrame:cc.SpriteFrame)=>{
                this.Sprite_modeIcon.spriteFrame = spriteFrame;
            });
        } else {
            TextureManager.instance.getLocalSprite('club_118',(spriteFrame:cc.SpriteFrame)=>{
                this.Sprite_modeIcon.spriteFrame = spriteFrame;
            });
        }

        if (gameMode == 0) {
            // 进入金币厅
            let Sprite_scoreFrame = cc.find("Sprite_topFrame/Sprite_scoreFrame", this.Node_top);
            let Sprite_coinFrame = cc.find("Sprite_topFrame/Sprite_coinFrame", this.Node_top);
            Sprite_scoreFrame.active = false;
            Sprite_coinFrame.active = true;
            let Sprite_butomFrame = this.node.getChildByName('Sprite_butomFrame');
            let Node_vector = Sprite_butomFrame.getChildByName('Node_vector');
            let vector = Node_vector.children;
            for (let i = 0; i < vector.length; i++) {
                const node = vector[i];
                node.active = false;
            }
            this.Node_tips.active = false;
            let Button_rank = Node_vector.getChildByName('Button_rank');
            let Button_personCard = Node_vector.getChildByName('Button_personCard');
            let Button_partner = Node_vector.getChildByName('Button_partner');
            Button_rank.active = true;
            Button_personCard.active = true;
            Button_partner.active = true;
            Button_partner.x = 346;

        } else {
            // 进入积分厅
            let Sprite_scoreFrame = cc.find("Sprite_topFrame/Sprite_scoreFrame", this.Node_top);
            let Sprite_coinFrame = cc.find("Sprite_topFrame/Sprite_coinFrame", this.Node_top);
            Sprite_scoreFrame.active = true;
            Sprite_coinFrame.active = false;
            let Sprite_butomFrame = this.node.getChildByName('Sprite_butomFrame');
            let Node_vector = Sprite_butomFrame.getChildByName('Node_vector');
            let vector = Node_vector.children;
            for (let i = 0; i < vector.length; i++) {
                const node = vector[i];
                node.active = true;
            }
            this.Node_tips.active = true;
            let Button_rank = Node_vector.getChildByName('Button_rank');
            Button_rank.active = false;
            let Button_partner = Node_vector.getChildByName('Button_partner');
            Button_partner.x = 116;

        }
        this.Node_top.active = true;
        this.Node_top.stopAllActions();
        this.Node_top.y = 80;
        this.Node_top.runAction(cc.moveTo(0.3, this.Node_top.x, 0));
        this.Sprite_leftFrame.active = true;
        this.Sprite_leftFrame.stopAllActions();
        this.Sprite_leftFrame.x = -767;
        this.Sprite_leftFrame.runAction(cc.moveTo(0.3, -667, this.Sprite_leftFrame.y));
    }

    clearTableList(){
        this.m_tablelist.set_data([]);
    }

    update_table_list_item(item: cc.Node, data: any, index: number,item_intact ?:any): void {
        if (data) {
            let tableInfo = item.getComponent(TableInfo);
            tableInfo.clickFunc = this.clickTableItem.bind(this);
            tableInfo.setData(data,index); 
        }
    }

    checkIsCanMoney(data:any) {
		let isCan = false;
		if (data.gameMode == 0) {
            // 金币厅
            let userData = UserDataManager.instance.getUserData();
			let coin = userData.coin;
			let lowerLimit = data.lowerLimit;
			let upperLimit = data.upperLimit;
			if (upperLimit == 0) {
				if (coin >= lowerLimit) {
					isCan = true;
				}
			} else {
				if (coin >= lowerLimit && coin <= upperLimit) {
					isCan = true;
				}
			}
		}
		else {
			// 积分厅
		}
		return isCan;
    }

    clickTableItem(data:Club.ITableInfo){
        cc.log('data',data)
        SceneManager.lastEntranceOpenModuleName = 'ClubModule'
        if (data.tbType == Club.TableType.PWTable) {
            if (!this.checkIsCanMoney(data.playWay)) {
                ModuleManager.instance.showModule(Toast,'您金币不足, 当前桌子限制{0}.'.format(data.playWay.lowerLimit));
                return;
            }
            let pwInfo = data.playWay;
            HallServer.instance.createGame(pwInfo.gameId,pwInfo.clubId,pwInfo.id.toString())
        }else if (data.tbType == Club.TableType.GameTable) {
            if (data.gameInfo.players.length >= data.gameInfo.chairCount) {
                ModuleManager.instance.showModule(Toast,'当前桌子人数已满.');
                return;
            }

            if (!this.checkIsCanMoney(data.gameInfo)) {
                ModuleManager.instance.showModule(Toast,'您金币不足, 当前桌子限制{0}.'.format(data.gameInfo.lowerLimit));
                return;
            }
            let gamInfo = data.gameInfo;
            HallServer.instance.enterGame(gamInfo);
        }
    }

    onDisable(){
        // this.hideAction();
    }

    on_destroy(){
        // this.hideAction();
    }
}
