import ViewBase from "../../../../framework/core/mvvm/view_base";
import { PdkMySeatPokers } from "../component/pdk_myseat_pokers";
import PokerTableMySeatPokers from "../../../public/poker_table/poker_myseat_pokers";
import PokerTableSeatBaseView from "../../../public/poker_table/poker_table_seat_base_view";
import { PdkHelp } from "../help/pdk_help";
import PdkSeatPokers from "../component/pdk_seat_pokers";
import { Card, CardNameIndex, CardColor } from "./poker/poker_data";
import GameUtil from "../../../../utility/game_util";
import { PConst } from "./data/pdk_const";
import AudioManager from "../../../../manager/audio_manager";
import TextureManager from "../../../../manager/texture_manager";
import { PdkData, SeatExtendHolder } from "./data/pdk_data";
import UIStateSwitcher from "../../../public/ui/ui_state_switcher";
import CustomUIStateSwitcher from "../../../public/ui/custom_ui_state_switcher";
import { PdkSound } from "../component/pdk_table_sound";
import SceneManager from "../../../../manager/scene_manager";
import Utility from "../../../../utility/Utility";

const {ccclass, property} = cc._decorator;

@ccclass
export class PdkBaseView extends ViewBase {
    readonly effect_offset_pos = cc.v2(0, -30)
    @property(PokerTableMySeatPokers)
    myhandPokers: PdkMySeatPokers = null

    @property(PokerTableSeatBaseView)//玩家座位信息预制
    prefabSeatView: PokerTableSeatBaseView = null

    @property([cc.Node])//玩家座位信息父节点
    nodeSeatParentList: cc.Node[] = []

    @property([PdkSeatPokers])
    seatHandPokersList: PdkSeatPokers[] = []

    @property([PdkSeatPokers])
    seatPokersList: PdkSeatPokers[] = []

    @property(cc.Node)
    nodechunTianAnim: cc.Node = null

    @property(cc.Node)
    hinitBtn:cc.Node = null;

    @property(cc.Node)
    OutButton:cc.Node = null;

    @property(cc.Node)
    nodeClock:cc.Node = null;

    @property(cc.Label)
    labelClock: cc.Label = null
    
    @property(CustomUIStateSwitcher)
    btnSwitchui:CustomUIStateSwitcher = null;

    @property(SeatExtendHolder)
    seatExtendViewList: SeatExtendHolder[] = []

    protected m_clockScheduleId: number = 0

    private m_allSeatViewList: PokerTableSeatBaseView[] = []
    protected mPosExtendSeatMap: SeatExtendHolder[] = []
    onLoad(){
        cc.log('pdkbaseviewonloaded');
        this.initAllSeatView();
        this.btnSwitchui.SwitchState('none');
        this.myhandPokers.setCustomGetSpriteFrameKeyByCodeFunc(this.getPokerSpriteKeyByCode.bind(this))
        for (let handPokers of this.seatHandPokersList) {
            handPokers.setCustomGetSpriteFrameKeyByCodeFunc(this.getPokerSpriteKeyByCode.bind(this));
        }
    }

    onShow(){
        SceneManager.lastEntranceOpenModuleName = 'ClubModule'
        // let codeList = [1, 2, 3, 4, 5, 6, 27, 8, 9, 10, 11, 12, 13, 14]
        // this.myhandPokers.showPokers(true, codeList, { hidePoker: true })
        // this.myhandPokers.playFaPaiAnim(function (index, totalCount) {
        //     if (index < totalCount) {
        //         PdkSound.playFaPaiSound()
        //     }

        // }, function () {

        // })

        // let viewid = 0;
        // this.showHandCards(viewid,true,[ 27, 8, 9])
        // // //this.showHandCards(0,true,[ 27, 8, 9])
        // // this.showHandCards(2,true,[ 27, 8, 9])
        // this.userEnter(viewid,{
        //     id:1,
        //     name:'saf',
        //     gender:1,
        //     avatarUrl:'http://sincebest-bull-test.oss-cn-hangzhou.aliyuncs.com/20180723_e852c0d11edf47529de8e7c69c848ca8.jpg',
        //     coins:3,
        //     gems:3,
        //     chairID:3,
        //     dissolveState:3,
        //     readyState:1,
        // })

        // this.showSeatClock(viewid,true,4);
        // this.showSingle(viewid,true);
    }

    initAllSeatView(){
        let self = this
        let seatPosNodeList = self.nodeSeatParentList
        for (let index = 0; index < seatPosNodeList.length; index++) {
            const element = seatPosNodeList[index];
            let nodeSeat = GameUtil.AddChild(this.prefabSeatView.node, element)
            let seatView = nodeSeat.getComponent<PokerTableSeatBaseView>(PokerTableSeatBaseView)
            seatView.resetSeatStyle(index)
            seatView.node.active = false;
            self.m_allSeatViewList.push(seatView)
            this.mPosExtendSeatMap.push(this.seatExtendViewList[index]);
        }
    }

    resetSeatViewPos(){
        for (let seatview of this.m_allSeatViewList) {
            seatview.reset();
        }
    }

    //隐藏某一类型的标记 0-all
    hidePlayerTag(tag:number){
        for (let seat of this.m_allSeatViewList) {
            this.operatorPlayerTag(seat,tag);
        }
    }

    playerTagVisible(viewid:number,tag:number){
        let seat = this.m_allSeatViewList[viewid];
        if (!seat) {
            return;
        }
        this.operatorPlayerTag(seat,tag);
    }

    private operatorPlayerTag(seat:PokerTableSeatBaseView,tag:number){
        if (!seat) {
            return;
        }
        if (tag == 1 || tag == 0) {
            seat.showReadyTag(false);
        }
        if (tag == 2 || tag == 0) {
            seat.showSelect(false);
        }
        if (tag == 3 || tag == 0) {
            seat.showSingle(false);
        }
        if (tag == 4 || tag == 0) {
            seat.showClock(false);
        }
    }

    //用户进入，刷新信息
    userEnter(viewid:number,data:PdkData.IUserInfo,roomInfo:PdkData.RoomInfoData){
        let seatView = this.m_allSeatViewList[viewid];
        if (seatView) {
            seatView.node.active = true;
            TextureManager.instance.getSprite(data.avatarUrl,(frame:cc.SpriteFrame)=>{
                if (seatView && seatView.isValid) {
                    seatView.showAvatar(true,frame);
                }
            })
            seatView.showName(true,data.name);
            seatView.showCoin(true,data.coin);
            seatView.showReadyTag(data.readyState == PConst.ReadyState.Ready_Yes && roomInfo.gameState != PConst.GameStatus.START);
            if (viewid == 0) {//我方
                if (roomInfo.gameState != PConst.GameStatus.START) {//未开始游戏
                    if (data.readyState == PConst.ReadyState.Ready_Yes) {
                        this.btnSwitchui.SwitchState('none');
                    }else{
                        this.btnSwitchui.SwitchState('ready');
                    } 
                }
            }
        }
    }

    //用户离开
    userLeave(viewid:number){
        let seatView = this.m_allSeatViewList[viewid];
        if (seatView) {
            seatView.node.active = false;
            this.playerTagVisible(viewid,0);
        }
    }

    changeBtnState(state:string){
        this.btnSwitchui.SwitchState(state);
    }

    isForbidOutBtn(isgray:boolean){
        Utility.instance.setBtnGray(this.OutButton.getComponent(cc.Button),isgray);
    }

    dealCards(card:number[]){
        this.myhandPokers.showPokers(true, card, { hidePoker: true })
        this.myhandPokers.playFaPaiAnim(function (index, totalCount) {
            if (index < totalCount) {
                PdkSound.playFaPaiSound()
            }
        }, function () {

        })
    }

    showReadyTag(viewid:number,show:boolean){
        let seatView = this.m_allSeatViewList[viewid];
        seatView.showReadyTag(show);
    }

    showSelect(viewid:number,show:boolean){
        let seatView = this.m_allSeatViewList[viewid];
        seatView.showSelect(show);
    }

    showSeatClock(viewid: number, show: boolean, secs?: number, needShake?: boolean, onFinish?: Function) {
        let seatView = this.m_allSeatViewList[viewid]
        if (viewid == 0) {
            this.nodeClock.active = show;
            if (show) {
                this.refreshClockLable(secs,needShake,onFinish);
            }
            return;
        }
        seatView.showClock(show, secs, needShake, onFinish)
    }

    showSingle(viewid:number,show:boolean){
        let seatView = this.m_allSeatViewList[viewid];
        seatView.showSingle(show);
    }

    protected refreshClockLable(secs: number, needShake?: boolean, onFinish?: Function){
        let self = this
        this.labelClock.string = secs.toString()
        if(secs <= 0){
            if(needShake){
                AudioManager.instance.vibrate()
            }
            if(onFinish){
                onFinish()
            }
            return
        }
        this.m_clockScheduleId = this.doScheduleOnce(function(){
            self.m_clockScheduleId = 0
            secs--
            self.refreshClockLable(secs, needShake, onFinish)
        }, 1)
    }


    showHandCards(viewid: number, show: boolean, codeList?: number[], withAnim?: boolean, onFinish?: Function){
        //let index = this.convertLocalPosToIndex(localPos)
        let seatPokers = this.seatHandPokersList[viewid]
        seatPokers.showPokers(show, codeList, {withAnim:true, onFinish:onFinish})
    }

    //刷新我方手牌
    updateMyHandCards(codeList: number[],){
        this.myhandPokers.showPokers(true, codeList, { hidePoker: false })
    }

    
    playFeiJiEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        this.playCommonEffect(extendSeat.nodeFeiJiAnim, centerWorldPos, onFinish, 2.33, false, this.effect_offset_pos)
    }

    playShunZiEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        this.playCommonEffect(extendSeat.nodeShunZiAnim, centerWorldPos, onFinish, 1.5, false, this.effect_offset_pos)
    }

    playLianDuiEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        this.playCommonEffect(extendSeat.nodeLianDuiAnim, centerWorldPos, onFinish, 1.5, false, this.effect_offset_pos)
    }

    playZhaDanEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        this.playCommonEffect(extendSeat.nodeZhaDanAim, centerWorldPos, onFinish, 2.1, false, this.effect_offset_pos)
    }

    playWangZhaEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish1?: Function, onFinish2?: Function){
        let self = this
        let offsetPos = self.effect_offset_pos
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        let anim = extendSeat.nodeWangZhaAim.getComponent(cc.Animation)
        let anim1 = extendSeat.nodeWangZhaAim1.getComponent(cc.Animation)
        anim.node.active = false
        anim1.node.active = false
        if(centerWorldPos){
            let pos = null;
            anim.node.parent.convertToNodeSpaceAR(centerWorldPos,pos)
            if(offsetPos){
                pos.x += offsetPos.x
                pos.y += offsetPos.y
            }
            anim.node.position = pos            
        }
        anim.on('finished', function(){
            anim.targetOff(self)
            anim.node.active = false
            anim1.node.active = true
            if(onFinish1){
                onFinish1()
            }
        }, self)
        anim1.on('finished', function(){
            anim1.targetOff(self)
            anim1.node.active = false
            if(onFinish2){
                onFinish2()
            }
        }, self)
        
        anim.node.active = true
    }

    playChunTianEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        this.playCommonEffect(this.nodechunTianAnim, centerWorldPos, onFinish, 3)
    }

    
    playSanDaiYiEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        this.playCommonEffect(extendSeat.nodeSanDaiYiAnim, centerWorldPos, onFinish, 1.67, false, this.effect_offset_pos)
    }

    playSanDaiErEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        this.playCommonEffect(extendSeat.nodeSanDaiErAnim, centerWorldPos, onFinish, 1.67, false, this.effect_offset_pos)
    }

    playSiDaiErEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        this.playCommonEffect(extendSeat.nodeSiDaiErAnim, centerWorldPos, onFinish, 1.67, false, this.effect_offset_pos)
    }

    playSiDaiLiangDuiEffect(localPos: number, centerWorldPos: cc.Vec2, onFinish?: Function){
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        this.playCommonEffect(extendSeat.nodeSiDaiLiangDuiAnim, centerWorldPos, onFinish, 1.67, false, this.effect_offset_pos)
    }

    showDispatchCards(localPos: number, show: boolean, codeList?: number[], withAnim?: boolean, onFinish?: Function){
        let seatPokers = this.seatPokersList[localPos]
        let centerWorldPos = seatPokers.showPokers(true,codeList);
        return centerWorldPos
    }


    showSeatPassAnim(localPos:number, show: boolean, withAnim?: boolean, onFinish?: Function){
        let self = this
        let extendSeat = this.getExtendSeatViewByPos(localPos)
        let node = extendSeat.nodePass
        node.active = show              
        if(show){
            self.playCommonEffect(node, null, onFinish, 0.8, false);
        }else{
            if(onFinish){
                onFinish()
            }
        }
    }

    getExtendSeatViewByPos(localPos:number): SeatExtendHolder{
        return this.mPosExtendSeatMap[localPos]
    }

    protected playCommonEffect(node: cc.Node, centerWorldPos: cc.Vec2, onFinish?: Function, duration?: number, notHide?: boolean, offsetPos?: cc.Vec2){
        let originalPos = null
        if(centerWorldPos){
            originalPos = node.position
            let pos;
            node.parent.convertToNodeSpaceAR(centerWorldPos,pos)
            if(offsetPos){
                pos.x += offsetPos.x
                pos.y += offsetPos.y
            }
            node.position = pos            
        }
        
        node.active = true
        if(duration == null){
            duration = 1.7
        }                
        this.scheduleOnce(function(){   
            if(originalPos){
                node.position = originalPos
            }  
            if(notHide){

            }else{
                node.active = false 
            }              
            if(onFinish){
                onFinish()
            }
        }, duration)
    }

    
    protected getPokerSpriteKeyByCode(code: number){
        let card = Card.makeCardByCode(code)
        if(card.index == CardNameIndex.card_big_king){
            return 'Public_大王'
        }
        if(card.index == CardNameIndex.card_small_king){
            return 'Public_小王'
        }
        let key = 'Public_'        
        switch(card.color){
            case CardColor.color_black_heart:
                key += 'heitao_'
                break
            case CardColor.color_red_heart:
                key += 'hongtao_'
                break
            case CardColor.color_plum:
                key += 'meihua_'
                break
            case CardColor.color_square:
                key += 'fangkuai_'
                break
        }
        return key + card.index
    }
}
