import PokerTableMySeatPokers from "../../../public/poker_table/poker_myseat_pokers";
import CustomUIStateSwitcher from "../../../public/ui/custom_ui_state_switcher";

const {ccclass, property} = cc._decorator;

@ccclass
export class PdkMySeatPokers extends PokerTableMySeatPokers {

    @property(cc.Integer)
    rightStyleMinCardsCount: number = 20
    
    @property(CustomUIStateSwitcher)
    uiSwitcher: CustomUIStateSwitcher = null    

    playFaPaiAnim(onFinishSingle: Function, onFinish: Function){
        let self = this
        let pokers = this.pokers
        let duration = 0.1      
        let totalCount = pokers.length  
        let doNextPokerMove  
        self.isPlayingSelectPokerAnim = true
        doNextPokerMove = function(index: number, onFinish: Function, startPos?: cc.Vec2){
            if(onFinishSingle){
                onFinishSingle(index, totalCount)
            }
            if(index == totalCount){
                self.isPlayingSelectPokerAnim = false
                if(onFinish){
                    onFinish()
                    return
                }
            }
            let poker = pokers[index]
            let targetPos = poker.node.position
            poker.show()
            if(startPos){
                poker.node.position = startPos
            }            
            let callFun = cc.callFunc(function(){
                doNextPokerMove(++index, onFinish, targetPos)
            }.bind(self))
            var move = cc.moveTo(duration, targetPos)
            var seq = cc.sequence([move, callFun]);
            poker.node.runAction(seq);
        }        

        doNextPokerMove(0, onFinish)
    }

    showPokers(show: boolean, codeList?: number[], params?: any){
        let centerLocalPos = super.showPokers(show, codeList, params)
        this.resetHandCardsRootPos()
        return centerLocalPos
    }

    showRecommends(selectCards:number[],isHaveMask:boolean = true){
        this.resetHandCardsRootPos()
        let selectIndex = 0;
        let codes = selectCards.concat([])
        for (let idx = 0; idx < this.pokers.length; idx++)
         {
            let poker = this.pokers[idx];
            poker.marked = isHaveMask;
            for (let i = selectIndex; i < codes.length; idx++) {
                let target = selectCards[idx];
                if (poker.code == target) {
                    codes.splice(i,1);
                    poker.selected = true;
                    poker.marked = false;
                    break;
                }
            }
        }
        this.refreshPokersMarkState();
        this.refreshPokerSelectState(false);
    }

    resetHandCardsRootPos(){
        let len = this.pokers.length
        if(len < this.rightStyleMinCardsCount){
            this.uiSwitcher.SwitchState('Center')
        }else{
            this.uiSwitcher.SwitchState('None')
        }
    }
}
