
let polyInst = null;
if (!window.i18n) {
    window.i18n = {
        languages: {},
        curLang: ''
    };
}

if (CC_EDITOR) {


    Editor.Profile.load('profile://project/i18n.json', (err, profile) => {
        if (err)
            cc.log("报错err:" + err);
        window.i18n.curLang = profile.data['default_language'];
        if (polyInst) {
            let data = loadLanguageData(window.i18n.curLang) || {};
            initPolyglot(data);
        }
    });
}

function loadLanguageData(language) {
    if (!language) {
        if (window.i18n.curLang)
            return LoadCurrentLanguageData(window.i18n.curLang)
        else return;
    }
    if (!window.i18n.languages[language]) {
        cc.log("！！！！！！！！！！！！！！！window.i18n.languages[language] 为空");
        window.i18n.languages[language] = LoadCurrentLanguageData(language);
    }
    return window.i18n.languages[language];
}
//新增读取
function LoadCurrentLanguageData(language) {
    const Fs = require('fs');
    const Path = require("path");
    
    let absolutePath = Path.join(Editor.Project.path, './packages/i18n/config/i18n/' + language + '.json');
    //cc.log("absolutePath:" + absolutePath);
    // var data = {
    //     "hello": "Hello!",
    //     "bye": "Bye"
    // }//
    var data = Fs.readFileSync(absolutePath);
    // if (data[0] === 0xEF && data[1] === 0xBB && data[2] === 0xBF) {
    //     data = data.slice(3);
    // }
    // data = data.toString('utf-8');
    // cc.log(data);
    // var result = JSON.parse(data);
    // //result = JSON.stringify(data);
    // cc.log("新增读取");
    // cc.log(result);
    return data;
}
function initPolyglot(data) {
    if (data) {
        //cc.log("data:" + data);
        if (polyInst) {
            polyInst = data;
        } else {
            //cc.log("polyInst 对象的初始话："+polyInst);
            polyInst = "";
            polyInst = data;
            //cc.log("初始化之后的polyInst："+polyInst);
        }
    }
}

module.exports = {
    /**
     * This method allow you to switch language during runtime, language argument should be the same as your data file name
     * such as when language is 'zh', it will load your 'zh.js' data source.
     * @method init
     * @param language - the language specific data file name, such as 'zh' to load 'zh.js'
     */
    init(language) {
        //cc.log("初始化....init(language):" + language);
        if (!language) {
            cc.log("language 未定义");
            cc.log("window.i18n.curLang:" + window.i18n.curLang);
            // if (window.i18n.curLang)
            //     language = window.i18n.curLang;
            // else
            //     return;
        }
        if (language === window.i18n.curLang) {
            return;
        }
        let data = loadLanguageData(language) || {};
        //data = JSON.parse(data);
        cc.log(data);
        //cc.log("打印出转换之后的json字串");
        cc.log(data);
        window.i18n.curLang = language;
        initPolyglot(data);
        this.inst = polyInst;
    },
    parse(key) {
        //cc.log("调用 t 方法key:" + key );
        // cc.log("polyInst:"+polyInst);
        if (polyInst[0] === 0xEF && polyInst[1] === 0xBB && polyInst[2] === 0xBF) {
            polyInst =polyInst.slice(3);
        }
        polyInst = polyInst.toString('utf-8');
        // cc.log("polyInst文字化:"+polyInst);
        var json = JSON.parse(polyInst);
        if (json.hasOwnProperty(key))
        {
            cc.log(json[key]);
            return json[key].toString();
        }
        else return "未定义字段";
    },

    inst: polyInst,
    //更新检测 检测场景中所有的ULabel
    updateSceneRenderers() { // very costly iterations
        //cc.log("ConfigLanguageData....updateSceneRenderers");
        let rootNodes = cc.director.getScene().children;
        // walk all nodes with localize label and update
        let allLocalizedLabels = [];
        for (let i = 0; i < rootNodes.length; ++i) {
            let labels = rootNodes[i].getComponentsInChildren('ULabel');
            Array.prototype.push.apply(allLocalizedLabels, labels);
        }
        // cc.log("更新000000000000000");
        // cc.log("更新检测 检测场景中所有的ULabel:"+allLocalizedLabels.length);
        for (let i = 0; i < allLocalizedLabels.length; ++i) {
            let label = allLocalizedLabels[i];
            // cc.log("!label.node.active:"+!label.node.active)
            // if (!label.node.active) continue;
            label.updateLabel();
        } 
    }
};
