import ViewBase from "../../../../framework/core/mvvm/view_base";
import { GameManager } from '../../../../manager/game_manager';

const { ccclass, property, menu} = cc._decorator;

@ccclass
export class ResultView extends ViewBase {
    
    @property(cc.Node)
    winner:cc.Node = null;

    @property(cc.Node)
    loser:cc.Node = null;

    updateUI(winner:number,mChairid:number){
        this.winner.active = winner == mChairid;
        this.loser.active = winner != mChairid;
    }
}
