import Module from "./module_base";
import GameUtil from "../../../utility/game_util";
import { GameManager } from "../../../manager/game_manager";
import LogReportManager from '../../../manager/log_report_manager';
import Debug from "../../../manager/debug";
import ModuleManager from "../module_manager";
import NetClient from "../../net/net_client";

//处理网络数据
export default class ModelBase {

    module: Module<any, any>
    netClient: NetClient;
    private _msgEventMap = {}
    public isDestroyed = false
    send(route:string, message?:any) {
        
        let time = new Date();
        cc.log(`${time.getHours()}:${time.getMinutes()}:${time.getSeconds()} [pomelo]:发送消息:`,route,message);

        this.netClient.send(route,message || {});
    }

    registerNetMsg(route:string,callback: Function,ignoreModuleActive: boolean = true){
        let self = this;
        let eventDataList = this._msgEventMap[route];
        if (!eventDataList) {
            eventDataList= []
            this._msgEventMap[route] = eventDataList
        }

        let cb = function(receiveMessageData){
            if (callback) {
                if (ignoreModuleActive) {
                    callback(receiveMessageData)
                }else{
                    if (!self.isDestroyed) {
                        callback(receiveMessageData)
                    }
                }
            }
        }

        let eventData = {
            msgName: route,
            callback: cb,
        }
        eventDataList.push(eventData)
        this.netClient.on(route,cb)
    }

    clearMsgEvent() {
        for (const key in this._msgEventMap) {
            if (this._msgEventMap.hasOwnProperty(key)) {
                const element = this._msgEventMap[key];
                for (let index = 0; index < element.length; index++) {
                    const e = element[index];
                    this.netClient.off(e.msgName, e.callback);
                }
            }
        }
        this._msgEventMap = {}
    }

    destroyModel() {
        this.clearMsgEvent()
    }

    showNetPrompt(show: boolean) {
        if (show) {
            ModuleManager.instance.showModuleName("NetPrompt")
        } else {
            ModuleManager.instance.hideModule("NetPrompt")
        }
    }

}
