import TextureManager from "../../manager/texture_manager";
import CustomUIStateSwitcher from "../public/ui/custom_ui_state_switcher";
import { GameCfg } from "../public/const/game_cfg";
import { Club } from "./club_data";

const {ccclass, property} = cc._decorator;

//牌桌信息
@ccclass
export class TableInfo extends cc.Component {
   
    @property(CustomUIStateSwitcher)
    seatStyle:CustomUIStateSwitcher = null;

    @property([cc.Node])
    seatNode:cc.Node[] = [];

    @property([cc.Node])
    avatar:cc.Node[] = [];

    @property(cc.Label)
    playWayName:cc.Label = null;

    @property(cc.Node)
    spriteJuShu:cc.Node = null;

    @property(cc.Label)
    lab_JuShu:cc.Label = null;

    tableData:Club.ITableInfo = null;

    @property(cc.Node)
    clickBtn:cc.Node = null;

    clickFunc:Function = null;

    onLoad(){
        this.clickBtn.on('click',this.clickItem,this)
    }

    clickItem(){    
        cc.log('----------->>>>item',this.node.name)
        if (this.tableData) {
            cc.log('----------->>>>进入游戏了')
            if (this.clickFunc) {
                this.clickFunc(this.tableData);
            }
        }
    }

    setData(data:Club.ITableInfo,idx:number){
        this.tableData = data;
        if (data.tbType == Club.TableType.GameTable) {//游戏牌桌
            this.setPlayWayName(data.gameInfo.playwayName)
            this.setStyle(data.gameInfo.players.length);
            this.updatePlayerAvatar(data);
            this.setPlayCount(data.gameInfo.gameCount,data.gameInfo.currentCount);
            //更新头像
        }else if (data.tbType == Club.TableType.PWTable) {
            this.setStyle(5);
            this.setPlayWayName(data.playWay.playwayName);
            this.setPlayCount(0,0);
        }
        // this.setPlayerIndex(idx+1);
    }

    setPlayCount(gameCount:number,countCount:number){
        this.spriteJuShu.active = countCount > 0;
        if (countCount > 0) {
            this.lab_JuShu.string = '{0} {1}/{2}'.format('已开局',countCount,gameCount);
        }
    }

    setPlayWayName(name:string){
        this.playWayName.string = name;
    }

    updatePlayerAvatar(data:Club.ITableInfo){
        let activeAvatar = [];
        for (let i = 0; i < this.avatar.length; i++) {
            let node = this.avatar[i];
            if (node.active) {
                let sp = node.getChildByName('SpriteAvatar');
                activeAvatar.push(sp.getComponent(cc.Sprite));
            }
        }

        for (let idx = 0; idx < data.gameInfo.players.length; idx++) {
            let player = data.gameInfo.players[idx];  
            let avatar = activeAvatar[idx];
            if (avatar) {
                this.setAvatar(player.avatarUrl,avatar);
            }
        }
    }

    setAvatar(url:string,avatar:cc.Sprite){
        TextureManager.instance.getSprite(url,(spriteFrame:cc.SpriteFrame)=>{
            if (this.node && this.node.isValid) {
                avatar.spriteFrame = spriteFrame;
            }
        });
    }


    setStyle(style:number){
        let st = 'two';
        switch (style) {
            case 1:
                st = 'one';
                break;
            case 2:
                st = 'two';
                break;
            case 3:
                st = 'three';
                break;
            case 4:
                st = 'four';
                break;
            case 5:
                st = 'none';
            default:
                break;
        }
        this.seatStyle.SwitchState(st);
    }
}
