import AppData from '../pacakge/public/const/app_data';
import GameServer from '../pacakge/server/gameserver/game_server';

export class GameManager {
    static loginServer: string
    static gameServer: string
    static hallServer:string

    static setGameServer(ip:string,port:number){
        GameManager.gameServer = "ws://{0}:{1}".format(ip,port);
    }

    static setHallServer(ip:string,port:string){
        GameManager.hallServer = "ws://{0}:{1}".format(ip,port);
    }



}