import ModelBase from "../../../framework/core/mvvm/model_base";
import { ClubRoute } from "../net_clubRoute";

const {ccclass, property} = cc._decorator;

@ccclass
export class ClubMemListModel extends ModelBase {
    curPageNum:number = 0;
    totalPageNum:number = 0;
    pageCeilNum:number = 25;
    clubId:number = 0;

    send_getClubMember(clubId:number, pageIdx:number, pageNum:number, isOutPartner:boolean){
        let data = {
            clubId:clubId,
            pageIdx:pageIdx,
            pageNum: pageNum,
            isOutPartner: isOutPartner,
        }
        this.send(ClubRoute.getClubMember,data);
    }
}
