
import TextureManager from "../../manager/texture_manager";
import { Club } from "./club_data";

const {ccclass, property} = cc._decorator;

@ccclass
export class ClubInfo extends cc.Component {
    @property(cc.Sprite)
    avatar:cc.Sprite = null;

    @property(cc.Label)
    clubName:cc.Label = null;

    @property(cc.Label)
    member:cc.Label = null;

    @property(cc.Node)
    clickBtn:cc.Node = null;

    clubData:Club.IClubData = null;

    clubInfCall:Function = null;

    onLoad(){
        this.clickBtn.on('click',this.clickItem,this)
    }

    clickItem(){    
        if (this.clubData) {
            cc.log('----------->>>>选择俱乐部')
            if (this.clubInfCall) {
                this.clubInfCall(this.clubData,this);
            }
        }
    }

    setClubName(name:string){
        this.clubName.string = name;
    }

    setBgOpacity(p:number){
        this.clickBtn.opacity = p
    }

    setAvatar(url:string){
        TextureManager.instance.getSprite(url,(spriteFrame:cc.SpriteFrame)=>{
            if (this.node && !this.node.isValid) {
                this.avatar.spriteFrame = spriteFrame;
            }
        });
    }
}
