import LoaclStorage from "../manager/local_storage";


export default class SdkStorage{


    public static loginType;

    static setLoginToken(value : any){
        
        LoaclStorage.setItem(this.loginType,value);
    }

    static getLoginToken(key : string){

        return LoaclStorage.getItem(key);
    }

    static clearLoginToken(){

        LoaclStorage.delete(this.loginType);
    }
}
