import ModuleManager from "../module_manager";
import GameUtil from "../../../utility/game_util";
import EventManager from "../../../manager/event_manager";
import ModelData from "../../../pacakge/public/model_data";
import AudioManager from "../../../manager/audio_manager";
import NetPrompt from "../../../pacakge/public/module/netprompt/netprompt";
import PromiseUtility from "../../../utility/promiseUtility";

const { ccclass, property } = cc._decorator;

@ccclass
export default class MVVMBase extends cc.Component {

    protected isDestroyed = false

    //隐藏模块，会同样停止http的返回事件
    protected isHide: boolean

    protected isInited: boolean

    private m_maxId = 0

    private m_callbackMap 
    private mEventCallback : object

    get nodeIsActive(): boolean {
        if (this.isInited && this.node != null && cc.isValid(this.node, true)) {
            return this.node.active;
        }
        return false;
    }

    get nodeIsValid():boolean{
        if (this.node != null && cc.isValid(this.node, true)) {
            return true;
        }
        return false;
    }

    setActive() {
        if (this.node != null && cc.isValid(this.node, true)) {
            this.node.active = true
        }
    }

    //是否正常显示
    get normalShow():boolean{
        return !this.isDestroyed && !this.isHide
    }
 
    /**
     * 加载动态资源
     * @param url 
     * @param completeCallback 
     * @param type 
     * @param connect 
     */
    loadRes(url: string, completeCallback: (error: Error, resource: any) => void, type?: typeof cc.Asset, connect?: number){
        let self = this;                
        GameUtil.loadRes(url,(error,resource)=>{
            if (!self.isDestroyed && !error && completeCallback) {                
                completeCallback(error,resource);
            }

        },type,connect);
    };
 
    /**
     * 播放音效
     * @param url 
     * @param loop 
     * @param onFinish 
     */
    playSound(url: string, loop: boolean = false, onFinish?: Function){

        let self = this;
        AudioManager.instance.playSound(url,loop,()=>{ 
            if (self.normalShow) {
                if (onFinish) {
                    onFinish();    
                }
            }
        });        
    }
    

    doSchedule(callback: Function, interval?: number, repeat?: number, delay?: number) {
        this.m_callbackMap = this.m_callbackMap ? this.m_callbackMap : {}
        let self = this
        let id = ++this.m_maxId
        let tmpFun = function () {
            if (!repeat) {
                self.cancelSchedule(id)
            }
            callback()
        }
        this.m_callbackMap[Number(id)] = tmpFun
        this.schedule(tmpFun, interval, repeat, delay)
        return id
    }

    doScheduleOnce(callback: Function, delay: number) {
        this.m_callbackMap = this.m_callbackMap ? this.m_callbackMap : {}
        let id = ++this.m_maxId
        let tmpFun = function () {
            this.m_callbackMap[id.toString()] = null
            callback()
        }
        this.m_callbackMap[Number(id)] = tmpFun
        this.scheduleOnce(tmpFun, delay)
        return id
    }

    cancelSchedule(id: number) {
        if (this.m_callbackMap) {
            let key = id.toString()
            let callback = this.m_callbackMap[key]
            if (callback) {
                this.unschedule(callback)
                this.m_callbackMap[key] = null
            }
        }

    }

    eventDispatch(eventName: string, ...data) {
        return EventManager.instance.dispatch(eventName, ...data)
    }

    eventBind(eventName : string, callback: Function) {
        this.mEventCallback = this.mEventCallback ? this.mEventCallback: {}
        this.mEventCallback[eventName] = callback
        EventManager.instance.on(eventName, callback, this, true)
    }

    //新增了绑定事件，只会触发调用一次就会自动回收
    eventBindOnce(eventName : string, callback: Function) {
        this.mEventCallback = this.mEventCallback ? this.mEventCallback: {}
        this.mEventCallback[eventName] = callback
        EventManager.instance.on(eventName, callback, this, true, true)
    }

    eventUnBind(eventName : string, callback: Function) {
        this.mEventCallback = this.mEventCallback ? this.mEventCallback: {}
        this.mEventCallback[eventName] = null
        EventManager.instance.off(eventName, callback, this)
    }

    protected _baseDestroy() {
        for (const key in this.mEventCallback) {
            if (this.mEventCallback.hasOwnProperty(key)) {
                const callback = this.mEventCallback[key];
                EventManager.instance.off(key, callback, this)
                
            }
        }
    }

}