import HallView from "./hall_view";
import Module, { moduleInit } from "../../framework/core/mvvm/module_base";
import ModuleManager from "../../framework/core/module_manager";
import GameServer from "../server/gameserver/game_server";
import HallServer from "../server/hallserver/hall_server";
import AppData from "../public/const/app_data";
import LoaclStorage from "../../manager/local_storage";
import ModelData from "../public/model_data";
import { HllModel } from "./hall_model";
import { HallServerRoute } from "../server/hallserver/hall_net_route";
import { HallNetRoute } from "./hall_route";
import { GameManager } from "../../manager/game_manager";
import { ClubModule } from "../club/club_module";
import { ServerCode } from "../public/const/const_code";
import GameUtil from "../../utility/game_util";
import Toast from "../public/module/toast/toast";
import JoinClubModule from "../joinClub/joinClub_module";

const {ccclass, property} = cc._decorator;

@ccclass
@moduleInit("HallModule")
export default class HallModule extends Module<HallView,HllModel> {
    constructor(){
        super()
        this.windowPrefabResPath = 'package/hall/hall';
        this.viewType = HallView;
        this.modelType = HllModel;
    }

    onLoad(){
    }

    onShow(){
        this.model.netClient = HallServer.instance.netClient;
        this.model.clearMsgEvent();
        this.registerMsg();
        this.view.updateUserInfo();
        this.model.send_getclubList();
    }

    registerMsg(){
        this.model.registerNetMsg(HallNetRoute.getClubList, this.retGetClubList.bind(this));
    }

    protected onButtonClick(node: cc.Node, customEventData){
        let pName = node.name;
        let clubId = LoaclStorage.getNumber('_select_club_id')
        switch (pName) {
            case 'Button_createClub':
                ModuleManager.instance.showModule(Toast,'您未获得创建联盟权限!');
                break;
            case 'Button_joinClub':
                ModuleManager.instance.showModule(JoinClubModule, null);
                break;
            case 'game_15':
                LoaclStorage.setNumber('_club_gameId', 15);
                ModuleManager.instance.showModule(ClubModule, clubId);
                break;
            case 'game_16':
            case 'game_20':
            case 'game_21':
            case 'game_22':
            case 'game_30':
            case 'game_31':
                LoaclStorage.setNumber('_club_gameId', 0);
                ModuleManager.instance.showModule(ClubModule, clubId);
                break;
            case 'game_23':
                ModuleManager.instance.showModule(Toast,'敬请期待!');
                break;

            default:
                break;
        }
    }

    //暂定为跑得快
    private gotoPdk(){
        HallServer.instance.close();
        GameServer.instance.connectServer(GameManager.hallServer)
    }

    retGetClubList(data:any){
        if (data.code != 0) {
            this.view.showClubList(null);
        } else{
            if (data.clubList.length <= 0) {
                this.view.showClubList(null);
            } else {
                this.view.showClubList(data);
            }
        }
    }
}
