import { IChildItem, IChumpItem } from "./rule_data";

export class RuleFactory{
    public static readonly instance: RuleFactory = new RuleFactory();

    getConfig(ruleCfg:any):IChumpItem[]{
        if (!ruleCfg) {
            cc.error('不存在规则文件');
        }
        let chumps:IChumpItem[] = [];

        ruleCfg.forEach(rule => {
            let chump:IChumpItem = {
                title:rule.tag.title,
                rowCount:rule.tag.rowCount,
                childArray:this.praseChildItem(rule.childList)
            }
            chumps.push(chump);
        });
        return chumps;
    }

    private praseChildItem(childList:any):IChildItem[]{
        let childs:IChildItem[] = [];
        for (let child of childList) {
            childs.push(child);
        }
        return childs;
    }


}