import { PatternType, CardPattern } from "./poker_pattern";
import { PokerRecommend } from "./poker_recommend";
import { DouDiZhuCardCommon, Card, CardNameIndex } from "./poker_data";

const {ccclass, property} = cc._decorator;

@ccclass
export class PokerSet {
    cards: number[] = []
    card_cnt: number = 0
    setCodeList(codeList: number[], len?: number){
        this.cards = []
        if(codeList){
            for (let i = 0; i < codeList.length; i++) {
                const element = codeList[i];
                this.cards.push(element)
            }
        }
        if(len != null){
            this.card_cnt = len
        }else{
            this.card_cnt = this.cards.length
        }        
    }

    hintIterator(pattern: CardPattern, cards?: number[]):{tishiFun:Function, count:number, auto:boolean, pattern_set: CardPattern[]}{
        let firstout = true
        let outcard = []
        let handcard = cards? cards: this.cards
        if(pattern != null){
            firstout = false
            outcard = pattern.cards
        }
        let tishiData = PokerRecommend.hintIterator(handcard, outcard, firstout)
        let pattern_set = []
        let has_sidaier_map = []
        for (let i = 0; i < tishiData.tishi.length; i++) {
            const element = tishiData.tishi[i];
            let pattern_tmp = CardPattern.newPattern(element)
            if (pattern_tmp == null) {
                return null;
            }
            if(pattern_tmp.type == PatternType.type_sandaiyi || pattern_tmp.type == PatternType.type_feiji){
                this.proc_sanzhang_tail_cards(pattern_tmp, pattern, handcard)
            }
            pattern_set.push(pattern_tmp) 
        }

        if(!pattern || pattern.type == PatternType.type_sidaier){
            let outData = PokerRecommend.get_cards_type(outcard)
            let cardsList = PokerRecommend.find_all_biger_sizhang(handcard, outData.card_value)
            for (let i = 0; i < cardsList.length; i++) {
                const element = cardsList[i];
                let pattern_tmp = CardPattern.newPattern(element)
                this.proc_sizhang_tail_cards(pattern_tmp, pattern, handcard)
                pattern_set.push(pattern_tmp) 
            }            
        }

        let index = 1
        if(pattern_set.length == 0){
            return {tishiFun: null, count: 0, auto: tishiData.auto, pattern_set: pattern_set}
        }else{
            return {
                tishiFun:function(){
                    let oldIndex = index
                    index = index % (pattern_set.length) + 1                    
                    return pattern_set[oldIndex - 1]
                },
                count: pattern_set.length,
                auto: tishiData.auto,     
                pattern_set: pattern_set,           
            }
        }
    }

    choose_hintIterator(codeList: number[], pattern: CardPattern){        
        let tishiData = this.hintIterator(pattern, codeList)
        if (tishiData == null) {
            return
        }
        if(tishiData.pattern_set.length > 0){
            tishiData.pattern_set.sort(function(a, b){  //按张数从多到少排序
                return b.card_cnt - a.card_cnt
            })
            let max_cnt = tishiData.pattern_set[0].card_cnt
            let set = []
            for (let i = 0; i < tishiData.pattern_set.length; i++) {
                const pattern = tishiData.pattern_set[i];
                if(pattern.card_cnt == max_cnt){
                    set.push(pattern)
                }
            }

            tishiData.pattern_set = set
            tishiData.pattern_set.sort(function(a, b){
                return DouDiZhuCardCommon.tishi[b.type] - DouDiZhuCardCommon.tishi[a.type]
            })

            return tishiData.pattern_set[0]
        }else{
            return {
                cards: codeList,
                card_cnt: codeList.length,
                type: PatternType.type_unknow,
            }
        }
    }

    //处理3张或飞机带牌 3个带两
    proc_sanzhang_tail_cards(hint_pattern: CardPattern, comp_pattern: CardPattern, cards: number[]){
        let with_cnt = hint_pattern.card_cnt / 3 * 2 //3带2
        let with_type

        if(comp_pattern){
            if(comp_pattern.type == PatternType.type_sandaiyi && comp_pattern.card_cnt == 3){
                return
            }

            if(comp_pattern.type == PatternType.type_feiji){
                if(with_cnt * 3 == comp_pattern.card_cnt){
                    return
                }
            }
        }

        if(hint_pattern.type != PatternType.type_sandaiyi && hint_pattern.type != PatternType.type_feiji){
            console.error(new Error('只处理3张或飞机的带牌').stack)
        }
        if(comp_pattern){
            if(comp_pattern.type == PatternType.type_sandaiyi){
                if(comp_pattern.card_cnt != 5 && comp_pattern.card_cnt != 4){
                    console.error(new Error('只处理3带1对或三带单张的带牌').stack)
                }
                with_type = comp_pattern.card_cnt == 5 ? PatternType.type_duizi: PatternType.type_danpai
            }else{
                if(comp_pattern.card_cnt <= with_cnt*3){
                    console.error(new Error('没有牌给飞机当翅膀了').stack)                    
                }
                let tail_cards_cnt = comp_pattern.card_cnt - with_cnt*3
                with_type = tail_cards_cnt / with_cnt
                if(tail_cards_cnt%with_cnt == 0 && (with_type == PatternType.type_danpai || with_type == PatternType.type_duizi)){
                    console.error(new Error('tail_cards_cnt%with_cnt == 0 && (with_type == PatternType.type_danpai || with_type == PatternType.type_duizi)').stack)  
                }                
            }
        }else{
            with_type = PatternType.type_danpai
        }

        let rest_cards = []
        let used_cards = []
        for (let i = 0; i < hint_pattern.cards.length; i++) {
            const card = hint_pattern.cards[i];
            used_cards[card] = true
        }

        for (let i = 0; i < cards.length; i++) {
            const card = cards[i];
            if(!used_cards[card]){
                rest_cards.push(card)
            }
        }


        rest_cards.sort(function(a, b){
            return a - b
        })
        if(rest_cards.length >= 2){ //去掉剩余牌中的火箭
            if(Card.makeCardByCode(rest_cards[rest_cards.length - 1]).index == CardNameIndex.card_big_king
            && Card.makeCardByCode(rest_cards[rest_cards.length - 2]).index == CardNameIndex.card_small_king){
                rest_cards.splice(rest_cards.length - 1, 1)
                rest_cards.splice(rest_cards.length - 1)
            }
        }
        if(rest_cards.length >= with_cnt * with_type){            
            let parseResult = DouDiZhuCardCommon.InitParse(rest_cards)            
            let tailData = this.get_tail_cards(parseResult.card_type_stat, parseResult.card_name_info, parseResult.card_name_stat, with_cnt, with_type)
            if(tailData.tail_type && tailData.tail_cards.length / tailData.tail_type == with_cnt){
                for (let i = 0; i < tailData.tail_cards.length; i++) {
                    const card = tailData.tail_cards[i];
                    hint_pattern.cards.push(card)
                }

                hint_pattern.card_cnt += tailData.tail_cards.length
                if(hint_pattern.type == PatternType.type_sandaiyi){
                    hint_pattern.parseDispType()
                }
            }
        }
    }

    //处理四张带牌 4带3
    proc_sizhang_tail_cards(hint_pattern: CardPattern, comp_pattern: CardPattern, cards: number[], with_type?: number){
        let with_cnt = hint_pattern.card_cnt / 4 * 3        

        if(comp_pattern){
            if(comp_pattern.type != PatternType.type_sidaier){
                return
            }
        }

        if(comp_pattern){
            if(comp_pattern.type == PatternType.type_sidaier){
                if(comp_pattern.card_cnt == 6){
                    with_type = PatternType.type_danpai
                }else{
                    with_type = PatternType.type_duizi
                }          
            }
        }else{
            with_type = with_type || PatternType.type_duizi
        }

        let rest_cards = []
        let used_cards = []
        for (let i = 0; i < hint_pattern.cards.length; i++) {
            const card = hint_pattern.cards[i];
            used_cards[card] = true
        }

        for (let i = 0; i < cards.length; i++) {
            const card = cards[i];
            if(!used_cards[card]){
                rest_cards.push(card)
            }
        }


        rest_cards.sort(function(a, b){
            return a - b
        })
        if(rest_cards.length >= 2){ //去掉剩余牌中的火箭
            if(Card.makeCardByCode(rest_cards[rest_cards.length - 1]).index == CardNameIndex.card_big_king
            && Card.makeCardByCode(rest_cards[rest_cards.length - 2]).index == CardNameIndex.card_small_king){
                rest_cards.splice(rest_cards.length - 1, 1)
                rest_cards.splice(rest_cards.length - 1)
            }
        }
        if(rest_cards.length >= with_cnt * with_type){            
            let parseResult = DouDiZhuCardCommon.InitParse(rest_cards)            
            let tailData = this.get_tail_cards(parseResult.card_type_stat, parseResult.card_name_info, parseResult.card_name_stat, with_cnt, with_type)
            if(tailData.tail_type && tailData.tail_cards.length / tailData.tail_type == with_cnt){
                for (let i = 0; i < tailData.tail_cards.length; i++) {
                    const card = tailData.tail_cards[i];
                    hint_pattern.cards.push(card)
                }

                hint_pattern.card_cnt += tailData.tail_cards.length
                hint_pattern.type = PatternType.type_sidaier
                hint_pattern.parseDispType()                
                return
            }
        }
        if(!comp_pattern && with_type == PatternType.type_duizi){
            this.proc_sizhang_tail_cards(hint_pattern, comp_pattern, cards, PatternType.type_danpai)
        }
    }

    
    get_tail_cards(card_type_stat, card_name_info, card_name_stat, with_cnt, with_type){
        let tail_cards = []
        let tail_type = null

        let sort_name = function(name1, name2){
            let get_name = function(name){
                if(name == CardNameIndex.card_A){
                    return CardNameIndex.card_K + 0.1
                }
                if(name == CardNameIndex.card_2){
                    return CardNameIndex.card_K + 0.2
                }
            }
            name1 = get_name(name1)
            name2 = get_name(name2)
            return name1 - name2
        }
        let straight_cards = [] //顺子(多个顺子时从小到大排序)
        let remain_single_cards = [] // 顺子中多余的单牌和不成牌型的单牌(从小到大排序)
        let remain_pair_cards = [] //顺子中多余的对子，非连对对子，连对对子(从小到大排序)
        let single_type_stat = card_type_stat[PatternType.type_danpai]
        if(single_type_stat.length > 0){
            this.porc_serial_cards(card_name_stat, PatternType.type_danpai, single_type_stat.length, single_type_stat, 5, straight_cards, remain_single_cards)            
        }

        let used_pair_cards = []
        for (let i = 0; i < straight_cards.length; i++) {
            const cards = straight_cards[i];
            for (let j = 0; j < cards.length; j++) {
                const name = cards[j];
                if(card_name_stat[name] == 2){
                    used_pair_cards[name] = {index: i, count: 1}
                    remain_single_cards.push(name)
                }else if(card_name_stat[name] == 3){
                    used_pair_cards[name] = {index: i, count: 2}
                    remain_pair_cards.push(name)    //顺子中多余的对子
                }
            }
        }

        let pair_type_stat = []
        for (let i = 0; i < card_type_stat[PatternType.type_duizi].length; i++) {
            const name = card_type_stat[PatternType.type_duizi][i];
            if(!used_pair_cards[name] && card_name_stat[name] == 2){
                pair_type_stat.push(name)
            }
        }

        let straight_pair_cards = []
        let plain_pair_cards = []
        if(pair_type_stat.length > 0){
            this.porc_serial_cards(card_name_stat, PatternType.type_duizi, pair_type_stat.length, pair_type_stat, 2, straight_pair_cards, plain_pair_cards)            
        }
        
        for (let i = 0; i < plain_pair_cards.length; i++) {
            const name = plain_pair_cards[i];
            remain_pair_cards.push(name)
        }        
        remain_pair_cards.sort(sort_name)
        for (let i = 0; i < straight_pair_cards.length; i++) {
            const names = straight_pair_cards[i];
            for (let j = 0; j < names.length; j++) {
                const name = names[j];
                remain_pair_cards.push(name)
            }            
        }

        let remain_three_cards = []
        for (let i = 0; i < card_type_stat[PatternType.type_sandaiyi].length; i++) {
            const name = card_type_stat[PatternType.type_sandaiyi][i];
            if(!used_pair_cards[name] && card_name_stat[name] == 3){
                remain_three_cards.push(name)
            }
        }

        if(with_type == PatternType.type_danpai){
            tail_type = PatternType.type_danpai
            remain_single_cards.sort(sort_name)
            let singleList = []
            for (let i = 0; i < remain_single_cards.length; i++) {
                const name = remain_single_cards[i];
                let tmp_code = card_name_info[name][0]
                tail_cards.push(tmp_code)
                singleList.push(tmp_code)
                if(tail_cards.length >= with_cnt){
                    break
                }
            }

            if(tail_cards.length < with_cnt){   //从对子中凑单张
                for (let i = 0; i < remain_pair_cards.length; i++) {
                    const name = remain_pair_cards[i];
                    if(tail_cards.length >= with_cnt){
                        break
                    }
                    let tmp_code = card_name_info[name][0]
                    let tmp_value =  Card.codeToValue(tmp_code)     
                    if(tail_cards.length + 1 == with_cnt){  //保留大的单牌
                        if(singleList.length > 0){
                            if(Card.codeToValue(singleList[singleList.length - 1]) > tmp_value){
                                tail_cards.pop()
                                singleList.pop()                                
                            }
                        }
                    }              
                    tail_cards.push(tmp_code)                    
                    if(tail_cards.length < with_cnt){
                        tail_cards.push(card_name_info[name][1])
                    }
                }
            }

            if(tail_cards.length < with_cnt){   //从三张中凑单张
                for (let i = 0; i < remain_three_cards.length; i++) {
                    const name = remain_three_cards[i];
                    if(tail_cards.length >= with_cnt){
                        break
                    }

                    tail_cards.push(card_name_info[name][0])
                    if(tail_cards.length < with_cnt){
                        tail_cards.push(card_name_info[name][1])
                        if(tail_cards.length < with_cnt){
                            tail_cards.push(card_name_info[name][2])
                        }
                    }
                }
            }

            if(tail_cards.length < with_cnt){   //从顺子中凑单张
                for (let i = 0; i < straight_cards.length; i++) {
                    const staight_names = straight_cards[i];
                    for (let j = 0; j < staight_names.length; j++) {
                        const name = staight_names[j];
                        tail_cards.push(card_name_info[name][card_name_info[name].length - 1])
                        if(tail_cards.length >= with_cnt){
                            break
                        }
                    }

                    if(tail_cards.length >= with_cnt){
                        break
                    }
                }
            }            
        }else if(with_type == PatternType.type_duizi){
            tail_type = PatternType.type_duizi
            for (let i = 0; i < remain_pair_cards.length; i++) {
                const name = remain_pair_cards[i];
                if(tail_cards.length >= with_cnt * 2){
                    break
                }
                tail_cards.push(card_name_info[name][0])
                tail_cards.push(card_name_info[name][1])
            }

            if(tail_cards.length < with_cnt * 2){   //从三张中凑对子
                for (let i = 0; i < remain_three_cards.length; i++) {
                    const name = remain_three_cards[i];
                    if(tail_cards.length >= with_cnt * 2){
                        break
                    }
                    tail_cards.push(card_name_info[name][0])
                    tail_cards.push(card_name_info[name][1])
                }
            }
        }

        return {tail_cards: tail_cards, tail_type: tail_type}
    }

    
    porc_serial_cards(card_name_stat, type, type_size, type_stat, least_repeat_cnt, straight_names, remain_names){
        let beg = 1
        if(type_size >= least_repeat_cnt){
            for (let i = 1; i <= type_size - 1; i++) {
                if(Card.indexToValue(type_stat[i - 1]) + 1 != Card.indexToValue(type_stat[i - 1 + 1])){
                    if(i - beg + 1 >= least_repeat_cnt){
                        straight_names.push([])
                        for (let j = beg; j <= i; j++) {
                            straight_names[straight_names.length - 1].push(type_stat[j - 1])                    
                        }
                    }else{
                        for (let j = beg; j <= i; j++) {
                            if(card_name_stat[type_stat[j - 1]] == type){
                                remain_names.push(type_stat[j - 1])
                            }   
                        }
                    }

                    beg = i + 1
                    if(type_size - beg + 1 < least_repeat_cnt){
                        break
                    }
                }                
            }
        }
        if(type_size - beg + 1 >= least_repeat_cnt){
            straight_names.push([])
            for (let i = beg; i <= type_size; i++) {                
                straight_names[straight_names.length - 1].push(type_stat[i - 1])
            }
        }else{
            for (let i = beg; i <= type_size; i++) {                
                if(card_name_stat[type_stat[i - 1]] == type){
                    remain_names.push(type_stat[i - 1])
                }
            }
        }
    }
}
