import LocalStorage from "./local_storage";
// import WXGameManager from "./wxgame_manager";
import Debug from "../manager/debug";
import GameUtil from '../utility/game_util';

const { ccclass, property } = cc._decorator;

@ccclass
export default class AudioManager extends cc.Component {

    static instance: AudioManager = new AudioManager()
    @property(cc.AudioSource)
    audioSource: cc.AudioSource = null
    _audioPool = []

    _musicOpen: string = "music_volume"
    _soundOpen: string = "sound_volume"
    _vibrateOpen: string = "vibrate"

    _musicVolume: string = "music_volume_number"
    _soundVolume: string = "sound_volume_number"

    _musicId = -1
    _lastMusicId = -1
    _soundUrls = []
    _curMusicIndex = 0
    _isLoadingMusic = false
    soundCtxPool = []
    maxSoundNum = 8
    _index = 0
    _musicUrl : string = "";

    onLoad() {
        this._audioPool = []
        this._soundUrls = []
        this._musicId = -1
        this._lastMusicId = -1
        this._isLoadingMusic = false
        let self = this
        // WXGameManager.onAudioInterruptionEnd(() => {
        //     //强行暂停音乐 如果不暂停，调用resumeMusic是无效的，因为是微信让声音消失了
        //     cc.audioEngine.pauseMusic();
        //     //恢复音乐播放，比如调用 cc.audioEngine.resumeMusic();
        //     self.resumeMusic()
        //     Debug.warn("音乐播放中断，现在恢复播放")
        // })
        //初始化音乐和音效的音量
        let musicLocalVo = Number(LocalStorage.getNumber(this._musicVolume));
        cc.audioEngine.setMusicVolume(0)
        let soundLocalVo = Number(LocalStorage.getNumber(this._soundVolume));
        cc.audioEngine.setEffectsVolume(0)
    }

    private getAudio(key: string, success: Function) {
        GameUtil.loadRes(key, function (err, clip) {
            if (err) {
                cc.warn(err)
                return
            }
            if (clip) {
                success(clip)
            }
        });
    }

    playMusicLoop() {
        this.playHallBgMusic()
    }

    playHallBgMusic(resume?: boolean) {
        if (this._curMusicIndex == 1 && !resume) {
            return
        }
        let url = "sound/bgm/hall_bg"
        this._curMusicIndex = 1
        this.playMusic(url, true)
    }

    playFightMusic(resume?: boolean) {
        if (this._curMusicIndex == 2 && !resume) {
            return
        }
        let url = "sound/bgm/bg_zhandou1"
        this._curMusicIndex = 2
        this.playMusic(url, true)
    }

    playFightMusic2(resume?: boolean) {
        if (this._curMusicIndex == 3 && !resume) {
            return
        }
        let url = "sound/bgm/bg_zhandou2"
        this._curMusicIndex = 3
        this.playMusic(url, true)
    }

    /**
     * 
     * @param url 
     * @param loop 
     * @param resetSame 是否从头播放相同的音乐
     */
    playMusic(url: string, loop: boolean = true,resetSame : boolean = false) {
        
        // if (!this.isMusicOpen()) {
        //     Debug.log("音乐未开启")
        //     return
        // }
        if (this._musicUrl == url && !resetSame) {
            return;
        }
        this._musicUrl = url;
        this._curMusicIndex = -1
        // cc.audioEngine.stopMusic()
        let self = this
        this.getAudio(url, (clip) => {
            //cc.audioEngine.setMusicVolume(1)
            cc.audioEngine.stopMusic()
            self._musicId = cc.audioEngine.playMusic(clip, true);
            //cc.audioEngine.setVolume(this._musicId, 1)
            Debug.log("播放音乐：", url)   
        });
    }        
       
    pauseMusic() {
        cc.audioEngine.pauseMusic()
    }   

    stopAllMusic() {
        cc.audioEngine.stopAll()
    }

    isMusicOpen() {
        return LocalStorage.getNumber(this._musicOpen, 0) == 0;
    }

    setMusicOpen(isOpen = true) {
        LocalStorage.setNumber(this._musicOpen, isOpen ? 0 : 1)
    }
    setMusicVolume(volume) {
        volume = Math.max(0,volume);
        volume = Math.min(1,volume);           

        cc.audioEngine.setMusicVolume(volume)
        LocalStorage.setNumber(this._musicVolume, volume)
    }
    isSoundOpen() {
        return LocalStorage.getNumber(this._soundOpen, 0) == 0
    }
    isNeedPlaySound()
    {
        return this.isSoundOpen() && cc.audioEngine.getEffectsVolume() > 0;//加上音量大于0的判断。bug:音量为0也有一点点声音
    }
    setSoundOpen(isOpen = true) {
        LocalStorage.setNumber(this._soundOpen, isOpen ? 0 : 1)
    }
    setSoundVolume(volume) {
        
        volume = Math.max(0,volume);
        volume = Math.min(1,volume);      
        
        cc.audioEngine.setEffectsVolume(volume)
        LocalStorage.setNumber(this._soundVolume, volume)
    }
    isVibrateOpen() {
        return LocalStorage.getNumber(this._vibrateOpen, 0) == 0
    }

    setVibrateOpen(isOpen = true) {
        LocalStorage.setNumber(this._vibrateOpen, isOpen ? 0 : 1)
    }

    resumeMusic() {
        let state = cc.audioEngine.getState(this._musicId)
        //cc.audioEngine.setMusicVolume(1)
        Debug.log(this._musicId, state, this.isMusicOpen())
        if (Number(state) >= 0) {
            cc.audioEngine.resumeMusic()
        } else {
            cc.audioEngine.stop(this._musicId)//防止微信中音乐播放状态未切换过来
            if (this._curMusicIndex == 0) {
                this._curMusicIndex = 1
            }
            this.startMusic()
        }
    }

    startMusic() {
        if (this._curMusicIndex == 1) {
            this.playHallBgMusic(true)
        } else if (this._curMusicIndex == 2) {
            this.playFightMusic(true)
        } else if (this._curMusicIndex == 3) {
            this.playFightMusic2(true)
        }
    }

    playSound(url: string, loop: boolean = false, onFinish?: Function) {
        
        if (this.isNeedPlaySound()) {
            this.getAudio(url, (clip : cc.AudioClip) => {
                // Debug.log("playsound:", url)
                // Debug.log("playsound volume:", cc.audioEngine.getEffectsVolume())

                let id = cc.audioEngine.playEffect(clip, false);    
                if (onFinish) {
                    cc.audioEngine.setFinishCallback(id, onFinish)
                }                            

                //cc.audioEngine.setEffectsVolume(1)
            });
        }
    }

    //释放缓存的声音
    release() {
        this.stopAllSound()
    }

    vibrate() {
        if (this.isVibrateOpen()) {
            // WXGameManager.vibrateLong()
        }
    }

    onDestroy() {
        this.unscheduleAllCallbacks()
    }

    // // 播放音频
    // playSound1(url: string) {
    //     let url1 = "resources/" + url + ".mp3"
    //     let localUrl = cc.url.raw(url1)
    //     let md5Url = wxDownloader.REMOTE_SERVER_ROOT + "/" + cc.loader.md5Pipe.transformURL(localUrl)
    //     Debug.log('音频播放地址：' + md5Url);
    //     let soundCtx = this.getSoundCtx();
    //     if (soundCtx) {
    //         soundCtx.src = md5Url;
    //         soundCtx.stop();
    //         soundCtx.play();
    //     }
    // }

    stopAllSound() {
        this.soundCtxPool.forEach(soundCtx => {
            // 暂停或停止了
            if (!soundCtx.paused) {
                soundCtx.stop();
            }
        });
    }

    // // 获取一个音频实例
    // getSoundCtx() {
    //     let soundCtx = null;
    //     for (let i = 0; i++; i < this.soundCtxPool.length) {
    //         soundCtx = this.soundCtxPool[i];
    //         // 暂停或停止了
    //         if (soundCtx.paused) {
    //             return soundCtx;
    //         }
    //     }
    //     if (!soundCtx) {
    //         // 留一个实例用于播放背景音乐
    //         if (this.soundCtxPool.length < this.maxSoundNum - 1) {
    //             soundCtx = wx.createInnerAudioContext();
    //         } else {
    //             soundCtx = this.soundCtxPool[this._index];
    //             this._index++;
    //             if (this._index > this.maxSoundNum) {
    //                 this._index = 0;
    //             }
    //             soundCtx.stop();
    //         }
    //     }
    //     return soundCtx;
    // }


}