import TextureManager from "../../../manager/texture_manager";
import ModuleManager from "../../../framework/core/module_manager";
import { ClubModule } from "../../club/club_module";

const {ccclass, property} = cc._decorator;

@ccclass
export class ClubListItem extends cc.Component {
    @property(cc.Sprite)
    Sprite_flag:cc.Sprite = null;

    @property(cc.Sprite)
    Sprite_head:cc.Sprite = null;

    @property(cc.Label)
    Label_clubName:cc.Label = null;

    @property(cc.Node)
    Button_enterClub:cc.Node = null;

    @property(cc.Label)
    Label_offer:cc.Label = null;

    @property(cc.Label)
    Label_invate:cc.Label = null;

    @property(cc.Label)
    Label_totalCount:cc.Label = null;

    @property(cc.Label)
    Label_onlineCount:cc.Label = null;

    // 俱乐部列表数据
    clubListData:any = null;

    onLoad() {
        this.Button_enterClub.on('click',this.enterClub,this)
    }

    enterClub() {
        let clubId = this.clubListData.clubId;
        ModuleManager.instance.showModule(ClubModule, clubId);
    }

    initUI(data:any) {
        this.clubListData = data;
        let myMemInfo = data.myMemInfo;
        if (myMemInfo.lastId == 0) {
            // 群主
            TextureManager.instance.getLocalSprite('offer_1',(spriteFrame:cc.SpriteFrame)=>{
                this.Sprite_flag.spriteFrame = spriteFrame;
            });
            this.Label_offer.string = '盟主'
        } else if(myMemInfo.invateCode != 0) {
            // 合伙人
            TextureManager.instance.getLocalSprite('offer_2',(spriteFrame:cc.SpriteFrame)=>{
                this.Sprite_flag.spriteFrame = spriteFrame;
            });
            this.Label_offer.string = '合伙人'
        } else {
            // 普通成员
            this.Sprite_flag.node.active = false;
            this.Label_offer.string = '成员'
        }

        TextureManager.instance.getSprite(data.leaderUrl,(sp:cc.SpriteFrame)=>{
            this.Sprite_head.spriteFrame = sp;
        });
        if (myMemInfo.invateCode == 0) {
            this.Label_invate.string = '无';
        } else {
            this.Label_invate.string = myMemInfo.invateCode;
        }
        this.Label_clubName.string = data.clubName;
        this.Label_totalCount.string = data.totalPlayerNum;
        this.Label_onlineCount.string = data.onlinePlayerNum;
    }
}
