import ModelBase from "../../framework/core/mvvm/model_base";
import { JoinClubNetRoute } from "./joinClub_route";

const {ccclass, property} = cc._decorator;

@ccclass
export class JoinClubModel extends ModelBase {
    // 邀请码加入俱乐部
    send_joinClubByCode(invateCode:number) {
        let data = {
            invateCode:invateCode
        }
        this.send(JoinClubNetRoute.joinClubByCode, data)
    }
}
