import ViewBase from "../../../framework/core/mvvm/view_base";
import TextureManager from "../../../manager/texture_manager";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ClubMemListView extends ViewBase {
    @property(cc.EditBox)
    EditBox_id:cc.EditBox = null;

    @property(cc.Label)
    Label_pageIdx:cc.Label = null;

    @property(cc.Sprite)
    Sprite_memNode:cc.Sprite = null;

    @property(cc.Node)
    Button_item:cc.Node = null;

    onShow(){
    }
    onLoad(){
    }

    loadMemList(data:any, curPage:number, totalPage:number) {
        this.Label_pageIdx.string = curPage + '/' + totalPage;
        this.Sprite_memNode.node.removeAllChildren()
        for (let i = 0; i < data.length; i++) {
            const mem = data[i];
            let Button_item = cc.instantiate(this.Button_item);
            Button_item.active = true;
            Button_item.parent = this.Sprite_memNode.node;
            let col = (i+1) % 5;
            if (col == 0) {
                col = 5
            }
            let row = Math.ceil((i+1) / 5);
            Button_item.x = -501 + (col - 1) * 250;
            Button_item.y = -58 - (row - 1) * 98;

            let Sprite_head = Button_item.getChildByName('Sprite_head').getComponent(cc.Sprite);
            let Label_name = Button_item.getChildByName('Label_name').getComponent(cc.Label);
            let Label_state = Button_item.getChildByName('Label_state').getComponent(cc.Label);
            TextureManager.instance.getSprite(mem.avatarUrl,(sp:cc.SpriteFrame)=>{
                Sprite_head.spriteFrame = sp;
            })
            Label_name.string = mem.name;
            if (mem.lastOfflineTime == 0) {
                Label_state.string = '在线'
                Label_state.node.color = cc.color(33, 197, 40, 255);
            } else {
                Label_state.string = '离线'
                Label_state.node.color = cc.color(170, 170, 170, 255);
            }
        }
    }
}
