import Module, { moduleInit } from "../../../../framework/core/mvvm/module_base";
import PublicRuleView from "./public_rule_view";

const { ccclass, property } = cc._decorator;

@ccclass
@moduleInit('PublicRule')
export default class PublicRule extends Module<PublicRuleView, null> {

    constructor() {
        super();
        this.windowPrefabResPath = 'package/pdk/module/Public_Rule';
        this.viewType = PublicRuleView;
        this.modelType = null;
        this.needAnim = true
        this.needViewMask = true
    }

    onShow() {
        this.view.showRule()
    }

}