//默认导入
const { ccclass, property } = cc._decorator;
import ModuleBase, { ViewLayer, moduleInit } from "../../../../framework/core/mvvm/module_base";
import ToastView from "./toast_view";


@ccclass
@moduleInit("Toast")
export default class Toast extends ModuleBase<ToastView, null> {

    constructor() {
        super();
        this.package = "public";
        this.layer = ViewLayer.High;
        this.windowPrefabResPath = "package/public/public_toast";
        this.viewType = ToastView;
    }

    onShow(intentData?: any) {
        if (intentData && typeof (intentData) == "object") {
            let text = intentData.text
            let long = intentData.long
            this._show(text, long)
        } else {
            this._show(intentData)
        }
    }

    _show(text, long?: boolean) {
        let node = cc.instantiate(this.view.holder);
        this.view.node.addChild(node)
        let spriteBackground = node.getChildByName("SpriteBackground").getComponent(cc.Sprite)
        let textLabel = node.getChildByName("Label").getComponent(cc.Label)
        textLabel.string = text; 
        //@ts-ignore
        if (!CC_JSB) {
            //@ts-ignore
            textLabel._updateRenderData(true)//刷新Label宽高度   
        }  
        spriteBackground.node.width = textLabel.node.width + 40;
        let finished = cc.callFunc(function (target) {
            node.destroy();
        }, this);
        let time1 = 1
        let time2 = 0.6
        if (long) {
            time1 = 2
            time2 = 1.2
        }

        //需求变更：不向上飘动
        let action = cc.sequence(cc.moveBy(time1, cc.v2(0, 50)), cc.fadeOut(time2), finished);
        node.runAction(action);
    }
}