import { GameManager } from "../../../manager/game_manager";
import Debug from "../../../manager/debug";
import { stat } from "fs";

export default class AppData {
    //版本信息，非常重要
    static appVersion: string = "1.0.19"
    static appName: string = "TM"
    static gameid: number = 15;//跑得快
    static apiGameName: string = "TM_LHD"
    static ip: string = "api.slddz.sincebest.com"
    static code:string;
    static port:number;
    static proxyHost: string = 'http://test.api.h5international.sincebest.com:8080/proxy?url='

    static isIphoneX: boolean = false //iphonex
    static iphonexOffset = 50
    static designResolution : cc.Size

    static isTest = false;

    static setServerByData() {
        if (AppData.isTest) {
            AppData.ip = '127.0.0.1'
            AppData.port = 8686
        }else{
            AppData.ip = "47.99.50.101";
            AppData.port = 8686
        }
     
        //登录服地址
        GameManager.loginServer = "ws://" + AppData.ip + ':' + AppData.port;
    }
}