import ViewBase from "../../../framework/core/mvvm/view_base";
import AppData from "../const/app_data";
import VersionManager from "../../../manager/version_manager";
import LanguageManager from "../../../manager/language_manager";
import LoaclStorage from "../../../manager/local_storage";


const {ccclass, property} = cc._decorator;

@ccclass
export default class LoadingView extends ViewBase {


    @property(cc.ProgressBar)
    public progressBar : cc.ProgressBar = null;

    @property(cc.Node)
    public barRightNode : cc.Node = null;

    @property(cc.Label)
    public textLabel : cc.Label = null;

    @property(cc.Label)
    public versionLabel : cc.Label = null;

    onLoad()
    {
        //this.startProgress();
        //this.barRightNode.active = false;

    }
    onShow()
    {
        super.onShow();        

        if (cc.sys.isNative) {
            this.showTextLabel( "版本检测中,请稍后...");     
        } else {
            //版本检测中...请稍后
            this.showTextLabel( "版本检测中,请稍后...");  
        }

      

        this.updateVersion(LoaclStorage.getNumber("version"));

        VersionManager.instance.checkVersion();

    }

    public updateVersion(version : string){

        if (version) {         
            this.versionLabel.string = "v:" + version;
        }        
    }

    private showTextLabel(content : string){

        this.textLabel.string = content;
    }

    public updatePercent(value : number){

        this.progressBar.node.active = true;

        this.progressBar.progress = value;        
        
        let bar = this.progressBar.node.getChildByName("SpriteBar");
        let barSprite = bar.getComponent(cc.Sprite);
        let width = barSprite.spriteFrame.getRect().width;
        this.barRightNode.position = new cc.Vec2(bar.position.x + width * value,this.barRightNode.position.y);        
        this.barRightNode.active = value > 0 ? true : false;        

        this.showTextLabel('资源加载中' + ": " +  (value * 100).toFixed(0) + "%");
    }
}
