
export interface IUserData {
    avatarUrl?:string;
    coin?:number;
    gender?:number;
    uid?:number;
    name?:string;
    openid?:number;
}

//用户类
export default class UserDataManager {

    public static readonly instance: UserDataManager = new UserDataManager();

    private _userInfo:IUserData = null;

    mChairID:number = null;
    mPlayerCount:number = null;

    setUserData(data:any){
        this._userInfo = data;
    }

    getUserData():IUserData{
        return this._userInfo;
    }

    isMale(){
        return this._userInfo.gender == 1;
    }
}