import ModelBase from "../../../framework/core/mvvm/model_base";
import NetClient from "../../../framework/net/net_client";
import TimeManager from "../../../manager/time_manager";
import { GameManager } from "../../../manager/game_manager";
import ModuleManager from "../../../framework/core/module_manager";
import NetPrompt from "../../public/module/netprompt/netprompt";
import GameServer from "../gameserver/game_server";
import ModelData from "../../public/model_data";
import AppData from "../../public/const/app_data";
import HallModule from "../../hall/hall_module";
import ServerBase from "../server_base";
import SceneManager from "../../../manager/scene_manager";
import LoginServer from "../loginserver/login_server";
import { HallServerRoute } from "./hall_net_route";
import { HallNetRoute } from "../../hall/hall_route";
import { ServerCode } from "../../public/const/const_code";
import UserDataManager from "../../../manager/user_manager";
import ModuleData from "../../public/const/module_data";

const { ccclass, property } = cc._decorator;

@ccclass
export default class HallServer extends ServerBase {

    public static instance: HallServer;

    isNeedCheck:boolean = true;//游戏中返回大厅

    onLoad() {
        HallServer.instance = this
        this.init('HallServer');
    }

    _init() {
        let self = this;
        this.model.registerNetMsg(HallServerRoute.enter,function(data){
            self.loginRsp(data);
        },true);
        this.model.registerNetMsg(HallServerRoute.joinRoom,function(data){
            self.joinRoom(data);
        },true)

        this.model.registerNetMsg(HallServerRoute.getGameServerInfo,function(data){
            self.enterTable(data);
        },true)
    }


    //登录成功断开登录服
    loginRsp(data:any){
        if (data.code == 200) {//登陆成功
            UserDataManager.instance.setUserData(data.info);
            let info = data.info.gameInfo;
            if (info && this.isNeedCheck) {
                if (info.code == ServerCode.GameStatus.GAME_PLAYING) {
                    ModelData.instance.tableId = info.gameInfo.tableId || 0;
                    ModelData.instance.clubID = info.gameInfo.clubId || 0;
                    ModelData.instance.playwayId = info.gameInfo.playwayId || '0';
                    this._enterGameServer(info.gameInfo.host,info.gameInfo.port)
                }else{
                    SceneManager.showHallUI();
                }
            }else{
                SceneManager.showHallUI();
            }
            this.isNeedCheck = true;//默认需要进行检测，仅仅返回大厅时不需
        }
    }

    enterTable(data:any){
        if (data.code == ServerCode.ServerAdrCode.OK) {
            this._enterGameServer(data.host,data.port)
        }else{
            SceneManager.showHallUI();
        }
    }

    private _enterGameServer(ip:string,port:number){
        ModuleManager.instance.showModule(NetPrompt)
        this.close();
        GameManager.setGameServer(ip,port);
        GameServer.instance.connectServer(GameManager.gameServer);
    }

    checkIsPlaying(data:any){
        ModuleManager.instance.showModule(NetPrompt)
        if (data.code == ServerCode.GameStatus.GAME_PLAYING) {
            this.close();
            let info = data.gameInfo;
            GameManager.setGameServer(info.ip,info.port);
            GameServer.instance.connectServer(GameManager.gameServer);
        }else{
            SceneManager.showHallUI();
        }
    }

    //创建游戏
    createGame(gameid:number,clubid:number,playwayid:string){
        this._joinGame(gameid,0,clubid,playwayid);
    }

    //加入游戏
    enterGame(gameInfo:any){
        ModuleManager.instance.showModule(NetPrompt)
        AppData.gameid = gameInfo.gameId;
        ModelData.instance.tableId = gameInfo.tableId;
        ModelData.instance.clubID = 0;
        ModelData.instance.playwayId = '';
        this._enterGameServer(gameInfo.host,gameInfo.port)
    }

    private _joinGame(gameid:number,tableid:number,clubid:number,playwayid:string){
        ModuleManager.instance.showModule(NetPrompt)
        AppData.gameid = gameid;
        ModelData.instance.tableId = tableid;
        ModelData.instance.clubID = clubid;
        ModelData.instance.playwayId = playwayid;
        this.model.send(HallServerRoute.getGameServerInfo,{gameId:gameid});
    }

    private joinRoom(data:any){
        if (data.code == 0) {
            this.close();
            ModuleManager.instance.showModule(NetPrompt, { showImmediately:true})
            GameServer.instance.connectServer(GameManager.hallServer);
        }
    }

    //账号登陆
    loginHallServer(){
        ModuleManager.instance.showModule(NetPrompt)
        this.connectServer(GameManager.hallServer,(state:string)=>{
            if (state == 'handshake') {
                let data = {
                    code:AppData.code,
                    userInfo:{
                        name:AppData.code,
                        avatarUrl:'http://sincebest-bull-test.oss-cn-hangzhou.aliyuncs.com/20180723_e852c0d11edf47529de8e7c69c848ca8.jpg',
                        gender:1,
                    },    
                    platform:'win',
                }
                
                this.model.send(HallServerRoute.enter,data);
                this.startCheckPing();
            }
        })
    }


    //连接登录
    connectServer(url, callback?: (state: string) => void) {
        this.connect(url,callback);
        cc.log("大厅服连接时间：", TimeManager.instance.getServerTime())

    }

    protected connectTimes(times:number){
        if (this._reconTimes >= 1) {
            ModuleManager.instance.showModule(NetPrompt, { showImmediately: true, reconnect: true })
        }

        if (this._reconTimes > this._maxReconTimes) {
            cc.warn("登陆服重连次数用完，提示登陆失败")
            SceneManager.gotoLogin();
        }
    }

    onConnected(){
        this._init();
        ModuleManager.instance.hideModule('NetPrompt');
    }
}
