import { Long, util } from "protobufjs";
import { GameManager } from "../manager/game_manager";
import AppData from "../pacakge/public/const/app_data";
import MD5 from "./md5";
import Debug from "../manager/debug";
import MemoryStorage from "../manager/memory_storage";
