export interface IUrlRequestData {
    baseUrl: string
    url ?: string
    showNetprompt?: boolean
    notRequiredToken?: boolean
    params?: object
    module?: object  //属于那个模块
    requestCount?: number  //请求次数，一般是用来统计在http请求失败
    useCache?: boolean      //是否使用cache数据
    cacheKey?: string
}


export default class GameUtil {

    private static _http_get(requestData: IUrlRequestData, responeSuccessCallback: any, responeErrorCallback: any) {
        var xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4) {
                if (xhr.status == 200) {
                    var response = JSON.parse(xhr.responseText);
                    if (!response) {
                        Debug.error("http返回数据格式错误：", xhr.responseURL, xhr.responseText)
                        return
                    }
 
                    if(requestData.useCache){
                        MemoryStorage.setJson(requestData.cacheKey, response)
                    }
                    responeSuccessCallback && responeSuccessCallback(response)
                } else {
                    if (xhr.responseText && xhr.responseText != "") {
                        var response = JSON.parse(xhr.responseText);
                        responeErrorCallback(response)
                    } else {
                        responeErrorCallback(null)
                    }
                    Debug.warn("http request error:", xhr.responseText)
                }
            }
        };
        //ontimeout 现在暂时只在web上生效，webchat需要使用onerror
        xhr.onerror = function(err : any) {
            //需要确认在超时的时候是否也会同时回调onreadystatechange
            cc.log(err)
            if (err && typeof(err) =='string' && err.indexOf('timeout') !== -1) {
                if (requestData.requestCount < 6) {
                    GameUtil._http_get(requestData, responeSuccessCallback, responeErrorCallback)
                }
                requestData.requestCount += 1
            }
        }
        //z
        xhr.ontimeout = function(err : any) {
            cc.log("ontimeout")
            if (requestData.requestCount < 6) {
                GameUtil._http_get(requestData, responeSuccessCallback, responeErrorCallback)
            }
            requestData.requestCount += 1
        }
        xhr.timeout = 5000
        xhr.open("GET", requestData.url, true);
        xhr.send();
    }

    static signature(params : object) {
        let sortedObjKeys = Object.keys(params).sort();
        let text = params["token"]
        for (let key of sortedObjKeys) {
            if (key != "token") {
                text += key + params[key]
            }
        }
        text += "LGCSBnMznC8IO5L1"
        let md5 = new MD5()
        return md5.hex_md5(text)
    }

    static loadTexture(url: string, callback) {
        if (url.length <= 0) {
            return;
        }
        var xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4) {
                if (xhr.status == 200) {
                    var blob = new Uint8Array(this.response);
                    var img = new Image();
                    let strinsdf = util.base64.encode(blob, 0, blob.length)
                    var base = "data:image/png;base64," + strinsdf;
                    img.src = base;
                    img.onload = function () {
                        var texture = new cc.Texture2D();
                        texture.initWithElement(img);
                        texture.handleLoadedTexture();
                        callback(null, texture)
                    }
                } else {
                    callback(xhr.statusText, null)
                    Debug.warn("load texture error:", xhr.statusText)
                }
            }
        };
        xhr.open("get", url);
        xhr.responseType = 'arraybuffer';
        xhr.send(null);
    }

    static formatParamsForHttp(data: IUrlRequestData): string {
        let url: string = ""
        let baseUrl = data.baseUrl
        let index: number = 0
        for (const key in data.params) {
            if (!data.params[key]) {
                Debug.warn("key not in params:", key, data.params)
            }
            if (index == 0) {
                url = baseUrl + key + "=" + this.encodeURL(data.params[key].toString())
            } else {
                url = url + "&" + key + "=" + this.encodeURL(data.params[key].toString())
            }
            index += 1
        }
        return url
    }
    //seach是指url从'?'后面的部分，包含?
    static getQueryString(seach: string, keyName: string) {
        //匹配指定name的QueryString
        var reg = new RegExp('(^|&)' + keyName + '=([^&]*)(&|$)', 'i');
        var r = seach.substr(1).match(reg);
        if (r != null) {
            return unescape(r[2]); //用于解码"="后的值,即$2
        }
        return null;
    };
    


    static encodeURL(s: string): string {
        return encodeURIComponent(s)
    }

    static string2point(s): string {
        let l = s.charCodeAt(0)
        let e = l.toString(16)
        return ("%" + e)
    }

    static get_now_timestamp() {
        let d = new Date()
        let timestamp = Date.parse(d.toString());
        return timestamp / 1000
    }

    static get_md5(timestamp: number | Long, userId: number | Long, salt_a: string, salt_b: string) {
        let md5 = new MD5()
        let s1 = md5.hex_md5(salt_a + userId.toString())
        let s2 = s1.replace("-", "").toLowerCase()
        let s3 = md5.hex_md5(timestamp.toString() + s2 + salt_b)
        let s4 = s3.replace("-", "").toLowerCase()
        return s4
    }

    //获取字符串的字节长度
    static stringBytesLength(str: string) {
        let length: number = 0
        for (let i = 0; i < str.length; i++) {
            let iCode = str.charCodeAt(i);
            if ((iCode >= 0 && iCode <= 255) || (iCode >= 0xff61 && iCode <= 0xff9f)) {
                length += 1;
            } else {
                length += 2;
            }
        }
        return length
    }
    //字符串转utf8字节
    static str2UTF8Byte(str) {
        var bytes = new Array();
        var len, c;
        len = str.length;
        for (var i = 0; i < len; i++) {
            c = str.charCodeAt(i);
            if (c >= 0x010000 && c <= 0x10FFFF) {
                bytes.push(((c >> 18) & 0x07) | 0xF0);
                bytes.push(((c >> 12) & 0x3F) | 0x80);
                bytes.push(((c >> 6) & 0x3F) | 0x80);
                bytes.push((c & 0x3F) | 0x80);
            } else if (c >= 0x000800 && c <= 0x00FFFF) {
                bytes.push(((c >> 12) & 0x0F) | 0xE0);
                bytes.push(((c >> 6) & 0x3F) | 0x80);
                bytes.push((c & 0x3F) | 0x80);
            } else if (c >= 0x000080 && c <= 0x0007FF) {
                bytes.push(((c >> 6) & 0x1F) | 0xC0);
                bytes.push((c & 0x3F) | 0x80);
            } else {
                bytes.push(c & 0xFF);
            }
        }
        return bytes;
    }

    static _ResCaches = {}

    static loadRes(url: string, completeCallback: (error: Error, resource: any) => void, type ?: typeof cc.Asset, connect?: number): void {
        let res = GameUtil._ResCaches[url]
        if (res) {
            completeCallback(null, res)
            return
        }

        if (!connect) {
            connect = 1
        }

        cc.loader.loadRes(url, type, function (error: Error, resource: any) {
            if (error) {
                connect += 1
                if (connect < 10) {
                    GameUtil.loadRes(url, completeCallback, type, connect)
                    Debug.warn("loadRes失败，重试：", url, error)
                } else {
                    completeCallback(error, resource)
                }
            } else {
                GameUtil._ResCaches[url] = resource
                completeCallback(error, resource)
            }
        })
    }

    
    static loadResArray(url: string[], completeCallback: (error: Error, resource: any[]) => void, connect?: number): void {
        // let res = GameUtil._ResCaches[url]
        // if (res) {
        //     completeCallback(null, res)
        //     return
        // }

        if (!connect) {
            connect = 1
        }
        cc.loader.loadResArray(url, function (error: Error, resource: any[]) {
            if (error) {
                connect += 1
                if (connect < 10) {
                    GameUtil.loadResArray(url, completeCallback, connect)
                    Debug.warn("loadRes失败，重试：", url, error)
                } else {
                    completeCallback(error, resource)
                }
            } else {
                // GameUtil._ResCaches[url] = resource
                completeCallback(error, resource)
            }
        })
    }

    /** 
   * @param str：需要截取的字符串 
   * @param len: 需要截取的长度 默认五个汉字的 
   */
    static cutStr(str, len = 7){
        var str_length = 0;
        var str_len = 0;
        let str_cut = new String();
        str_len = str.length;
        for (var i = 0; i < str_len; i++) {
            let a = str.charAt(i);
            str_length++;
            if (escape(a).length > 4) {
                //中文字符的长度经编码之后大于4 
                str_length++;
            }
            str_cut = str_cut.concat(a);
            if (str_length >= len) {
                str_cut = str_cut.concat("...");
                return str_cut;
            }
        }
        //如果给定字符串小于指定长度，则返回源字符串； 
        if (str_length <= len) {
            return str;
        }

    }

    // 货币格式 fixedNumb:保留小数
    static currencyFormatting(money: number,fixedNumb:number = 2):string {
        if (money == null) {
            return '';
        }
        if (money <= 0.001) {
            return '0';
        }
        let unit = ['','K','M','B','T'];
        let unitIndex = 0;
        let i = money;
        for (;i >= 1e3;) {
            i /= 1e3;
            unitIndex++;
        }
        if (unitIndex < unit.length) {
            let num = Math.floor(i * 1000 / 10) / 100;//这样做为了处理一个误差77.1*100
            return num.toFixed(fixedNumb) + unit[unitIndex];
        }else{//超过T
            return (money / 1e12).toFixed(fixedNumb) + 'T';
        }
    }

    //保留两位小数
    static getRightMount(mount:number):number{
        if (mount == undefined) {
            cc.warn('mount 异常',mount)
            return 0;
        }
        let r   =   /^[0-9]*[1-9][0-9]*$/
        let m = mount
        if (!r.test(mount.toString()) ) {
            m = parseFloat(mount.toFixed(2));
        }
        return m;
    }

    //千分位显示
    static addThousands(number) {
        if (number == undefined) {
            return '0';
        }
        if(number==0){
            return number;
        }else if(number==0.0){
            return number;
        }
        var num = number + "";
        num = num.replace(new RegExp(",","g"),"");
        // 正负号处理
        var symble = "";
        if(/^([-+]).*$/.test(num)) {
            symble = num.replace(/^([-+]).*$/,"$1");
            num = num.replace(/^([-+])(.*)$/,"$2");
        }
        if(/^[0-9]+(\.[0-9]+)?$/.test(num)) {
            var num = num.replace(new RegExp("^[0]+","g"),"");
            if(/^\./.test(num)) {
                num = "0" + num;
            }
            var decimal = num.replace(/^[0-9]+(\.[0-9]+)?$/,"$1");
            var integer= num.replace(/^([0-9]+)(\.[0-9]+)?$/,"$1");
            var re=/(\d+)(\d{3})/;
            while(re.test(integer)){
                integer = integer.replace(re,"$1,$2");
            }
            return symble + integer + decimal;
        } else {
            return number;
        }
    }

    static AddChild(prefab:cc.Node,parent:cc.Node):cc.Node{
        if (parent === null || prefab === null) {
            cc.log('prefab or parent is null');
            return null;
        }
        let node = cc.instantiate(prefab);
        node.position = cc.Vec2.ZERO;
        node.angle = 0;
        node.scale = 1;
        node.active = true;
        parent.addChild(node);
        return node;
    }

    static setNodeString(item:cc.Node,str:string){
        let richComp = item.getComponent(cc.RichText)
        if(richComp)
        {
            richComp.string = str;
        }else{
            let labelComp = item.getComponent(cc.Label)
            if(labelComp)
            {
                labelComp.string = str;
            }
        }
    }


}
