import ViewBase from "../../framework/core/mvvm/view_base";
import ModuleManager from "../../framework/core/module_manager";
import UserDataManager, { IUserData } from "../../manager/user_manager";
import Utility from "../../utility/Utility";
import GameUtil from "../../utility/game_util";
import SceneManager from "../../manager/scene_manager";
import TextureManager from "../../manager/texture_manager";
import { ClubListItem } from "./component/clubList_item";

const {ccclass, property} = cc._decorator;

@ccclass
export default class HallView extends ViewBase {
    @property(cc.Node)
    userIDLab:cc.Node = null;

    @property(cc.Node)
    userNameLab:cc.Node = null;

    @property(cc.Sprite)
    avatar:cc.Sprite = null;

    @property(cc.Sprite)
    Sprite_noClubFlag:cc.Sprite = null;

    @property(cc.Label)
    Label_noClubFlag:cc.Label = null;

    @property(cc.PageView)
    PageView_clubList:cc.PageView = null;

    @property(cc.Node)
    Sprite_clubFrame:cc.Node = null;

    @property(cc.Node)
    Node_gameItem:cc.Node = null;

    onShow(){
        SceneManager.lastEntranceOpenModuleName = ''
    }
    onLoad(){
        ModuleManager.instance.hideModule("NetPrompt")
    }

    updateUserInfo(){
        let data = UserDataManager.instance.getUserData();
        GameUtil.setNodeString(this.userIDLab,"ID:"+data.uid);
        let subName = Utility.instance.playerNameClamp(data.name,5,'...');
        GameUtil.setNodeString(this.userNameLab,subName);
        TextureManager.instance.getSprite(data.avatarUrl,(sp:cc.SpriteFrame)=>{
            this.avatar.spriteFrame = sp;
        })
    }

    showClubList(data:any) {
        if (data) {
            this.Sprite_noClubFlag.node.active = false;
            this.Label_noClubFlag.node.active = false;
            this.Node_gameItem.active = true;
            this.PageView_clubList.removeAllPages();
            for (let i = 0; i < data.clubList.length; i++) {
                const clubData = data.clubList[i];
                let Sprite_clubFrame = cc.instantiate(this.Sprite_clubFrame);
                Sprite_clubFrame.active = true;
                this.PageView_clubList.addPage(Sprite_clubFrame);
                let item = Sprite_clubFrame.getComponent(ClubListItem);
                item.initUI(clubData);
            }
            
        } else {
            this.Sprite_noClubFlag.node.active = true;
            this.Label_noClubFlag.node.active = true;
            this.Node_gameItem.active = false;
        }
    }

    
}
