const configLanguageData = require('./ConfigLanguageData');
// const uiLabel = require('../uiextend/ULabel.js');
// Returns a function, that, as long as it continues to be invoked, will not
// be triggered. The function will be called after it stops being called for
// N milliseconds. If `immediate` is passed, trigger the function on the
// leading edge, instead of the trailing.
function debounce(func, wait, immediate) {
    var timeout;
    return function () {

        var context = this, args = arguments;
        //cc.log("this:" + this + ",args:" + arguments + "," + "context:" + context);
        var later = function () {
            timeout = null;
            if (!immediate) func.apply(context, args);
        };
        var callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) func.apply(context, args);
    };
}

cc.Class({
    extends: cc.Component,

    editor: {
        executeInEditMode: true,
        menu: 'UI/TLabel'
    },
    // properties:
    // {
    //     uiLabel: {
    //         get() {
    //             return this.getComponent("ULabel");
    //         },
    //     }
    // },
    properties: {
        ULabelKey: {
            get() {
                return this._ulabelKey;
            },
            set(val) {
                let ulabel = this.getComponent("ULabel");
                if (ulabel) {
                    ulabel.i18nKey = this._ulabelKey;
                }
                if (this._ulabelKey !== val) {
                    this._ulabelKey = val;
                    if (ulabel) {
                        if (CC_EDITOR) {
                            if (!this._debouncedUpdateLabel) {
                                this._debouncedUpdateLabel = debounce(this.updateULabel, 200);
                            }
                            this._debouncedUpdateLabel();
                        }
                        // else {
                        //     this.updateLabel();
                        // }
                    }
                }
            }
        },
        _ulabelKey: ''
    },

    onLoad() {
        //cc.log("cc.log(UILabel):"+UILabel);
        if (CC_EDITOR) {
            let ulabel = this.getComponent("ULabel");
            if (ulabel && ulabel.i18nKey != "")
                this._debouncedUpdateLabel = debounce(this.updateULabel, 200);
        }
        if (!configLanguageData.inst) {
            configLanguageData.init();
        }

        //cc.log('dataID: ' + this.dataID + ' value: ' + i18n.t(this.dataID));
        this.fetchRender();
    },
    fetchRender() {
        //cc.log("fetchRender...");
        let label = this.getComponent(cc.Label);
        //uiLabel.onChangeLanguage();
        //cc.log("this.parent:" + uiLabel);

        let ulabel = this.getComponent("ULabel");
        if (ulabel)
            ulabel.i18nKey = this._ulabelKey;
        //ulabel.i18nKey = "123"
        //cc.log("ulabel.i18nKey" + ulabel.i18nKey);
        //cc.log("sdk:"+require('SdkBridge'));
        if (label && ulabel) {
            this.label = label;
            this.updateULabel();
            return;
        }
    },

    updateULabel() {
        let ulabel = this.getComponent("ULabel");
        if(ulabel)
        ulabel.i18nKey = this._ulabelKey;
        if (!this.label) {
            this.label = this.getComponent(cc.Label);
        }
        if (!this.label) {
            cc.error('Failed to update localized label, label component is invalid!');
            return;
        }
        if (!ulabel) {
            cc.error('Failed to update localized ULabel, ULabel component is invalid!');
            return;
        }
        //在这里修改读取json文件的类容
        let localizedString = configLanguageData.parse(ulabel.i18nKey);
        if (localizedString) {
            this.label.string = configLanguageData.parse(ulabel.i18nKey);
        }
    }
});