const {ccclass} = cc._decorator;

export enum NetClientProtoType {
    LoginServer = 1,
    GameServer,
    HallServer,
}

//@ts-ignore
require('pomelo')
//网络客户端
@ccclass
export default class NetClient{

    pomelo:any;//cc.Pomelo

    netClientProtoType:NetClientProtoType;

    netState: boolean

    name:string;

    lastReceivePingTime: number = 0

    heartbeatInterval:number = 0;//心跳间隔

    heartbeatTimeout:number = 0;//心跳超时

    private msgMap = {};

    onConnectStateEvent:(state: string, info?: string) => void;

    heartbeatEvent:()=>void;

    constructor(){
        //@ts-ignore
        this.pomelo = new cc.Pomelo();
    }

    connect(url:string,protoType?: NetClientProtoType){
        if (this.pomelo != null) {
            this.pomelo.clear();
        }
        this.registerNetMsg();
        this.pomelo.connect(url,null);
        if (protoType == null) {
            this.netClientProtoType = NetClientProtoType.LoginServer;
        }
        else {
            this.netClientProtoType = protoType
        }
    }

    private registerNetMsg(){
        this.clearSysNetMsg();
        this.sysNetMsg('onOpen',this.onOpen.bind(this))
        this.sysNetMsg('onclose',this.onclose.bind(this))
        this.sysNetMsg('onerror',this.onerror.bind(this))
        this.sysNetMsg('handshake',this.handshake.bind(this));
        this.sysNetMsg('heartbeat',this.heartbeatFunc.bind(this));
    }

    //监听网络
    on(route:string,callback: Function) {
        this.pomelo.on(route,callback);
    }

    //移除网络监听
    off(route:string, callback: Function) {
        this.pomelo.off(route,callback);
    }

    //发送网络请求,func 
    send(route:string,message:any){
        this.pomelo.request(route,message || {},(msg:any)=>{
            this.emit(route,msg);
        });
    }

    private emit(event:string,msg:any){
        this.pomelo.emit(event,msg);
    }

    heartbeat(){
        this.pomelo.heartbeat();
    }

    //移除所有网络监听
    removeAllNetListeners(){
        // @ts-ignore
        this.pomelo.off();
    }

    sysNetMsg(route:string,callback:Function){
        this.on(route,callback);
        this.msgMap[route] = callback;
    }

    clearSysNetMsg(){
        for (let key in this.msgMap) {
            let callback = this.msgMap[key];
            if (this.msgMap.hasOwnProperty(key)) {
                this.off(key,callback);
            }
        }   
        this.msgMap = {};
    }

    private onOpen(event){
        cc.log('onPen');
        this.lastReceivePingTime = Date.now();
        this.netState = true;
        if (this.onConnectStateEvent) {
            this.onConnectStateEvent("OPEN")
        }
    }

    private onclose(event){
        cc.log('onclose',event);
        this.netState = false;
        if (this.onConnectStateEvent) {
            this.onConnectStateEvent("CLOSE")
        }
    }

    private onerror(event){
        cc.error('onerror',event);
        this.netState = false;
        if (this.onConnectStateEvent) {
            this.onConnectStateEvent("error")
        }
    }

    //连接成功
    private handshake(data){
        this.netState = true;
        this.heartbeatTimeout = 7 * 1000;//7-秒
        this.heartbeatInterval = 2;
        if (this.onConnectStateEvent) {
            this.onConnectStateEvent("handshake")
        }
    }

    private heartbeatFunc(){
        this.lastReceivePingTime = Date.now();
        //cc.log('-->>>receive ping',this.lastReceivePingTime);
        if (this.heartbeatEvent) {
            this.heartbeatEvent();
        }
    }

    //检查是否心跳超时 已连接状态超时
    checkHeartBeatTimeOut(time: number = 12000) {
        if ((Date.now() > this.lastReceivePingTime + time) && this.netState) {
            cc.log("超时，重连！")
            return true;
        }
        return false;
    }

    //主动断开网络
    activeClose(){
        this.netState = false;
        if (this.pomelo != null) {
            this.pomelo.clear();
            let socket = this.pomelo.websocket();
            if (socket) {
                socket.close(1000, "activeClose");
            }
        }
        this.pomelo.socket = null;
        if (this.onConnectStateEvent) {
            this.onConnectStateEvent("ActiveClose")
            this.onConnectStateEvent = null
        }
    }

    disconnect(){
        this.netState = false;
        if (this.pomelo != null) {
            this.pomelo.clear();
            let socket = this.pomelo.websocket();
            if (socket) {
                socket.close(1000, "disconnect");
            }
        }
        if (this.onConnectStateEvent) {
            this.onConnectStateEvent("CLOSE")
            this.onConnectStateEvent = null
        }
    }
}
