import ModelBase from "../../framework/core/mvvm/model_base";
import { HallNetRoute } from "./hall_route";

const {ccclass, property} = cc._decorator;

@ccclass
export class HllModel extends ModelBase {
    
    send_enterClub(clubid:number){
        let data = {
            clubId:clubid
        }
        this.send(HallNetRoute.enterClub,data)
    }

    send_getclubList(){
        this.send(HallNetRoute.getClubList);
    }

}
