import EventManager from "./event_manager";
import AudioManager from "./audio_manager";
// import MakePointsManager from "./make_points_manager";
import ModuleManager from "../framework/core/module_manager";
import LogReportManager from "./log_report_manager";
import Debug from "./debug";

const { ccclass } = cc._decorator;

enum TimeType {
    INTERVAL,
    COUNT_DOWN
}


@ccclass
export default class TimeManager extends cc.Component {

    _needCheckModule: string[] = [
        "AuthModule",
        "HallModule",
        "RandomAwardModule",
        "RankingMatchStarup",
        "DouDiZhuTableModule",
        "DouDiZhuMatchingTableModule",
        "Match",
        "GoldEntranceModule",
        "Bag",
        "ShopModule",
        "CreatRoom",
        "HallRank",
    ]

    public static readonly instance: TimeManager = new TimeManager();
    backGround = false

    private _handles = []
    private _serverMarkTime: number = 0//服务器初始化时间
    private _serverLocalDifTime: number = 0//服务器初始化时和本地相差时间
    private _lastTime = 0

    private _hideTime = 0//切后台的时间
    private _showTime = 0//切到前台的时间

    private _startCheckBlack = false//开始黑屏检测
    private _blackTimes = 0//检测黑屏次数

    private _backTarget = ""//切到后台前标记，切回前台后使用

    m_maxId: number = 0
    m_callbackMap = {}

    AppRunningTime = 0

    start() {
        if (CC_WECHATGAME) {
            // WXGameManager.onShow(this.wx_game_focus)
            // WXGameManager.onHide(this.wx_game_hide)
            //WXGameManager.onDeviceOrientationChange(this.onDeviceOrientationChange)
        } else {
            cc.game.off(cc.game.EVENT_HIDE, this.onApplicationHide.bind(this))
            cc.game.off(cc.game.EVENT_SHOW, this.onApplicationShow.bind(this))
            cc.game.on(cc.game.EVENT_HIDE, this.onApplicationHide.bind(this))
            cc.game.on(cc.game.EVENT_RESTART, this.onApplicationShow.bind(this))
            cc.view.getScaleX
        }
    }

    setBackTarget(value: string) {
        TimeManager.instance._backTarget = value
    }

    getBackTarget() {
        return TimeManager.instance._backTarget
    }




    private onApplicationHide() {
        TimeManager.instance._hideTime = Date.now()
        Debug.log("切到后台了！", TimeManager.instance._hideTime)
        this.backGround = true
        AudioManager.instance.release()//必须释放掉声音，不然微信中切到前台会自动播放缓存的声音
        EventManager.instance.dispatch("game_event_show", false)
    }

    private onApplicationShow() {
        TimeManager.instance._showTime = Date.now()
        let difTime = TimeManager.instance._showTime - TimeManager.instance._hideTime
        Debug.log("切到前台了！", TimeManager.instance._showTime, difTime)
        this.backGround = false
        EventManager.instance.dispatch("game_event_show", true, difTime, TimeManager.instance._backTarget)
        //MatchingManager.instance.dealSpecialQrcode(28)
    }

    //轮询 name为key值 每个interval调用一次callback count是执行次数不传或0都是无限次
    setInerval(name: string, callback: Function, target: any, interval: number, count?: number) {
        let index = this.hasHander(name, callback, TimeType.INTERVAL)
        if (index > 0) {
            TimeManager.instance._handles[index].remove = true
        }
        let data = {
            type: TimeType.INTERVAL,
            name: name,
            callback: callback,
            target: target,
            interval: interval,//间隔
            totalCount: count ? count : 0,//interval执行总次数
            curCount: 0,//当前执行了多少次
            progress: 0,//每执行一次 进度每秒加1
            remove: false,
            step: (nowTime) => {
                data.progress++
                if (data.progress >= data.interval) {//可以执行一次了
                    data.progress = 0
                    data.curCount++
                    let callback: Function = data.callback
                    callback.call(data.target, data.curCount)
                    if (data.curCount >= data.totalCount && data.totalCount) {//所有interval已经完成
                        data.remove = true
                    }
                }
            }
        }
        TimeManager.instance._handles.push(data)
    }

    //倒计时 name 为key值 间隔interval秒后调用callback
    countDown(name: string, callback: Function, target: any, interval: number) {
        let now = this.getServerTime()
        let index = this.hasHander(name, callback)
        if (index > 0) {
            TimeManager.instance._handles[index].remove = true
        }
        let data = {
            type: TimeType.COUNT_DOWN,
            name: name,
            callback: callback,
            target: target,
            targetTime: now + interval,
            remove: false,
            step: (nowTime) => {
                if (data.targetTime <= nowTime && data.remove == false && data.target) {
                    let callback: Function = data.callback
                    callback.call(data.target)
                    data.remove = true
                }
            }
        }
        TimeManager.instance._handles.push(data)
    }

    //倒计时 name 为key值  到targetTime时调用callback
    countDownTargetTime(name: string, callback: Function, target: any, targetTime: number) {
        let index = this.hasHander(name, callback)
        if (index >= 0) {
            TimeManager.instance._handles[index].remove = true
        }
        let data = {
            type: TimeType.COUNT_DOWN,
            name: name,
            callback: callback,
            target: target,
            targetTime: targetTime,
            remove: false,
            step: (nowTime) => {
                if (data.targetTime <= nowTime && data.remove == false && data.target) {
                    let callback: Function = data.callback
                    callback.call(data.target)
                    data.remove = true
                }
            }
        }
        TimeManager.instance._handles.push(data)
    }

    offAllByKey(name: string) {
        let length = TimeManager.instance._handles.length
        for (let i = 0; i < length; i++) {
            let data = TimeManager.instance._handles[i]
            if (data.name == name) {
                TimeManager.instance._handles[i].remove = true
            }
        }
    }

    offCountDown(name: string, callback: Function) {
        this.offHander(name, callback, TimeType.COUNT_DOWN)
    }

    offInterval(name: string, callback: Function) {
        this.offHander(name, callback, TimeType.INTERVAL)
    }

    private offHander(name: string, callback: Function, type = TimeType.COUNT_DOWN) {
        let index = this.hasHander(name, callback, type)
        if (index >= 0) {
            TimeManager.instance._handles[index].remove = true
        }
    }

    private hasHander(name: string, callback: Function, type = TimeType.COUNT_DOWN) {
        let length = TimeManager.instance._handles.length
        for (let i = 0; i < length; i++) {
            let data = TimeManager.instance._handles[i]
            if (data.type == type && data.callback == callback && data.name == name) {
                return i
            }
        }
        return -1
    }

    //初始化服务器时间
    initServerTime(serverTime: number) {
        this._serverMarkTime = serverTime
        this._serverLocalDifTime = Math.floor(new Date().getTime() / 1000) - serverTime
    }

    //没有初始化得到的是本地时间
    getServerTime() {
        let nowTime = Math.floor(new Date().getTime() / 1000)
        if (this._serverMarkTime <= 0) {
            return nowTime
        }
        return nowTime - this._serverLocalDifTime
    }

    //TimeManager每秒执行一次 所有TimeManager中的方法都是以秒为精确的 可能有1秒内误差
    private step() {
        let nowTime = this.getServerTime()
        for (let i = 0; i < TimeManager.instance._handles.length; i++) {
            let data = TimeManager.instance._handles[i]
            if (!data.remove && data.target) {
                data.step(nowTime)
            } else {
                TimeManager.instance._handles.splice(i, 1)
                i--
            }
        }
    }


    update(dt) {//切到后台不执行和cc.schedule一样
        TimeManager.instance.AppRunningTime += dt
        let now = Date.now()
        if (this._lastTime == 0 || this._lastTime + 1000 < now) {
            this._lastTime = now
            this.step()
            this.checkBlack()
        }
    }

    //检测黑屏
    startCheckBlack() {
        TimeManager.instance._startCheckBlack = true
    }

    checkBlack() {
        if (!TimeManager.instance._startCheckBlack) {
            return
        }

        for (let i = 0; i < TimeManager.instance._needCheckModule.length; i++) {
            let isActive = ModuleManager.instance.moduleHasActive(TimeManager.instance._needCheckModule[i])
            if (isActive) {
                TimeManager.instance._blackTimes = 0
                return
            }
        }

        TimeManager.instance._blackTimes = TimeManager.instance._blackTimes + 1
        if (TimeManager.instance._blackTimes > 3) {
            TimeManager.instance._blackTimes = 0
            ModuleManager.instance.setModuleActive("HallModule")
            cc.error("出现了黑屏")
            LogReportManager.report("出现了黑屏")
        }
    }


    private genId() {
        return ++this.m_maxId
    }

    doSchedule(callback: Function, interval?: number, repeat?: number, delay?: number) {
        let self = this
        let id = this.genId()
        let tmpFun = function () {
            if (!repeat) {
                self.cancelSchedule(id)
            }
            callback()
        }
        this.m_callbackMap[Number(id)] = tmpFun
        this.schedule(tmpFun, interval, repeat, delay)
        return id
    }

    doScheduleOnce(callback: Function, delay: number): number {
        let self = this
        let id = this.genId()
        let tmpFun = function () {
            this.m_callbackMap[id.toString()] = null
            callback()
        }
        this.m_callbackMap[Number(id)] = tmpFun
        this.scheduleOnce(tmpFun, delay)
        return id
    }

    cancelSchedule(id: number) {
        let key = id.toString()
        let callback = this.m_callbackMap[key]
        if (callback) {
            this.unschedule(callback)
            this.m_callbackMap[key] = null
        }
    }

    cleanDoShedules() {
        let object = this.m_callbackMap
        let list = []
        for (const key in object) {
            if (object.hasOwnProperty(key)) {
                list.push(key)
            }
        }
        for (let i = 0; i < list.length; i++) {
            const key = list[i];
            this.cancelSchedule(Number(key))
        }
        this.m_callbackMap = {}
    }
}