//默认导入
const { ccclass, property } = cc._decorator;
import ModuleBase, { ViewLayer, moduleInit } from "../../../../framework/core/mvvm/module_base";
import DialogTipsView from "./dialog_tips_view";

@ccclass
@moduleInit("dialogtips")
export default class DialogTips extends ModuleBase<DialogTipsView, null> {

    enterCallback: Function
    closeCallback: Function
    constructor() {
        super();
        this.package = "public";
        this.layer = ViewLayer.High;
        this.windowPrefabResPath = "package/public/dialog_tips";
        this.viewType = DialogTipsView;
        this.needAnim = true
        this.needViewMask = true
    }

    onShow(intentData) {
        if (intentData.Content != null){
            this.view.showTips(intentData.Content)
        }
        if (intentData.EnterCallBack != null){
            this.enterCallback = intentData.EnterCallBack
        }

        if (intentData.CloseCallBack != null){
            this.closeCallback = intentData.EnterCallBack
        }
    }

    protected onButtonClickName(name : string) {
        switch (name) {
            case "CloseBtn":
                this.hide()
                if (this.closeCallback != null) { this.closeCallback() }
                break
            case "EnterBtn":
                this.hide()
                if (this.enterCallback != null) { this.enterCallback() }
                break
            default:
                break
        }
    }


    onHide() {
        super.onHide()
    }

    onDisable() {
        this.unscheduleAllCallbacks()
    }

    onDestroy() {
        this.unscheduleAllCallbacks()
    }
}