import { PokerNode, IPokerNodeData } from "./poker_node";

const { ccclass, property } = cc._decorator;

@ccclass
export class PokerNodeSet extends cc.Component {
    @property([PokerNode])
    pokerNodes:PokerNode[] = [];

    playerCardAnim(hands:cc.Node[],number,toCount:number){
        for (let idx = 0; idx < toCount; idx++) {
            let hand = hands[idx];
            let tempTransform = this.getTransFromData(toCount,idx);
            let targetPos;
            hand.parent.convertToNodeSpaceAR(tempTransform.worldPos,targetPos);
            this.scheduleOnce(()=>{
                this.playAnim(hand,targetPos,tempTransform.scale,tempTransform.angle,0.2,0,null,cc.easeCubicActionInOut());
            },0.1*idx);
        }
    }

    getTransFromData(showCount:number,index:number):IPokerNodeData{
        let pokernode = this.pokerNodes[showCount];
        if (pokernode) {
            return pokernode.transformDataList[index];
        }
        return null;
    }

    private playAnim(node: cc.Node, targetPos: cc.Vec2, targetScale: number, targetAngle: number, duration: number, delay?: number, onFinish?: Function, easingType?){
        let self = this
        let do_fun = function(){
            let moveAnim = cc.moveTo(duration, targetPos)           
            let rotaAnim = cc.rotateTo(duration, -targetAngle)
            let scaleAnim = cc.scaleTo(duration, targetScale)
            if(easingType){
                moveAnim = moveAnim.easing(easingType)
                rotaAnim = rotaAnim.easing(easingType)
                scaleAnim = scaleAnim.easing(easingType)
            }
            let tmpFun = function(){
                onFinish && onFinish()
            }.bind(self)
            let callFun = cc.callFunc(tmpFun)   
            let swap = cc.spawn([moveAnim, rotaAnim, scaleAnim])
            var seq = cc.sequence([swap, callFun])
            node.stopAllActions()
            node.runAction(seq)
        }
        if(delay && delay > 0){
            self.scheduleOnce(function(){
                do_fun()
            }, delay)
        }else{
            do_fun()
        }
    }
}