import ModuleManager from "../framework/core/module_manager";

import EventManager from "./event_manager";
// import { Polyglot } from "../lib/polyglot";
import GameUtil from '../utility/game_util';



export default class LanguageManager {

    private static _textChineseData

    private static _curLang = "zh-cn"

    private static _polyInst

    static init (language) {
        if (language === LanguageManager._curLang) {
            return;
        }
        LanguageManager._curLang = language
        LanguageManager.loadLanguageData(language)
    }

    static change() {
        let ch = LanguageManager._curLang == "zh-cn" ? "en" : "zh-cn"
        this.init(ch)
    }

    static loadLanguageData (language) {
        GameUtil.loadRes("i18n/" + language, (error: Error, asset: cc.JsonAsset) => {
            if (error) {
                cc.log(error)
                return
            }
            LanguageManager._polyInst = asset.json;
        })
    }


    
    static t (key : string, opt ?: any) {
        // if (LanguageManager._polyInst) {
        //     return LanguageManager._polyInst.t(key, opt);
        // }
        return LanguageManager._polyInst[key] || ""
    }
}