import AudioManager from "../../../../manager/audio_manager"
import Utility from "../../../../utility/Utility"
import { CardPattern, PatternType } from "../table/poker/poker_pattern"
import { Card, CardNameIndex } from "../table/poker/poker_data"

const const_malePath: string = 'package/pdk/sound/man/'
const const_femalePath: string = 'package/pdk/sound/woman/'
const const_effectPath: string = 'package/pdk/sound/effect/'
const const_musicPath: string = 'sound/bgm/'


export  class PdkSound{
    static playPokerTypeSound(male:boolean, pattern?: CardPattern, onFinish?: Function){

        let path = const_femalePath
        if(male){
            path = const_malePath
        }
        let type = pattern.type
        let disp_type = pattern.disp_type


        let soundName = ''
        switch (type) {
            case PatternType.type_danpai:
                soundName = 'single_'
                break;
            case PatternType.type_shunzi:
                soundName = 'shunzi'
                this.playSound(path + soundName, soundName, onFinish)
                return
            case PatternType.type_duizi:
                soundName = 'dui'
                break;
            case PatternType.type_liandui:
                soundName = 'liandui'
                this.playSound(path + soundName, soundName, onFinish)
                return
            case PatternType.type_sandaiyi:
                soundName = 'tuple'
                if(disp_type == 'sanzhang'){
                    soundName = 'tuple'
                }else if(disp_type == 'sandaiyi'){
                    soundName = 'sandaiyi'
                    this.playSound(path + soundName, soundName, onFinish)
                    return
                }else if(disp_type == 'sandaier'){
                    soundName = 'sandaiyidui'
                    this.playSound(path + soundName, soundName, onFinish)
                    return
                }else{
                    return
                }
                break;
            case PatternType.type_feiji:
                soundName = 'feiji'
                if(pattern.feijinum > 0){
                    if(Math.floor(pattern.card_cnt / pattern.feijinum) > 0){
                        soundName = 'feijidaichibang'
                    }
                }
                
                this.playSound(path + soundName, soundName, onFinish)
                return
            case PatternType.type_zhadan:
                soundName = 'zhadan'
                this.playSound(path + soundName, soundName, onFinish)
                return         
            case PatternType.type_sidaier:
                soundName = 'sidaier'
                if(disp_type == 'sidaisi'){
                    soundName = 'sidailiangdui'
                }
                this.playSound(path + soundName, soundName, onFinish)
                return              
            case PatternType.type_huojian:
                soundName = 'wangzha'
                this.playSound(path + soundName, soundName, onFinish)
                return                
            default:
                break;
        }

        let name = Card.valueToIndex(pattern.value)
        if(!name || name == CardNameIndex.card_unknow){
            onFinish && onFinish('unknow pattern')
            return
        }
        soundName = soundName + name
        this.playSound(path + soundName, soundName, onFinish)
    }

    static playPokerTypeEffectSound(pattern: CardPattern, onFinish?: Function){
        let type = pattern.type
        if(type == PatternType.type_shunzi){
            this.playShunZiEffectSound(onFinish)
            return
        }
        if(type == PatternType.type_liandui){
            this.playCommonEffectSound(onFinish)
            return
        }
        if(type == PatternType.type_feiji){
            this.playFeiJiEffectSound(onFinish)
            return
        }
        if(type == PatternType.type_zhadan){
            this.playZhaDanEffectSound(onFinish)
            return
        }
        if(type == PatternType.type_huojian){
            this.playHuoJianEffectSound(onFinish)
            return
        }
        if(type != PatternType.type_unknow){
            // this.playPiaEffectSound(onFinish)
        }
    }

    static playPiaEffectSound(onFinish?: Function){
        let soundName = 'special_give'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playPassSound(male:boolean, onFinish?: Function){
        let path = const_femalePath + 'action/'
        if(male){
            path = const_malePath + 'action/'
        }
        let list = ['buyao1', 'buyao2', 'buyao3', 'buyao4']
        let randomIndex = this.getRandomIndex(0, list.length - 1)       
        let soundName = list[randomIndex]
        PdkSound.playSound(path + soundName, soundName, onFinish)
    }

    static playDaNiSound(male: boolean, onFinish?: Function){
        let path = const_femalePath + 'action/'
        if(male){
            path = const_malePath + 'action/'
        }
        let list = ['dani1', 'dani2', 'dani3']
        let soundName = list[this.getRandomIndex(0, list.length - 1)]
        PdkSound.playSound(path + soundName, soundName, onFinish)
    }

    static playFaPaiSound(onFinish?: Function){
        let soundName = 'fapai'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playWarningSound(male: boolean, single: boolean, onFinish?: Function){
        let path = const_femalePath + 'action/'
        if(male){
            path = const_malePath + 'action/'
        }
        let list = ['baojing1', 'baojing1_1']
        if(!single){
            list = ['baojing2', 'baojing2_1']
        }
        let soundName = list[this.getRandomIndex(0, list.length - 1)]
        PdkSound.playSound(path + soundName, soundName, onFinish)
    }

    static playMingPaiSound(male: boolean, onFinish?: Function){
        let path = const_femalePath + 'action/'
        if(male){
            path = const_malePath + 'action/'
        }
        let soundName = 'share'
        PdkSound.playSound(path + soundName, soundName, onFinish)
    }

    static playGrabLordSound(male: boolean, score: number, onFinish?: Function){
        let path = const_femalePath + 'action/'
        if(male){
            path = const_malePath + 'action/'
        }
        let soundName = 'score_order_'
        if(score == 0){
            soundName = 'score_no_order'
        }else{
            soundName = soundName + score
        }
        PdkSound.playSound(path + soundName, soundName, onFinish)
    }

    static playSetLordEffectSound(onFinish?: Function){
        let soundName = 'dztxbh'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playRemind(onFinish?: Function){
        let soundName = 'remind'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playFeiJiEffectSound(onFinish?: Function){
        let soundName = 'feiji'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playShunZiEffectSound(onFinish?: Function){
        this.playCommonEffectSound(onFinish)
    }

    static playZhaDanEffectSound(onFinish?: Function){
        let soundName = 'bomb_new'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playHuoJianEffectSound(onFinish?: Function){
        let soundName = 'bomb'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playCommonEffectSound(onFinish?: Function){
        let soundName = 'common'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playGameLoseSound(onFinish?: Function){
        let soundName = 'gamelose'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playGameWinSound(onFinish?: Function){
        let soundName = 'gamewin'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playMathWinSound(onFinish?: Function){
        let soundName = 'gamewin'
        PdkSound.playSound(const_effectPath + soundName, soundName, onFinish)
    }

    static playSound(fullPath:string, soundName: string, onFinish: Function){
        AudioManager.instance.playSound(fullPath, false, onFinish)
    }

    static playMusic(fullPath: string, soundName: string){
        AudioManager.instance.playMusic(fullPath, false)
    }

    static playBgm(resum: boolean){
        // let list = ['bg_zhandou1']
        // let soundName = list[this.getRandomIndex(0, list.length - 1)]
        // this.playMusic(const_musicPath + soundName, soundName)
        let list = [AudioManager.instance.playFightMusic.bind(AudioManager.instance), AudioManager.instance.playFightMusic2.bind(AudioManager.instance)]
        let play = list[ PdkSound.getRandomIndex(0, list.length - 1)]
        if(resum){            
            play(true)
        }else{
            let tmp = list[AudioManager.instance._curMusicIndex - 2]
            if(tmp){
                tmp(false)
            }else{
                play(true)
            }
        }
    }

    static stopBgm(){
        AudioManager.instance.stopAllMusic()
    }

    static playChatShortMsgSound(male: boolean, key: string, onFinish?: Function){
        let path = const_femalePath + 'chat/'
        if(male){
            path = const_malePath + 'chat/'
        }
        let soundName = 'chat_' + key
        PdkSound.playSound(path + soundName, soundName, onFinish)
    }

    static getRandomIndex(under: number = 0, over: number = 0){
        return Utility.instance.getIntRandom(under, over)
    }
}