import Module, { bindButtonClickEvent, moduleInit } from "../../../../framework/core/mvvm/module_base";
import AuthView from "./auth_view";
import ModuleManager from "../../../../framework/core/module_manager";
import Utility from "../../../../utility/Utility";
import LoaclStorage from "../../../../manager/local_storage";
import AppData from "../../../public/const/app_data";
import AudioManager from "../../../../manager/audio_manager";
import LanguageManager from '../../../../manager/language_manager';
import Debug from "../../../../manager/debug";
import LoginServer from "../../../server/loginserver/login_server";


const { ccclass, menu } = cc._decorator;

@ccclass
@menu("package/hall/auth/auth")
@moduleInit("AuthModule")
export default class AuthModule extends Module<AuthView, null> {

    //是否获取到了远程的服务器数据
    _getServerVersionData: boolean
    _startBtn = null
    _assets = null
    _loadCount = 0
    _httpRequestCount = 0

    constructor() {
        super();
        this.windowPrefabResPath = "package/hall/module/auth/AuthModule";
        this.viewType = AuthView;
        this.modelType = null;
    }

    onInit() {
        AudioManager.instance.playMusicLoop()
        if (LoaclStorage.getNumber("loginUserID") != null && LoaclStorage.getNumber("loginUserID") != "null" ) {
            this.view.editBoxAccount.string = LoaclStorage.getNumber("loginUserID");
        } else {
            this.view.editBoxAccount.string = (Math.floor(Math.random()* 900) + 100).toString()
        }
    }

    @bindButtonClickEvent("ButtonChangeLanguage")
    changeLanguage() {
        LanguageManager.change()
        this.view.changeLanguage()
        ModuleManager.instance.destroyAllModule()
        ModuleManager.instance.showModule(AuthModule)
    }

    @bindButtonClickEvent("ButtonTest")
    test() {

    }
    
    @bindButtonClickEvent("Button_Login")
    onLoginBtnClick() {        
        let account = parseInt(this.view.editBoxAccount.string)
        if (account) {
            Utility.instance.playButtonSound()
            ModuleManager.instance.showModuleName("NetPrompt", { showImmediately: true })
            LoginServer.instance.accountLogin(this.view.editBoxAccount.string);
            LoaclStorage.setNumber("loginUserID", account)
        } else {
            ModuleManager.instance.toast("请输入正确的测试账号")
        }
    }

    onShow() {
        this._loadCount = 0
        this._httpRequestCount = 0
        ModuleManager.instance.preLoadRes()
    }
}




