import ClubMemListView from "./clubMemList_view";
import Module, { moduleInit } from "../../../framework/core/mvvm/module_base";
import { ClubMemListModel } from "./clubMemList_model";
import ModuleManager from "../../../framework/core/module_manager";
import Toast from "../../public/module/toast/toast";
import HallServer from "../../server/hallserver/hall_server";
import { ClubRoute } from "../net_clubRoute";

const {ccclass, property} = cc._decorator;

@ccclass
@moduleInit("ClubMemListModule")
export default class ClubMemListModule extends Module<ClubMemListView,ClubMemListModel> {
    constructor(){
        super()
        this.windowPrefabResPath = 'package/club/clubMemList';
        this.viewType = ClubMemListView;
        this.modelType = ClubMemListModel;
        this.needAnim = true;
        this.clickAnywhereClose = false;
        this.needViewMask = true;
    }

    onLoad(){
    }

    onShow(clubInfo){
        this.model.netClient = HallServer.instance.netClient;
        this.model.clearMsgEvent();
        this.registerMsg();
        this.model.clubId = clubInfo.clubId;
        this.model.curPageNum = 1;
        this.model.send_getClubMember(this.model.clubId, this.model.curPageNum, this.model.pageCeilNum, false);
    }

    registerMsg() {
        this.model.registerNetMsg(ClubRoute.getClubMember, this.retGetClubMember.bind(this));
    }

    protected onButtonClick(node: cc.Node, customEventData){
        let pName = node.name;
        switch (pName) {
            case 'Button_close':
                ModuleManager.instance.destroyModule('ClubMemListModule');
                break;
            case 'Button_add':
                ModuleManager.instance.showModule(Toast, '暂未开放!');
                break;
            case 'Button_find':
                ModuleManager.instance.showModule(Toast, '请输入正确的用户ID!');
                break;
            case 'Button_leftPage':
                if (this.model.curPageNum > 1) {
                    this.model.curPageNum = this.model.curPageNum - 1;
                    this.model.send_getClubMember(this.model.clubId, this.model.curPageNum, this.model.pageCeilNum, false);
                }
                break;
            case 'Button_rightPage':
                if (this.model.curPageNum < this.model.totalPageNum) {
                    this.model.curPageNum = this.model.curPageNum + 1;
                    this.model.send_getClubMember(this.model.clubId, this.model.curPageNum, this.model.pageCeilNum, false);
                }
                break;
            default:
                break;
        }
    }

    retGetClubMember(data:any) {
        if (data.code == 0) {
            let totalNum = Math.ceil(data.totalNum / this.model.pageCeilNum);
            this.model.totalPageNum = totalNum;
            this.view.loadMemList(data.members, this.model.curPageNum, totalNum);
        } else {
            ModuleManager.instance.showModule(Toast,'获取成员列表错误code=' + data.code);
        }
    }
}
