import ModelBase from "../../../../framework/core/mvvm/model_base";
import { PdkNetRoute } from "../../net/pdk_route";
import UserDataManager from "../../../../manager/user_manager";
import { PdkData } from "./data/pdk_data";
import { PdkUtils } from "../help/PdkUtils";
import { PokerSet } from "./poker/poker_logic_set";
import { CardPattern } from "./poker/poker_pattern";

const {ccclass, property} = cc._decorator;

@ccclass
export class PdkModel extends ModelBase {
    constructor(){
        super();
    }

    users:PdkData.SeatInfoData[] = [];

    mySeatInfo:PdkData.SeatInfoData = null;

    mChair:number;

    mCommonData:PdkData.ICommonData = null;

    //初始化牌局
    initModel(msg:any){
        this.users = [];
        if (!this.roomInfo) {
            this.roomInfo = new PdkData.RoomInfoData();
        }
        if (!this.mCommonData) {
            this.mCommonData = {};
        }
        this.roomInfo.gameState = msg.gameStatus;
        //设置mChairID
        for (let i = 0; i < msg.players.length; i++) {
            let player = msg.players[i];
            if (this.isRoleById(player.id)) {
                this.mChair = player.chairID;
                break;
            }
        }
        for (let idx = 0; idx < msg.players.length; idx++) {
            let player = msg.players[idx];
            let _user = this.enterUser(player);
            if (this.isRoleById(_user.user.id)) {
                this.mySeatInfo = _user;
                this.mySeatInfo.handSet = new PokerSet();
            }
        }
    }

    setPokerList(handcard:number[],len?:number){
        if (this.mySeatInfo) {
            this.mySeatInfo.handSet.setCodeList(handcard,len);
        }
    }

    isRoleById(id:number):boolean{
        return  id == Number(UserDataManager.instance.getUserData().uid)
    }

    getUser(id:number):PdkData.SeatInfoData{
        for (let idx = 0; idx < this.users.length; idx++) {
                let user = this.users[idx];
                if (user.user.id == id) {
                    return user;
                }
        }
        return null;
    }

    getUserLocalID(chairid:number){
        for (let idx = 0; idx < this.users.length; idx++) {
            let user = this.users[idx];
            if (user.user.chairID == chairid) {
                return user;
            }
        }
        return null;
    }

    //进入
    enterUser(msg:any):PdkData.SeatInfoData{
        let _user = this.getUser(msg.id)
        if (!_user) {
            _user = new PdkData.SeatInfoData();
            this.users.push(_user);
        }
        _user.user = msg;
        _user.viewID = this.viewLocation(_user.user.chairID);
        return _user;
    }
    
    //退出
    exitUser(id:number){
        for (let idx = 0; idx < this.users.length; idx++) {
            let user = this.users[idx];
            if (user.user.id == id) {
                this.users.splice(idx,1);
                break;
            }
        }
    }

    isRole(chairid:number){
        return chairid == this.mChair;
    }

    isMyOutCard(){
        return this.mCommonData.chairID == this.mChair;
    }

    viewLocation(chairid:number){
        let wPlayerCount = this.roomInfo.playerCount;
        let meChairID = this.mChair;
        let viewID = (chairid + wPlayerCount - meChairID)%wPlayerCount
        return viewID;
    }

    roomInfo:PdkData.RoomInfoData = null;

    //进入游戏或者重连执行
    initRoomInfo(msg:any){
        if (!this.roomInfo) {
            this.roomInfo = new PdkData.RoomInfoData();
        }
        this.roomInfo.playerCount = msg.gameParameter.bPlayerCount;
    }

    send_readyGame(){
        this.send(PdkNetRoute.readyGame,{});
    }

    send_playCard(bCardData:number[],count:number){
        let data = {
            bCardData:bCardData,
            bCardCount:count,
        }
        this.send(PdkNetRoute.playCard,data);
    }

    send_dissolveRoom(msg:any){
        this.send(PdkNetRoute.dissolveGame,msg);
    }

    send_leaveRoom(){
        this.send(PdkNetRoute.leaveRoom,{});
    }
}
