import Debug from "./debug";
import EventManager from "./event_manager";
import AppData from "../pacakge/public/const/app_data";
import LoaclStorage from "./local_storage";


const { ccclass, property } = cc._decorator;


@ccclass
export default class VersionManager extends cc.Component{

    public static instance : VersionManager;

    private storagePath : string ;
    private assetManager : any;

    private updating: boolean;
    private canRetry: boolean;

    private progress : number;

    @property(cc.Object)
    public manifestUrl : cc.Asset = null;
        
    onLoad() {               
        VersionManager.instance = this;   
    
    }

    public checkVersion(){

        // Hot update is only available in Native build
        if (!cc.sys.isNative) {
            return;
        }                

        if (!this.manifestUrl) {
            Debug.error(" the manifestUrl is null ");
            return;
        }

        this.storagePath = ((jsb.fileUtils ? jsb.fileUtils.getWritablePath() : '/') + 'remote-asset-ssqp');
        cc.log('Storage path for remote asset : ' + this.storagePath);
        
        let self = this;

        // Init with empty manifest url for testing custom manifest
        this.assetManager = new jsb.AssetsManager('', this.storagePath, this.versionCompareHandle.bind(this));
        
        // Setup the verification callback, but we don't have md5 check function yet, so only print some message
        // Return true if the verification passed, otherwise return false
        this.assetManager.setVerifyCallback(function (path, asset) {
            // When asset is compressed, we don't need to check its md5, because zip file have been deleted.
            var compressed = asset.compressed;
            // Retrieve the correct md5 value.
            var expectedMD5 = asset.md5;
            // asset.path is relative path and path is absolute.
            var relativePath = asset.path;
            // The size of asset file, but this value could be absent.
            var size = asset.size;

            if (compressed) {
                //self.panel.info.string = "Verification passed : " + relativePath;
                return true;
            }
            else {
                //self.panel.info.string = "Verification passed : " + relativePath + ' (' + expectedMD5 + ')';
                return true;
            }
        });

        //this.panel.info.string = 'Hot update is ready, please check or directly update.';

        if (cc.sys.os === cc.sys.OS_ANDROID) {
            // Some Android device may slow down the download process when concurrent tasks is too much.
            // The value may not be accurate, please do more test and find what's most suitable for your game.
            this.assetManager.setMaxConcurrentTask(2);
            //this.panel.info.string = "Max concurrent tasks count have been limited to 2";
        }
        
        //this.panel.fileProgress.progress = 0;
        //this.panel.byteProgress.progress = 0;

        this.checkUpdate();

    }

    private versionCompare(versionA , versionB ) : number{

        var vA = versionA.split('.');
        var vB = versionB.split('.');   

        for (var i = 0; i < vA.length; ++i) {
            var a = parseInt(vA[i]);
            var b = parseInt(vB[i] || 0);
            if (a == b) {
                continue;
            }
            else {       
                return a - b;
            }
        }
        
        if (vB.length > vA.length) {
            return -1;
        }
        else {
            return 0;
        } 
    }

    // Setup your own version compare handler, localVer and remoteVer is versions in string
    // if the return value greater than 0, localVer is greater than remoteVer,
    // if the return value equals 0, localVer equals to remoteVer,
    // if the return value smaller than 0, localVer is smaller than remoteVer.
    private versionCompareHandle (localVer, remoteVer) {

        cc.log("JS Custom Version Compare: local version is " + localVer + ', remote version is ' + remoteVer);
        var vA = localVer.split('.');
        var vB = remoteVer.split('.');       
        
        // let storageVer  = LoaclStorage.getNumber("version");
        // if (storageVer) {
            
        //     if(this.versionCompare(localVer,storageVer) > 0 ){
        //         jsb.fileUtils.removeDirectory(this.storagePath);
        //         console.log(" remove storage dic ");
        //     }
        // }

        AppData.appVersion = localVer;     
        LoaclStorage.setNumber("version",localVer);
        
        EventManager.instance.dispatch("get_app_version",localVer);
        
        for (var i = 0; i < vA.length; ++i) {
            var a = parseInt(vA[i]);
            var b = parseInt(vB[i] || 0);
            if (a == b) {
                continue;
            }
            else {
                //大版本检测
                if (i == 0 && a < b) {
                    
                    console.log("please down load the lastest app package .");                                                                  
                    //self.panel.info.string = "please down load the lastest app package .";
                    return 0;
                }
                return a - b;
            }
        }
        
        if (vB.length > vA.length) {
            return -1;
        }
        else {
            return 0;
        } 
    }


    public checkUpdate() {
        //console.log(" checkUpdate  ");
        if (this.updating) {
            //this.panel.info.string = 'Checking or updating ...';
            return;
        }
        if (this.assetManager.getState() === jsb.AssetsManager.State.UNINITED) {
            // Resolve md5 url
            var url = this.manifestUrl.nativeUrl;        
            if (cc.loader.md5Pipe) {
                url = cc.loader.md5Pipe.transformURL(url);
            }
            
            this.assetManager.loadLocalManifest(url);
        }
        if (!this.assetManager.getLocalManifest() || !this.assetManager.getLocalManifest().isLoaded()) {
            //this.panel.info.string = 'Failed to load local manifest ...';
            return;
        }
        this.assetManager.setEventCallback(this.checkCb.bind(this));

        this.assetManager.checkUpdate();
        this.updating = true;
    }

    private checkCb (event) {

        let code = event.getEventCode();

        cc.log('checkCb Code: ' + code);

        this.updating = false;        
        
        switch (code)
        {
            case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST:
                cc.log("No local manifest file found, hot update skipped.");
                EventManager.instance.dispatch("error_no_local_manifest.");
                //this.panel.info.string = "No local manifest file found, hot update skipped.";
                break;
            case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST:
            case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST:
                cc.log("Fail to download manifest file, hot update skipped.");
                EventManager.instance.dispatch("error_download_manifest_file.");
                //this.panel.info.string = "Fail to download manifest file, hot update skipped.";
                break;
            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE:            
                cc.log("Already up to date with the latest remote version.");                
                EventManager.instance.dispatch("already_up_to_date");
                //this.panel.info.string = "Already up to date with the latest remote version.";
                break;
            case jsb.EventAssetsManager.NEW_VERSION_FOUND:
                // this.panel.info.string = 'New version found, please try to update.';
                // this.panel.checkBtn.active = false;
                // this.panel.fileProgress.progress = 0;
                // this.panel.byteProgress.progress = 0;
                
                EventManager.instance.dispatch("new_version_found");
                cc.log("New version found, do update.");                
                this.scheduleOnce(this.hotUpdate,0.01);
                break;
            default:
                cc.log(" check cb default");
                return;
        }               
        
        this.assetManager.setEventCallback(null);


    }

    public hotUpdate () {

        //console.log(" hotUpdate " , jsb.AssetsManager.State.UNINITED);
        if (this.assetManager && !this.updating) {
            this.assetManager.setEventCallback(this.updateCb.bind(this));

            if (this.assetManager.getState() === jsb.AssetsManager.State.UNINITED) {
                // Resolve md5 url
                var url = this.manifestUrl.nativeUrl;
                if (cc.loader.md5Pipe) {
                    url = cc.loader.md5Pipe.transformURL(url);
                }
                this.assetManager.loadLocalManifest(url);
            }
            //this._failCount = 0;
            this.assetManager.update();
            //this.panel.updateBtn.active = false;
            this.updating = true;
        }
    }

    public updateCb (event) {
        var needRestart = false;
        var failed = false;

        console.log(" updateCb ",event.getEventCode());

        switch (event.getEventCode())
        {
            case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST:
                //this.panel.info.string = 'No local manifest file found, hot update skipped.';
                console.log("No local manifest file found, hot update skipped.");
                failed = true;
                break;
            case jsb.EventAssetsManager.UPDATE_PROGRESSION:
                //this.panel.byteProgress.progress = event.getPercent();
                //this.panel.fileProgress.progress = event.getPercentByFile();         
                var msg = event.getMessage();
                if (msg) {
                    //this.panel.info.string = 'Updated file: ' + msg;      
                    console.log(" updateProgress msg " , msg);              
                }

                let downFiles = Number(event.getDownloadedFiles() );
                let totalFiles = Number(event.getTotalFiles());

                let downBytes = Number(event.getDownloadedBytes());
                let totalBytes = Number(event.getTotalBytes());         
                
                console.log(" update progress files : ",downFiles + " / " + totalFiles);
                console.log("update progress bytes : ",downBytes + ' / ' + totalBytes);                
                
                let progress = totalBytes == 0 ?  0 :  Number((downBytes / totalBytes).toFixed(2));
                //progress = event.getPercent();

                console.log(" progress ", progress);                
                if (this.progress != progress) {
                                        
                    EventManager.instance.dispatch("update_progress",progress);
                }

                this.progress = progress;
                
                break;
            case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST:
            case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST:
                //this.panel.info.string = 'Fail to download manifest file, hot update skipped.';
                console.log("Fail to download manifest file, hot update skipped.");
                failed = true;
                break;
            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE:
                //this.panel.info.string = 'Already up to date with the latest remote version.';
                console.log("Already up to date with the latest remote version.");
                failed = true;
                break;
            case jsb.EventAssetsManager.UPDATE_FINISHED:
                //this.panel.info.string = 'Update finished. ' + event.getMessage();
                console.log(" Update finished", event.getMessage());
                EventManager.instance.dispatch("update_finished");
                needRestart = true;
                break;
            case jsb.EventAssetsManager.UPDATE_FAILED:
                //this.panel.info.string = 'Update failed. ' + event.getMessage();
                //this.panel.retryBtn.active = true;
                this.updating = false;
                this.canRetry = true;
                console.log("Update failed. ",event.getMessage());
                break;
            case jsb.EventAssetsManager.ERROR_UPDATING:
                //this.panel.info.string = 'Asset update error: ' + event.getAssetId() + ', ' + event.getMessage();
                console.log("Asset update error: ",event.getAssetId()+ ", " ,event.getMessage());
                break;
            case jsb.EventAssetsManager.ERROR_DECOMPRESS:
                //this.panel.info.string = event.getMessage();
                console.log(" ERROR_DECOMPRESS ",event.getMessage());
                break;
            default:
                break;
        }

        if (failed) {
            this.assetManager.setEventCallback(null);
            //this._updateListener = null;
            this.updating = false;
        }

        if (needRestart) {
            this.assetManager.setEventCallback(null);
            //this._updateListener = null;
            // Prepend the manifest's search path
            var searchPaths = jsb.fileUtils.getSearchPaths();
            var newPaths = this.assetManager.getLocalManifest().getSearchPaths();
            console.log(JSON.stringify(newPaths));
            Array.prototype.unshift.apply(searchPaths, newPaths);
            // This value will be retrieved and appended to the default search path during game startup,
            // please refer to samples/js-tests/main.js for detailed usage.
            // !!! Re-add the search paths in main.js is very important, otherwise, new scripts won't take effect.
            cc.sys.localStorage.setItem('HotUpdateSearchPaths', JSON.stringify(searchPaths));
            jsb.fileUtils.setSearchPaths(searchPaths);

            cc.audioEngine.stopAll();
            cc.game.restart();

        }
    }

    retry () {
        if (!this.updating && this.canRetry) {
            //this.panel.retryBtn.active = false;
            this.canRetry = false;
            
            //this.panel.info.string = 'Retry failed Assets...';
            this.assetManager.downloadFailedAssets();
        }
    }


    

}

