import JoinClubView from "./joinClub_view";
import { JoinClubModel } from "./joinClub_model";
import Module, { moduleInit } from "../../framework/core/mvvm/module_base";
import ModuleManager from "../../framework/core/module_manager";
import Toast from "../public/module/toast/toast";
import { JoinClubNetRoute } from "./joinClub_route";
import HallServer from "../server/hallserver/hall_server";
import { ClubModule } from "../club/club_module";

const {ccclass, property} = cc._decorator;

@ccclass
@moduleInit("JoinClubModule")
export default class JoinClubModule extends Module<JoinClubView,JoinClubModel> {
    constructor(){
        super()
        this.windowPrefabResPath = 'package/joinClub/joinClub';
        this.viewType = JoinClubView;
        this.modelType = JoinClubModel;
        this.needAnim = true;
        this.clickAnywhereClose = false;
        this.needViewMask = true;
    }

    onLoad(){
    }

    onShow(){
        this.model.netClient = HallServer.instance.netClient;
        this.model.clearMsgEvent();
        this.registerMsg();
        this.view.emitBindFunc(this.joinClubByCode.bind(this));
    }

    registerMsg() {
        this.model.registerNetMsg(JoinClubNetRoute.joinClubByCode, this.retJoinClubByCode.bind(this));
        this.model.registerNetMsg(JoinClubNetRoute.onjoinClub, this.retOnJoinClub.bind(this));
    }

    joinClubByCode(invateCode:number) {
        this.model.send_joinClubByCode(invateCode);
    }

    retJoinClubByCode(data:any) {
        if (data.code != 0) {
            ModuleManager.instance.showModule(Toast,'亲友圈不存在');
        }
    }

    retOnJoinClub(data:any) {
        let clubId = data.clubId;
        ModuleManager.instance.showModule(ClubModule, clubId);
    }

    protected onButtonClick(node: cc.Node, customEventData){
        let pName = node.name;
        cc.log(pName)
        switch (pName) {
            case 'Button_close':
                ModuleManager.instance.destroyModule('JoinClubModule');
                break;
            case 'Button_copy':
                ModuleManager.instance.showModule(Toast,'粘贴板中暂无正确的邀请码!');
                break;
            case 'Button_0':
                this.view.inputNumber(0);
                break;
            case 'Button_1':
                this.view.inputNumber(1);
                break;
            case 'Button_2':
                this.view.inputNumber(2);
                break;
            case 'Button_3':
                this.view.inputNumber(3);
                break;
            case 'Button_4':
                this.view.inputNumber(4);
                break;
            case 'Button_5':
                this.view.inputNumber(5);
                break;
            case 'Button_6':
                this.view.inputNumber(6);
                break;
            case 'Button_7':
                this.view.inputNumber(7);
                break;
            case 'Button_8':
                this.view.inputNumber(8);
                break;
            case 'Button_9':
                this.view.inputNumber(9);
                break;
            case 'Button_10':
                this.view.resetNumber();
                break;
            case 'Button_11':
                this.view.deleteNumber();
                break;

            default:
                break;
        }
    }

}
