import ModuleManager from "../framework/core/module_manager";
import AppData from "../pacakge/public/const/app_data";
import LoginServer from "../pacakge/server/loginserver/login_server";
import HallServer from "../pacakge/server/hallserver/hall_server";
import GameServer from "../pacakge/server/gameserver/game_server";
import HallModule from "../pacakge/hall/hall_module";
import AuthModule from "../pacakge/hall/module/auth/auth_module";
import AudioManager from "./audio_manager";
import { GameCfg } from "../pacakge/public/const/game_cfg";


//场景跳转
export default class SceneManager {

    //当前进入的牌桌
    static tableModuleName = 'DouDiZhuTableModule';

    static lastEntranceOpenModuleName = ""

    static subGames:{[key:string]:string} = {};

    //初始化子游戏
    static init(){
        this.subGames['FN_PDK'] = 'PdkModule';
    }

    static gotoSubGame(data, onFinish?: () => void) {
        let gameCgf = GameCfg[AppData.gameid]
        if (!gameCgf) {
            cc.error('游戏配置不存在: ',AppData.gameid);
            return;
        }
        this.tableModuleName = gameCgf.module;
        if (!this.tableModuleName) {
            cc.error('no subGame：',this.tableModuleName);
            return;
        }
        ModuleManager.instance.removeModule(this.tableModuleName)
        ModuleManager.instance.showModuleName(this.tableModuleName, data, () => {
            ModuleManager.instance.hideAllModule([this.tableModuleName])
            ModuleManager.instance.destroyRemoveModule(this.tableModuleName)
            if (onFinish) {
                onFinish()
            }
        })
    }

    //登录
    static gotoLogin() {
        LoginServer.instance.close();
        HallServer.instance.close();
        GameServer.instance.close();
        ModuleManager.instance.showModule(AuthModule) 
    }

    //跳转大厅
    static gotoHall(isNeedCheck:boolean = true) {
        LoginServer.instance.close();
        HallServer.instance.close();
        GameServer.instance.close();        
        HallServer.instance.isNeedCheck = isNeedCheck;//需要检测
        HallServer.instance.loginHallServer();
    }

    //外部无需调用
    static showHallUI(){
        AudioManager.instance.playMusicLoop()
        ModuleManager.instance.hideAllModule(["AuthModule", this.tableModuleName])
        if (SceneManager.lastEntranceOpenModuleName != "") {
            ModuleManager.instance.showModuleName(this.lastEntranceOpenModuleName, null, () => {
                ModuleManager.instance.destroyModule(this.tableModuleName)
            })      
        } else {
            ModuleManager.instance.showModule(HallModule, null, () => {
                ModuleManager.instance.destroyModule(this.tableModuleName)
            })      
        }
    }




    //网络连接故障
}