const { ccclass, property } = cc._decorator;
// import ViewBase from "../../../../framework/core/mvvm/view_base";
// import { start } from "repl";
import CustomUIStateSwitcher from "../../../public/ui/custom_ui_state_switcher";
import EventManager from "../../../../manager/event_manager";
@ccclass
export default class MarqueeComponent extends cc.Component {

    @property(cc.Node)
    root: cc.Node = null
    @property(cc.RichText)
    labelPrefab: cc.RichText = null

    @property(cc.Mask)
    mask: cc.Mask = null

    @property(cc.Float)
    offset: number = 0

    @property(cc.Float)
    startOffset: number = 0

    @property(cc.Float)
    endOffset: number = 0

    @property(cc.Float)
    speed: number = 100
    // @property({ type: [cc.String] })
    // tag:string[] = [];
    @property(cc.String)
    tag:string="";
    protected m_availableLabelQueue: cc.RichText[] = []
    protected m_playingLabelQueue: cc.RichText[] = []
    protected m_isEmpty = false
    protected m_max_count = 10
    protected m_index: number = 0
    protected m_message_queue: any[] = []
    protected m_playing_queue: any[] = []
    onload()
    {
    }
    start()
    {
        this.m_isEmpty = true;
        this.bindEvent();

        //test
        // let str = {
        //     content:"恭喜<color=#ffff00>我</color>在<color=#00ffff>跑的都快</color><color=#00ffff>初级场</color>赢得<color=#fff100>2</color>金币",
        //     tag:'',
        //     showCount:1000,
        // }
        // this.on_msg_coming(str);
        
    }
    reset(){
        for (let index = 0; index < this.m_playingLabelQueue.length; index++) {
            const element = this.m_playingLabelQueue[index];
            element.node.active = false
            this.m_availableLabelQueue.push(element)
        }
        this.m_playingLabelQueue = []
    }

    showMarquee(show: boolean){
        this.root.active = show
    }
    play(msg){
        let self = this
        self.m_playing_queue.push(msg)
        self.showMarquee(true)
        let text = msg.content
        let message = msg;
        self.playMessage(text, self.on_next_play.bind(self), function(){
            if(self&&self.root.active){
                self.m_playing_queue.shift()
                var data = message.content
                var count = message.showCount
                if(count>1)
                {
                    message.showCount--;
                    self.play(message);
                }
            if(self.m_playing_queue.length == 0){
                self.showMarquee(false)
            }
            }            
        })
    }
    protected on_next_play(){
        let self = this      
        if(self&&self.root.active){
            let msg = self.m_message_queue.shift()
            if(msg){
                self.play(msg)
                self.m_isEmpty = false
            }else{
                self.m_isEmpty = true
            }
        }  
    }
    playMessage(text: string, next: Function, onFinish: Function){
        let self = this
        let maskWidth = self.mask.node.width
        let label = self.getAvailableLabel()      
        label.string = text
        let startPosX = maskWidth * 0.5 + self.startOffset
        label.node.position = cc.v2(startPosX, label.node.position.y)
        label.node.active = true;
        this.scheduleOnce(function(){
            let labelWidth = self.calcLabelWidth(label)
            let totalDis = labelWidth + maskWidth + self.startOffset - self.endOffset
            let dis1 = labelWidth + self.offset
            let duration1 = self.calcDuration(dis1)
            let duration2 = self.calcDuration(totalDis - dis1)    
            let targetPos1 = startPosX - dis1
            let targetPos2 = -totalDis + startPosX
            let callFun1 = cc.callFunc(function(){
                next && next()
            }.bind(self))
            let callFun2 = cc.callFunc(function(){
                label.node.active = false
                self.addToAvailableLabel(label)
                onFinish && onFinish()
            }.bind(self))
            var move1 = cc.moveTo(duration1, targetPos1, 0)
            var move2 = cc.moveTo(duration2, targetPos2, 0)
            var seq = cc.sequence([move1, callFun1, move2, callFun2]);
            label.node.runAction(seq);
        })

    }
    //消息过滤 只针对当前模块注册的对应标签
    messageFilter(msg)
    {
        // cc.log('目标标签:',msg.tag);
        // cc.log("筛选 msg.tag[1],this.tag",msg.tag[1],this.tag);
        //cc.log("555555",msg.tag);
        // cc.log("msg.tag.length:",msg.tag.length);
        return msg.tag == this.tag?true:false;
    }
    // changeStyle(styleName: string){
    //     //cc.log("styleName:"+styleName);
    //     styleName = styleName? styleName: 'hall'
    //     this.uiStateSwitcher.SwitchState(styleName)
    //     //cc.log("switchPosition(styleName)")
    //     //this.switchPosition(styleName);
    // }
    protected getAvailableLabel(): cc.RichText{
        let label = this.m_availableLabelQueue.shift()
        if(!label){
            label = this.cloneLabel()
        }
        return label
    }

    protected cloneLabel(){
        let node = cc.instantiate(this.labelPrefab.node)
        node.parent = this.labelPrefab.node.parent
        node.position = this.labelPrefab.node.position
        node.scale = this.labelPrefab.node.scale
        return node.getComponent(cc.RichText)
    }

    protected addToAvailableLabel(label: cc.RichText){
        this.m_availableLabelQueue.push(label)
    }

    protected calcLabelWidth(label: cc.RichText): number{
        return label.node.width
    }

    protected calcDuration(dis: number){
        return dis / this.speed
    }
    protected on_msg_coming(msg){
        if(this.m_isEmpty){
            this.m_isEmpty = false
            cc.log("paly__msg");
            this.play(msg)
        }else{
            this.m_index++
            msg._index = this.m_index
            cc.log("push__msg");
            this.m_message_queue.push(msg)
            this.sortQueue()    
            this.cutQueue()        
        }
    }

    protected cutQueue(){
        if(this.m_message_queue.length > this.m_max_count){
            this.m_message_queue.splice(this.m_max_count, this.m_message_queue.length - this.m_max_count)
        }
    }

    protected sortQueue(){
        this.m_message_queue.sort(function(a, b){
            if(a.level == b.level){
                return a._index - b._index
            }
            return a.level - b.level
        })
    }
    private bindEvent() {
        EventManager.instance.on('Event_Notify_Data', this.on_msg_coming, this,true)
    }

    private unbindEvent() {
        EventManager.instance.off('Event_Notify_Data', this.on_msg_coming,this)
    }

    onDisable(){
        this.unbindEvent();
    }

    //销毁
    onDestroy()
    {
        this.unbindEvent();
    }

}
