import { PConst } from "./pdk_const";
import { PokerSet } from "../poker/poker_logic_set";
import { CardPattern } from "../poker/poker_pattern";

const { ccclass, property } = cc._decorator;
export module PdkData{
    //一个座位信息
    export class SeatInfoData{
        user:IUserInfo;
        viewID:number;
        handcode?:number[];
        handSet?:PokerSet;
    }

    export interface IUserInfo{
        id:number;
        name:string;
        gender:number;
        avatarUrl:string;
        coin:number;
        gems:number;
        chairID:number;
        dissolveState:number;
        readyState:number;
    }

    export interface ICommonData{
        outCards?:number[]
        chairID?:number;
        lastPattern?:CardPattern;
    }

    //房间信息
    export class RoomInfoData{
        roomId:number;
        playerCount:number;
        gameState:PConst.GameStatus;
    }
}


@ccclass("DouDiZhuTable.SeatExtendView")
export class SeatExtendHolder{
    @property(cc.Node)
    nodePass: cc.Node = null

    @property(cc.Node)
    nodeFeiJiAnim: cc.Node = null

    @property(cc.Node)
    nodeShunZiAnim: cc.Node = null

    @property(cc.Node)
    nodeLianDuiAnim: cc.Node = null

    @property(cc.Node)
    nodeZhaDanAim: cc.Node = null

    @property(cc.Node)
    nodeWangZhaAim: cc.Node = null
    @property(cc.Node)
    nodeWangZhaAim1: cc.Node = null

    @property(cc.Node)
    nodeSanDaiYiAnim: cc.Node = null

    @property(cc.Node)
    nodeSanDaiErAnim: cc.Node = null

    @property(cc.Node)
    nodeSiDaiErAnim: cc.Node = null

    @property(cc.Node)
    nodeSiDaiLiangDuiAnim: cc.Node = null

    @property(cc.Node)
    nodeMingPaiAnim: cc.Node = null
    
}