import Debug from "./debug";

// import WXGameManager from "./wxgame_manager";


export default class LoaclStorage {

    //异步存储 微信专用 注意需要实时读取的不要用
    static setStorageNumber(key: string, data: number) {
        if (CC_WECHATGAME) {
            // WXGameManager.setStorage(key, data)
        } else {
            LoaclStorage.setNumber(key, data)
        }
    }

    static setStorageString(key: string, data: string) {
        if (CC_WECHATGAME) {
            // WXGameManager.setStorage(key, data)
        } else {
            LoaclStorage.setString(key, data)
        }
    }

    static setStorage(key: string, data: any) {
        if (CC_WECHATGAME) {
            // WXGameManager.setStorage(key, data)
        } else {
            LoaclStorage.setItem(key, data)
        }
    }

    static setNumber(key: string, data: number) {
        try {
            cc.sys.localStorage.setItem(key, data.toString());
        } catch (error) {
            Debug.warn(key, error)
        }
    }

    static getNumber(key: string, defaultValue?: number) {
        try {
            var value = cc.sys.localStorage.getItem(key);
            //这里有一个坑 key不存在时返回的是 字符串“null”
            if (value == null || value == "null") {
                if (defaultValue != null) {
                    return defaultValue;
                }
                return null;
            }
            return value;
        } catch (error) {
            Debug.warn(key, error)
        }
    }

    static setJson(key: string, data: JSON) {
        try {
            cc.sys.localStorage.setItem(key, JSON.stringify(data));
        } catch (error) {
            Debug.warn(key, error)
        }
    }

    static getJson(key: string) {
        try {
            var str = cc.sys.localStorage.getItem(key);
            if (str == null || str == "" || str == "null") {
                return null;
            }
            var data = JSON.parse(str);
            return data;
        } catch (error) {
            Debug.warn(key, error)
        }
    }

    static setString(key: string, data: String) {
        try {
            cc.sys.localStorage.setItem(key, data.toString());
        } catch (error) {
            Debug.warn(key, error)
        }
    }

    static getString(key: string, defaultValue?: string): string {
        try {
            var str = cc.sys.localStorage.getItem(key);
            if (str == null || str == ""|| str == "null") {
                if (defaultValue != null) {
                    return defaultValue;
                }
                return null;
            }
            return str;
        } catch (error) {
            Debug.warn(key, error)
        }
    }

    static setItem(key: string, data: any) {
        try {
            cc.sys.localStorage.setItem(key, data);
        } catch (error) {
            Debug.warn(key, error)
        }
    }

    static getItem(key: string): any {
        try {
            var str = cc.sys.localStorage.getItem(key);
            if (str == null || str == "null") {
                return null;
            }
            return str;
        } catch (error) {
            Debug.warn(key, error)
        }
    }

    static hasItem(key: string) {
        var str = cc.sys.localStorage.getItem(key);
        return str != null;
    }

    static delete(key: string) {
        var str = cc.sys.localStorage.setItem(key, null);
    }
}
