import ModuleBase, { ViewLayer, moduleInit, bindButtonClickEvent } from "../../../../framework/core/mvvm/module_base";
import SettingView from "./setting_view";
import ModuleManager from "../../../../framework/core/module_manager";
import LocalStorage from "../../../../manager/local_storage";
import AudioManager from "../../../../manager/audio_manager";
import Utility from "../../../../utility/Utility";
import AuthModule from "../auth/auth_module";
import AppData from "../../../public/const/app_data";
import SceneManager from "../../../../manager/scene_manager";
import { GameManager } from '../../../../manager/game_manager';

const { ccclass, property } = cc._decorator;

@ccclass
@moduleInit("Setting")
export default class Setting extends ModuleBase<SettingView, null> {

    isClick = false
    constructor() {
        super();
        this.layer = ViewLayer.High;
        this.windowPrefabResPath = "package/hall/module/setting/hall_setting";
        this.viewType = SettingView;
        this.modelType = null;
        this.needAnim = true
        this.needViewMask = true
    }

    protected onButtonClickName(name : string) {
        switch (name) {
            case "closeBtn":
                Utility.instance.playButtonSound()
                this.hide()
                break;
            default:
                break;
        }
    }
    @bindButtonClickEvent("btnCancel")
    onButtonCancel()
    {
        this.hide()
    }
    @bindButtonClickEvent("btnSure")
    onButtonSure()
    {
        //SceneManager.gotoAuth()
    }

    onInit() {
        let self = this
        this.isClick = false

        // 0 - 开  1 - 闭
        this.view.musicToggle.node.on('toggle', function (event: cc.Toggle) {
            if (this.isClick) {
                Utility.instance.playButtonSound()
            }
            let toggle = event;
            let bgNode = self.view.musicToggle.node.getChildByName("Background")
            let markNode = self.view.musicToggle.node.getChildByName("checkmark")
            if (toggle.isChecked == true) {
                bgNode.active = false
                markNode.active = true
                AudioManager.instance.setMusicOpen(true)
                AudioManager.instance.resumeMusic()
            } else {
                bgNode.active = true
                markNode.active = false
                AudioManager.instance.setMusicOpen(false)
                AudioManager.instance.pauseMusic()
            }
        }, this);

        this.view.soundToggle.node.on('toggle', function (event: cc.Toggle) {
            if (this.isClick) {
                Utility.instance.playButtonSound()
            }
            let bgNode = self.view.soundToggle.node.getChildByName("Background")
            let markNode = self.view.soundToggle.node.getChildByName("checkmark")
            let toggle = event;
            if (toggle.isChecked == true) {
                bgNode.active = false
                markNode.active = true
                AudioManager.instance.setSoundOpen(true)
            } else {
                bgNode.active = true
                markNode.active = false
                AudioManager.instance.setSoundOpen(false)
            }
        }, this);

        this.view.musicSlider.node.on("slide", function(event)
        {
            let musicValue = self.view.musicSlider.progress
            AudioManager.instance.setMusicVolume(musicValue)
            this.view.musicProgressBar.progress = musicValue;
        },this)

        this.view.soundSlider.node.on("slide", function(event)
        {
            let soundValue = self.view.soundSlider.progress
            AudioManager.instance.setSoundVolume(soundValue)
            this.view.soundProgressBar.progress = soundValue;
        },this)
    }

    onShow() {
        this.isClick = false
        if (AudioManager.instance.isMusicOpen()) {
            this.view.musicToggle.check();
        } else {
            this.view.musicToggle.uncheck();
        }

        if (AudioManager.instance.isSoundOpen()) {
            this.view.soundToggle.check();
        } else {
            this.view.soundToggle.uncheck();
        }

        this.view.soundSlider.progress = cc.audioEngine.getEffectsVolume()
        this.view.soundProgressBar.progress = cc.audioEngine.getEffectsVolume()
        this.view.musicSlider.progress = cc.audioEngine.getMusicVolume()
        this.view.musicProgressBar.progress = cc.audioEngine.getMusicVolume()
        //this.view.labVersion.string = "版本号:" + GameManager.getAppVersionBuildTime()
        this.isClick = true
    }

}
