import Module, { moduleInit } from "../../../../framework/core/mvvm/module_base";
import { ResultView } from "./result_view";
import ModuleManager from "../../../../framework/core/module_manager";



const { ccclass, menu } = cc._decorator;

@ccclass
@moduleInit("ResultModule")
export class ResultModule extends Module<ResultView, null> {

    private lineHeight = 1

    constructor() {
        super();
        this.windowPrefabResPath = "package/pdk/module/table/doudizhu_gold_onegame_result";
        this.viewType = ResultView;
        this.modelType = null;
        this.needAnim = true
        this.clickAnywhereClose = false;
        this.needViewMask = true
    }

    private func:Function;
    private data:any
    onShow(arg){
        super.onShow(arg)
        this.func = arg.func;
        this.data = arg.data;
        this.refreshUI();
    }

    private refreshUI(){
        this.view.updateUI(this.data.msg.winUser,this.data.roleid)
    }

    protected onButtonClick(node: cc.Node, customEventData){
        let pName = node.name;
        switch (pName) {
            case 'LeaveButton':
                ModuleManager.instance.destroyModule('ResultModule');
                break;
            case 'ContinuButton':
                if (this.func) {
                    this.func();
                }
                ModuleManager.instance.destroyModule('ResultModule');
                break;
            default:
                break;
        }
    }



}




