

export interface IPokerNodeData{
    localPos?:cc.Vec2;
    worldPos?:cc.Vec2;
    angle?:number;
    scale?:number;
}

const { ccclass, property } = cc._decorator;

@ccclass
export class PokerNode extends cc.Component {
    protected mTransformDataList:IPokerNodeData[] = null;

    onLoad(){
        if (!this.mTransformDataList) {
            let count = this.node.childrenCount;
            for (let idx = 0; idx < count; idx++) {
                let node = this.node.getChildByName('p'+(idx+1));
                let data:IPokerNodeData = {};
                data.localPos = node.position;
                //data.worldPos = node.parent.convertToWorldSpaceAR(node.position);
                data.angle = node.angle + node.parent.angle;
                data.scale = node.scale + node.parent.scale;
                this.mTransformDataList.push(data);
            }
        }
    }

    get transformDataList():IPokerNodeData[]{
        return this.mTransformDataList;
    }
}
