
export module Club{

    //一个俱乐部
    export interface IClubData{
        clubId:number;//俱乐部id
        leaderId:number;
        clubName:string;
        leaderName:string;
        leaderUrl:string;
        announcement:string;
        isSelect:boolean;//是否选中
    }

    export enum TableType{
        PWTable = 0,//玩法桌
        GameTable = 1,//游戏桌
    }

    //玩法桌数据


    export interface IPlayer{
        id:number,
        name:string,
        avatarUrl:string;
    }

    export interface IPlayWayInfo{
        id:string,
        clubId:number,
        gameId:number,
        playwayName:string,
        payMode:number,
        payCount:number,
        isPercentage:number,
        sorceCell:number,
        lowerLimit:number,
        upperLimit:number,
        gameParameter:any
    }

    //游戏桌数据
    export interface IGameTableInfo{
        host: string,
		port: number,
		clubId: number,
		gameId: number,
		tableId: number,
		gameCount: number,
		currentCount: number,
		chairCount: number,
		playwayId: string,
		playwayName: string,
		gameMode: number,
		lowerLimit: number,
		upperLimit: number,
		players: IPlayer[],
    }

    //一个桌子数据
    export interface ITableInfo{
        playWay?:IPlayWayInfo;
        gameInfo?:IGameTableInfo;
        tbType:TableType;
        tableid:number|string;
    }

    //牌桌排序
    export function rank(allTables:ITableInfo[]){
        allTables.sort(function(a:Club.ITableInfo,b:Club.ITableInfo){
            return getSortIndex(a) - getSortIndex(b);
        })
    }

    // 优先没满人的，然后玩法空桌子，最后满人的
    function getSortIndex(tableData: any) {
        if (tableData.tbType == Club.TableType.GameTable) {
            if (tableData.gameInfo.chairCount > tableData.gameInfo.players.length) {
                return -1;
            } else {
                return 999;
            }
        } else {
            return 0;
        }
    }
}
