import PokerTableSeatBaseView from "../../../public/poker_table/poker_table_seat_base_view";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PokerTableSeatView extends PokerTableSeatBaseView {

    getSeatStyleName(index: number):string{
        switch (index) {
            case 0:
                return 'Bottom'
            case 1:
                return 'Right'
            case 2:
                return 'Left'
        
            default:
                break;
        }
        return ''
    }
}
