import ModelBase from "../../../../framework/core/mvvm/model_base";
import { PdkNetRoute } from "../../net/pdk_route";
import { PdkBaseView } from "./pdk_base_view";
import CustomUIStateSwitcher from "../../../public/ui/custom_ui_state_switcher";
import { PdkData } from "./data/pdk_data";

const {ccclass, property} = cc._decorator;

@ccclass
export class PdkView extends PdkBaseView {

    @property(cc.Node)
    menueRoot:cc.Node = null;

    @property(cc.Node)
    menueMask:cc.Node = null;

    @property(CustomUIStateSwitcher)
    chupaiBtnSwither: CustomUIStateSwitcher = null

    onLoad(){
        super.onLoad();
        this.menueRoot.active = false;
    }

    showMenue(){
        this.menueRoot.active = !this.menueRoot.active;
    }

    //更新玩家状态
    updateAllUser(data:PdkData.SeatInfoData[],roomInfo:PdkData.RoomInfoData){
        this.resetSeatViewPos();
        for (let idx = 0; idx < data.length; idx++) {
            this.userEnter(data[idx].viewID,data[idx].user,roomInfo);
        }
    }

}
