import ViewBase from "../../framework/core/mvvm/view_base";
import ModuleManager from "../../framework/core/module_manager";
import Toast from "../public/module/toast/toast";

const {ccclass, property} = cc._decorator;

@ccclass
export default class JoinClubView extends ViewBase {
    @property(cc.Label)
    Label_input:cc.Label = null;

    @property(cc.Node)
    Node_num:cc.Node = null;

    sendJoinClubCallBack:Function = null;

    onShow(){
    }

    onLoad(){
    }

    emitBindFunc(cb) {
        this.sendJoinClubCallBack = cb;
    }

    inputNumber(num:number) {
        let subStr = "";
        let items = this.Node_num.getComponentsInChildren(cc.Label);
        for (let i = 0; i < items.length; i++) {
            const item = items[i];
            if (item.string == "") {
                item.string = num.toString();
                subStr = subStr + item.string;
                if (i == 7) {
                    if (this.sendJoinClubCallBack) {
                        this.sendJoinClubCallBack(parseInt(subStr));
                    }
                }
                break;
            } else {
                subStr = subStr + item.string;
            }
        }
        this.Label_input.node.active = false;
    }

    deleteNumber() {
        let items = this.Node_num.getComponentsInChildren(cc.Label);
        for (let i = items.length-1; i >= 0; i--) {
            const item = items[i];
            if (item.string != "") {
                item.string = "";
                if (i == 0) {
                    this.Label_input.node.active = true;
                }
                break;
            }
        }
    }

    resetNumber() {
        let items = this.Node_num.getComponentsInChildren(cc.Label);
        for (let i = 0; i < items.length; i++) {
            const item = items[i];
            item.string = "";
        }
        this.Label_input.node.active = true;
    }

}
