import Debug from "../../manager/debug";
import Utility from "../../utility/Utility";
import ModuleManager from "../../framework/core/module_manager";
import HallServer from "../../pacakge/hall/server/hall/hall_serve";
import SceneManager from "../../manager/scene_manager";


export default class SdkLine{

    private static _instance : SdkLine;
    public static get instance() : SdkLine {

        if (!SdkLine._instance) {
            
            SdkLine._instance = new SdkLine();
        }
        return SdkLine._instance;
    }

    public receive(json : any){
        
        if (json.funcName == "login") {
            
            this.login(json.param);
        }
    }

    public login(jsonParam : string){

        let param = jsonParam;
   
        Debug.log(" line login  : " + param );

        //param =  AES.encrypt(AES.key,jsonParam);

        //Debug.log(" line login base64 ",jsonParam);

        SceneManager.loginFromAuth(param);

        // Utility.instance.playButtonSound();
        // ModuleManager.instance.showModuleName("NetPrompt", { showImmediately: true })
        // HallServer.instance.accountLogin("", "", "",null,null,6,param);
    }

}
