import GameUtil, { IUrlRequestData } from "../utility/game_util";
import LoaclStorage from './local_storage';
import AppData from "../pacakge/public/const/app_data";
import Debug from "./debug";





export default class LogReportManager {

    static _uid : Number

    static _baseHttpUrl = "https://dhqp-h5-log.cn-hangzhou.log.aliyuncs.com/logstores/h5log/track?APIVersion=0.6.0"

    static _httpUrl : string

    static _serialNumber : number = 0

    static _reportFailureHttpUrls = []

    static init(uid : number) {
        if (uid == null) {
            uid = 0
        } 
        // if (AppData.remoteVersionConfig && AppData.remoteVersionConfig.log && AppData.remoteVersionConfig.logReportHttpUrl) {
        //     LogReportManager._baseHttpUrl = AppData.remoteVersionConfig.logReportHttpUrl
        // }

        LogReportManager._httpUrl = LogReportManager._baseHttpUrl + "&appinit_time=" + Math.ceil(new Date().getTime() / 1000) +  "&uid=" + uid +"&os=" + cc.sys.os + "&"
    }

    static report(text : string) {
        let requestData = {
            baseUrl: LogReportManager._httpUrl,
            params: {
                text : text
            },
        }
        LogReportManager.http(requestData)
    }

    static report_cmd(cmd : string, text : string) {
        let requestData = {
            baseUrl: LogReportManager._httpUrl,
            params: {
            cmd : cmd,
                text : text
            },
        }
        LogReportManager.http(requestData)
    }

    static report_data(cmd : string, data : object) {
        let requestData = {
            baseUrl: LogReportManager._httpUrl,
            params: {
                cmd : cmd,
                text : JSON.stringify(data)
            },
        }
        LogReportManager.http(requestData)
    }

    static report_msg_cmd(cmd : string, msg : string, isRequest ?: boolean) {
        // if (!AppData.remoteVersionConfig) {
        //     return
        // }

        // if (AppData.remoteVersionConfig.log && AppData.remoteVersionConfig.log.disableWebsocketMsgLog) {
        //     // cc.log("不上报report_msg_cmd")
        //     return
        // }

        let requestData = {
            baseUrl: LogReportManager._httpUrl,
            params: {
                msg : msg
            },
        }
        if (isRequest) {
            requestData.params["request_cmd"] = cmd
        } else {
            requestData.params["response_cmd"] = cmd
        }
        LogReportManager.http(requestData)
    }
    

    static http(data: IUrlRequestData) {
        // if (!AppData.remoteVersionConfig) {
        //     return
        // }

        // if (AppData.remoteVersionConfig.log && AppData.remoteVersionConfig.log.disableAllLog) {
        //     return
        // }

        LogReportManager._serialNumber = LogReportManager._serialNumber + 1
        data.params["sn"] = LogReportManager._serialNumber
        data.params["time"] = new Date().getTime()
        //data.params['gameName'] = AppData.gameName
        let requestUrl = GameUtil.formatParamsForHttp(data)

        var xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4) {
                if (xhr.status == 200) {
                    // var response = JSON.parse(xhr.responseText);
                    // if (!response) {
                    //     cc.error("http返回数据格式错误：", xhr.responseURL, xhr.responseText)
                    //     return
                    // }
                    Debug.log(requestUrl)
                } else {
                    //上传失败的
                    if (data.requestCount < 2) {
                        LogReportManager.http(data)
                    }
                    Debug.log("http request error:", xhr.status)
                }
            }
        };
        if (data.requestCount == null) {
            data.requestCount = 0
        }
        ++data.requestCount
        xhr.timeout = 10000
        xhr.open("GET", requestUrl, true);
        xhr.send();
    }
}
