import ServerBase from "../server_base";
import AppData from "../../public/const/app_data";
import ModuleData from "../../public/const/module_data";
import SceneManager from "../../../manager/scene_manager";
import NetPrompt from "../../public/module/netprompt/netprompt";
import ModuleManager from "../../../framework/core/module_manager";
import { GameServerRoute } from "./game_net_route";
import { ServerCode } from "../../public/const/const_code";
import UserDataManager from "../../../manager/user_manager";
import ModelData from "../../public/model_data";
import { GameCfg } from "../../public/const/game_cfg";

const { ccclass } = cc._decorator;

@ccclass
export default class GameServer extends ServerBase {

    public static instance: GameServer;
    onLoad() {
        GameServer.instance = this
        this.init("GameServer")
    }


    _init(){
        let self = this;
        this.model.registerNetMsg(GameServerRoute.enter,function(data){
            self.loginRsp(data);
        },true);
    }

    loginRsp(data){
        if (data.code == ServerCode.EnterTableCode.OK ) {
            
        }else{
            SceneManager.gotoHall();
        }
    }

    connectServer(url) {
        ModuleManager.instance.showModule(NetPrompt, { showImmediately: true})
        this.connect(url, function (state) {
            if (state == 'handshake') {
                this.startCheckPing();
            }
            cc.log("connect server state:", state)
        })
    }

    protected onConnected() {
        ModuleManager.instance.hideModule('NetPrompt');
        this._init();
        let self = this
        let cf = GameCfg[AppData.gameid].gName
        cc.log(`连接游戏服成功 ${cf}：`, ModuleData.instance.isTableModuleActive())

        SceneManager.gotoSubGame(null, () => {
            self.login()
        })
    }

    protected connectTimes(times:number){
        if (this._reconTimes >= 1) {
            ModuleManager.instance.showModule(NetPrompt, { showImmediately: true, reconnect: true })
        }

        if (this._reconTimes > this._maxReconTimes) {
            cc.warn("登陆服重连次数用完，提示登陆失败")
            SceneManager.gotoHall();
        }
    }

    private login() {
        let data = {
            openid:UserDataManager.instance.getUserData().openid,
            clubId:ModelData.instance.clubID,
            playwayId:ModelData.instance.playwayId,
            tableId:ModelData.instance.tableId,
        }
        this.model.send(GameServerRoute.enter,data)
    }

    protected reconnectServerFail() {
        SceneManager.gotoHall()
    }

    protected onReconnectServer() {
        ModuleManager.instance.showModule(NetPrompt, { "showImmediately": true, "reconnect": true })
    }
}
