import Module, { bindButtonClickEvent, moduleInit, ViewLayer } from "../../../framework/core/mvvm/module_base";
import LoadingView from "./loading_view";
import ModuleManager from "../../../framework/core/module_manager";
import Debug from "../../../manager/debug";
import GameUtil from "../../../utility/game_util";
import AuthModule from "../../hall/module/auth/auth_module";

const {ccclass, property} = cc._decorator;

@ccclass
@moduleInit("LoadingModule")
export default class LoadingModule extends Module<LoadingView,null> {

    constructor() {
        super();
        this.layer = ViewLayer.Highest;
        this.windowPrefabResPath = "package/public/public_loading";
        this.viewType = LoadingView;
        this.modelType = null;
        this.bindEvent();
    }

    bindEvent(){

        this.eventBind("already_up_to_date",this.already_up_to_date);        
        this.eventBind("new_version_found",this.on_new_version_found);
        this.eventBind("update_progress",this.on_update_progress);
        this.eventBind("update_finished",this.on_update_finished);
        this.eventBind("get_app_version",this.on_get_app_version);
        this.eventBind("error_no_local_manifest",this.on_update_finished)
        this.eventBind("error_download_manifest_file.",this.on_update_finished)
        
    }

    show(){
        super.show();

        //判断是否为第一次loading
        if (cc.sys.os === cc.sys.OS_ANDROID) {
            let className = "org/cocos2dx/javascript/AppActivity";
            let methodName = "removeSplashImage";               
            let methodSignature = "()V";
            jsb.reflection.callStaticMethod(className, methodName,methodSignature);
        }        
    }

    already_up_to_date(){

        if (!this.view) {
            
            this.AddHandleToWaitDealQueue(this.already_up_to_date,null,this);
            return;
        }

        this.loadAuth();
    }

    on_new_version_found(){

        if (!this.view) {
            
            this.AddHandleToWaitDealQueue(this.on_new_version_found,null,this);
            return;
        }

    }

    on_update_progress(data){

        if (!this.view) {
            this.AddHandleToWaitDealQueue(this.on_update_progress,data,this);
            return;
        }

        this.updatePercent(data)
    }

    

    on_update_finished(){

        this.loadAuth();
    }

    on_get_app_version(version : string){

        this.view.updateVersion(version);
    }

    loadAuth(){

        let self = this;
                
        GameUtil.loadResArray(["package/hall/module/auth/AuthModule", "package/hall/module/hall/hall_base"], function (error: Error, resource: any[]) {
            if (error) {
                //TODO:需要处理的异常
            } else {
                Debug.log("加载完成")
                ModuleManager.instance.showModule(AuthModule,null,()=>{
                    self.hide();
                })               
                
            }
        })

    }

    onInit()
    {
        
    }

    updatePercent(percent : number){
        
        this.view.updatePercent(percent);
    }

}
