import Module, { moduleInit } from "../../../framework/core/mvvm/module_base";
import ModuleManager from "../../../framework/core/module_manager";
import Toast from "../../public/module/toast/toast";
import HallServer from "../../server/hallserver/hall_server";
import { ClubRoute } from "../net_clubRoute";
import ClubRankView from "./clubRank_view";
import { ClubRankModel } from "./clubRank_model";

const {ccclass, property} = cc._decorator;

@ccclass
@moduleInit("ClubRankModule")
export default class ClubMemListModule extends Module<ClubRankView,ClubRankModel> {
    constructor(){
        super()
        this.windowPrefabResPath = 'package/club/clubRank';
        this.viewType = ClubRankView;
        this.modelType = ClubRankModel;
        this.needAnim = true;
        this.clickAnywhereClose = false;
        this.needViewMask = true;
    }

    onLoad(){
    }

    onShow(){
        this.model.netClient = HallServer.instance.netClient;
        this.model.clearMsgEvent();
        this.registerMsg();
        this.model.curPageNum = 1;
        this.model.send_getRankList(0, this.model.curPageNum, this.model.pageCeilNum);
    }

    registerMsg() {
        this.model.registerNetMsg(ClubRoute.getRankList, this.retGetRankList.bind(this));
    }

    retGetRankList(data:any) {
        if (data.code == 0) {
            let totalNum = Math.ceil(data.totalNum / this.model.pageCeilNum);
            this.model.totalPageNum = totalNum;
            this.view.loadRankList(data.list, this.model.curPageNum, this.model.pageCeilNum, data.myRank);
        } else {
            ModuleManager.instance.showModule(Toast,'获取排行榜错误code=' + data.code);
        }
    }

    protected onButtonClick(node: cc.Node, customEventData){
        let pName = node.name;
        switch (pName) {
            case 'Button_close':
                ModuleManager.instance.destroyModule('ClubRankModule');
                break;
            case 'Button_leftPage':
                if (this.model.curPageNum > 1) {
                    this.model.curPageNum = this.model.curPageNum - 1;
                    this.model.send_getRankList(0, this.model.curPageNum, this.model.pageCeilNum);
                }
                break;
            case 'Button_rightPage':
                if (this.model.curPageNum < this.model.totalPageNum) {
                    this.model.curPageNum = this.model.curPageNum + 1;
                    this.model.send_getRankList(0, this.model.curPageNum, this.model.pageCeilNum);
                }
                break;
            default:
                break;
        }
    }
}
